<template>
  <div>
    <va-form :id="id" :item="item" @validate="onValidate" v-model="model">
      <v-card>
        <v-tabs v-model="tabs">
          <v-tab>{{ $t("invoices.tab1") }}</v-tab>
          <v-tab
            >{{ $t("invoices.tab3") }} ({{ invoiceOrderItems.length }})
          </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tabs">
          <v-tab-item>
            <v-card flat>
              <v-card-text>
                <v-row>
                  <v-col sm="3">
                    <va-select-input
                      source="customerId"
                      reference="customers"
                      :error-messages="customerIdErrors"
                    ></va-select-input>
                    <va-text-input
                      v-show="false"
                      source="id"
                      :label="$t('invoices.id')"
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3">
                    <va-text-input
                      model="invoiceNo"
                      source="invoiceNo"
                      :error-messages="invoiceNoErrors"
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3">
                    <va-text-input
                      model="eInvoiceNo"
                      source="eInvoiceNo"
                      :error-messages="eInvoiceNoErrors"
                    ></va-text-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="3">
                    <va-select-input
                      source="invoiceYearId"
                      reference="years"
                      :error-messages="invoiceYearIdErrors"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="3">
                    <va-select-input
                      source="invoiceMonthId"
                      reference="months"
                      :error-messages="invoiceMonthIdErrors"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="3">
                    <va-date-input
                      source="invoiceDate"
                      format="short"
                      :error-messages="invoiceDateErrors"
                    ></va-date-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="3">
                    <va-select-input
                      source="invoiceCurrencyId"
                      :error-messages="invoiceCurrencyIdErrors"
                      @change="detectCurrencyInput"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="3">
                    <va-currency-input
                      source="invoiceAmount"
                      v-model="model.invoiceAmount"
                      :key="model.invoiceCurrencyId + '_invoiceAmount'"
                      :options="{ currency: currencyId, precision: 2 }"
                      :label="$t('invoices.invoiceAmount')"
                    ></va-currency-input>
                  </v-col>
                  <v-col sm="3">
                    <va-currency-input
                      source="invoiceTaxAmount"
                      v-model="model.invoiceTaxAmount"
                      :key="model.invoiceCurrencyId + '_invoiceTaxAmount'"
                      :options="{ currency: currencyId, precision: 2 }"
                      :label="$t('invoices.invoiceTaxAmount')"
                    ></va-currency-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="6">
                    <va-text-input
                      source="invoiceDescription"
                      multiline
                      :error-messages="invoiceDescriptionErrors"
                    ></va-text-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="9">
                    <va-text-input
                      v-show="false"
                      :label="$t('invoices.invoiceItems')"
                      v-model="model.invoiceItems"
                    ></va-text-input>
                    <invoices-items
                      v-if="model.customerId"
                      :key="model.customerId"
                      :items="invoiceItems"
                      :customerId="model.customerId"
                      @save="saveInvoiceItems"
                    >
                    </invoices-items>
                  </v-col>
                </v-row>
                <va-save-button></va-save-button>
              </v-card-text>
            </v-card>
          </v-tab-item>

          <v-tab-item>
            <v-card flat>
              <v-card-text>
                <v-row>
                  <v-col sm="12">
                    <va-text-input
                      v-show="false"
                      :label="$t('invoices.invoiceOrderItems')"
                      v-model="model.invoiceOrderItems"
                    ></va-text-input>
                    <template>
                      <v-data-table
                        :headers="headerInvoiceOrderItems"
                        :items="invoiceOrderItems"
                        sort-by="name"
                        class="v-data-table__editable_wrapper no_top_border"
                        hide-default-footer
                      >
                        <template v-slot:top>
                          <v-toolbar flat>
                            <v-spacer></v-spacer>
                            <v-dialog v-model="dialogOi" max-width="1000px">
                              <template v-slot:activator="{ on, attrs }">
                                <v-btn
                                  color="success"
                                  icon
                                  v-bind="attrs"
                                  v-on="on"
                                >
                                  <v-icon>mdi-plus</v-icon>
                                </v-btn>
                              </template>
                              <v-card>
                                <v-card-text>
                                  <v-container>
                                    <template v-if="showError">{{
                                      errorMessage
                                    }}</template>
                                    <template v-else>
                                      <va-list
                                        resource="orderItems"
                                        :filter="{
                                          customerId: model.customerId,
                                        }"
                                        :items-per-page="50"
                                        :disableExport="true"
                                        :disableCreate="true"
                                        :disable-items-per-page="false"
                                        :disable-global-search="true"
                                        :disablePagination="true"
                                        :hideBulkDelete="true"
                                        @selected="onSelect"
                                      >
                                        <va-data-table
                                          :disableActions="true"
                                          :fields="fields"
                                        ></va-data-table>
                                      </va-list>
                                    </template>
                                  </v-container>
                                </v-card-text>
                                <v-card-actions>
                                  <v-spacer></v-spacer>
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="closeOi"
                                  >
                                    {{ $t("dialog.cancelButton") }}
                                  </v-btn>
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="saveOi"
                                  >
                                    {{ $t("dialog.saveButton") }}
                                  </v-btn>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>

                            <v-dialog
                              v-model="dialogOiDelete"
                              max-width="500px"
                            >
                              <v-card>
                                <v-card-title class="text-h5">{{
                                  $t("dialog.deleteConfirm")
                                }}</v-card-title>
                                <v-card-actions>
                                  <v-spacer></v-spacer>
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="closeOiDelete"
                                    >{{ $t("dialog.closeDeleteButton") }}</v-btn
                                  >
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="deleteOiItemConfirm"
                                    >{{
                                      $t("dialog.confirmDeleteButton")
                                    }}</v-btn
                                  >
                                  <v-spacer></v-spacer>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>
                          </v-toolbar>
                        </template>
                        <template v-slot:item.actions="{ item }">
                          <v-icon small @click="deleteOiItem(item)">
                            mdi-delete
                          </v-icon>
                        </template>
                      </v-data-table>
                    </template>
                  </v-col>
                </v-row>
                <va-save-button></va-save-button>
              </v-card-text>
            </v-card>
          </v-tab-item>
        </v-tabs-items>
      </v-card>
    </va-form>
  </div>
</template>

<script>
/// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components
///
import { generateId } from "../../js/common";
import { required, maxLength } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      tabs: null,
      currencyId: null,
      itemsEditWindow: false,
      renderKeyItems: 0,
      invoiceItems: [],
      model: {
        id: null,
        customerId: "",
        invoiceNo: "",
        eInvoiceNo: "",
        invoiceDate: "",
        invoiceYearId: "",
        invoiceMonthId: "",
        invoiceDescription: "",
        invoiceCurrencyId: "",
        invoiceAmount: "0",
        invoiceTaxAmount: "0",
        invoiceItems: "",
        invoiceOrderItems: "",
      },
      fields: [
        {
          source: "itemDepartmentName",
          label: this.$t("invoices.itemDepartmentName"),
          type: "select",
        },
        {
          source: "purchaseOrderNo",
          label: this.$t("invoices.purchaseOrderNo"),
        },
        {
          source: "startDate",
          label: this.$t("invoices.startDate"),
        },
        {
          source: "endDate",
          label: this.$t("invoices.endDate"),
        },
        {
          source: "itemDescription",
          label: this.$t("invoices.itemDescription"),
        },
        {
          source: "itemCurrencyId",
          label: this.$t("invoices.itemCurrencyId"),
        },
        {
          source: "itemTotalAmount",
          label: this.$t("invoices.itemTotalAmount"),
        },
      ],
      currencies: [
        { id: "TRY", name: "Türk Lirası" },
        { id: "USD", name: "Dolar" },
        { id: "EUR", name: "Euro" },
      ],
      dialogOi: false,
      dialogOiDelete: false,
      invoiceOrderItems: [],
      oiEditedIndex: -1,
      oiEditedItem: {
        invoiceId: null,
        purchaseOrderId: null,
        purchaseOrderItemId: null,
        customerOrderNo: "",
        purchaseOrderNo: "",
        startDate: "",
        endDate: "",
        itemCurrencyId: "",
        itemCurrencyName: "",
        itemDepartmentName: "",
        itemDescription: "",
        itemExchangeRate: "0",
        itemAmount: "0",
        itemTaxAmount: "0",
        itemTotalTaxAmount: "0",
        itemTotalAmount: "0",
      },
      oiDefaultItem: {
        invoiceId: null,
        purchaseOrderId: null,
        purchaseOrderItemId: null,
        customerOrderNo: "",
        purchaseOrderNo: "",
        startDate: "",
        endDate: "",
        itemCurrencyId: "",
        itemCurrencyName: "",
        itemDepartmentName: "",
        itemDescription: "",
        itemExchangeRate: "0",
        itemAmount: "0",
        itemTaxAmount: "0",
        itemTotalTaxAmount: "0",
        itemTotalAmount: "0",
      },
      selected: [],
      headerInvoiceOrderItems: [
        { text: this.$t("orders.purchaseOrderNo"), value: "purchaseOrderNo" },
        { text: this.$t("orders.customerOrderNo"), value: "customerOrderNo" },
        { text: this.$t("orders.departmentName"), value: "itemDepartmentName" },
        { text: this.$t("orders.startDate"), value: "startDate" },
        { text: this.$t("orders.endDate"), value: "endDate" },
        { text: this.$t("orders.description"), value: "itemDescription" },
        { text: this.$t("orders.currencyId"), value: "itemCurrencyId" },
        { text: this.$t("orders.totalAmount"), value: "itemTotalAmount" },
        { text: this.$t("orders.orderActions"), value: "actions" },
      ],
      showError: false,
      errorMessage: "",
    };
  },
  validations: {
    model: {
      invoiceNo: {
        required,
        maxLength: maxLength(40),
      },
      eInvoiceNo: {
        required,
        maxLength: maxLength(20),
      },
      invoiceDate: {
        required,
      },
      invoiceYearId: {
        required,
      },
      invoiceMonthId: {
        required,
      },
      customerId: {
        required,
        maxLength: maxLength(36),
      },
      invoiceCurrencyId: {
        required,
        maxLength: maxLength(3),
      },
      invoiceDescription: {
        maxLength: maxLength(255),
      },
    },
  },
  watch: {
    dialog(val) {
      if (val == true) {
        // dialog open
        this.itemsRenderModalComponent();
        this.editedItem.itemCustomerId = this.model.customerId;
        this.detectCurrencyInput();
      }
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    invoiceNoErrors() {
      const errors = [];
      const field = "invoiceNo";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    eInvoiceNoErrors() {
      const errors = [];
      const field = "eInvoiceNo";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    customerIdErrors() {
      const errors = [];
      const field = "customerId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    invoiceYearIdErrors() {
      const errors = [];
      const field = "invoiceYearId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    invoiceMonthIdErrors() {
      const errors = [];
      const field = "invoiceMonthId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    invoiceDateErrors() {
      const errors = [];
      const field = "invoiceDate";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    invoiceCurrencyIdErrors() {
      const errors = [];
      const field = "invoiceCurrencyId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "3" }));
      return errors;
    },
    invoiceDescriptionErrors() {
      const errors = [];
      const field = "invoiceDescription";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "255" }));
      return errors;
    },
  },
  async created() {
    this.model.id = generateId(this);
    this.initializeInvoiceItems();
    this.initializeInvoiceOrderItems();
  },
  mounted() {
    this.currencyId = "TRY";
    if (this.item) {
      this.currencyId = this.item.invoiceCurrencyId
        ? this.item.invoiceCurrencyId
        : "TRY";
    }
  },
  methods: {
    saveInvoiceItems(invoiceItems) {
      this.invoiceItems = invoiceItems;
      this.model.invoiceItems = JSON.stringify(invoiceItems);
    },
    itemsRenderModalComponent() {
      this.renderKeyItems = this.renderKeyItems + 1;
    },
    detectCurrencyInput() {
      if (this.model.invoiceCurrencyId) {
        this.currencyId = this.model.invoiceCurrencyId;
      }
    },
    onValidate() {
      if (this.$v.$invalid) {
        this.tabs = 0;
      }
    },
    initializeInvoiceItems() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "invoiceItems")
      ) {
        this.invoiceItems = JSON.parse(this.item.invoiceItems);
        this.model.invoiceItems = this.item.invoiceItems;
      }
    },
    initializeInvoiceOrderItems() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "invoiceOrderItems")
      ) {
        this.invoiceOrderItems = JSON.parse(this.item.invoiceOrderItems);
        this.model.invoiceOrderItems = this.item.invoiceOrderItems;
      }
    },
    deleteOiItem(item) {
      this.oiEditedIndex = this.invoiceOrderItems.indexOf(item);
      this.dialogOiDelete = true;
    },
    deleteOiItemConfirm() {
      this.invoiceOrderItems.splice(this.oiEditedIndex, 1);
      this.model.invoiceOrderItems = JSON.stringify(this.invoiceOrderItems);
      this.closeOiDelete();
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(async () => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    itemsModalSave(editedItem) {
      this.editedItem = editedItem;
      this.save();
    },
    onSelect(selected) {
      this.selected = selected;
    },
    closeOi() {
      this.dialogOi = false;
      this.$nextTick(async () => {
        this.oiEditedItem = Object.assign({}, this.oiDefaultItem);
        this.oiEditedIndex = -1;
      });
    },
    closeOiDelete() {
      this.dialogOiDelete = false;
      this.$nextTick(async () => {
        this.oiEditedItem = Object.assign({}, this.oiDefaultItem);
        this.oiEditedIndex = -1;
      });
    },
    saveOi() {
      let invoiceId = this.id ? this.id : this.uid;
      let Self = this;
      this.selected.forEach(function (val) {
        var doesExist = Self.invoiceOrderItems.some(function (ele) {
          return ele.purchaseOrderItemId === val.purchaseOrderItemId;
        });
        // do not add same record two times
        //
        if (!doesExist) {
          Self.invoiceOrderItems.push({
            invoiceId: invoiceId,
            purchaseOrderId: val.purchaseOrderId,
            purchaseOrderItemId: val.purchaseOrderItemId,
            customerOrderNo: val.customerOrderNo,
            purchaseOrderNo: val.purchaseOrderNo,
            startDate: val.startDate,
            endDate: val.endDate,
            itemCurrencyId: val.itemCurrencyId,
            itemCurrencyName: val.itemCurrencyName,
            itemDepartmentName: val.itemDepartmentName,
            itemDescription: val.itemDescription,
            itemExchangeRate: val.itemExchangeRate,
            itemAmount: val.itemAmount,
            itemTaxAmount: val.itemTaxAmount,
            itemTotalTaxAmount: val.itemTotalTaxAmount,
            itemTotalAmount: val.itemTotalAmount,
          });
        }
      });
      this.model.invoiceOrderItems = JSON.stringify(this.invoiceOrderItems);
      this.closeOi();
    },
  },
};
</script>
