<template>
  <div>
    <div v-if="isFormEnabled">
      <v-row>
        <v-col sm="12">
          <v-row>
            <v-col sm="12">
              <va-select-input
                v-model="model.customerId"
                :label="$t('expenses.customerId')"
                :error-messages="customerIdErrors"
              ></va-select-input>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col sm="12">
          <v-btn
            color="primary"
            :loading="saving"
            dark
            class="mb-2"
            @click="save"
          >
            {{ $t("dialog.batchUpdateButton") }}
          </v-btn>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";

export default {
  props: [],
  data() {
    return {
      isFormEnabled: false,
      saving: false,
      affectedRows: 0,
      batchInputType: "text",
      choices: [
        {
          id: "expenseApplication",
          name: this.$t("expensereports.expenseApplication"),
        },
        { id: "packageNo", name: this.$t("expensereports.packageNo") },
        { id: "poNo", name: this.$t("expensereports.poNo") },
        {
          id: "pernetInvoiceNo",
          name: this.$t("expensereports.pernetInvoiceNo"),
        },
        {
          id: "sentForApproval",
          name: this.$t("expensereports.sentForApproval"),
        },
      ],
      model: {
        customerId: "",
        sowId: null,
        target: null,
        value: null,
      },
    };
  },
  computed: {
    customerIdErrors() {
      const errors = [];
      const field = "customerId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    sowIdErrors() {
      const errors = [];
      const field = "sowId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    targetErrors() {
      const errors = [];
      const field = "target";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    valueErrors() {
      const errors = [];
      const field = "value";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  validations: {
    model: {
      customerId: {
        required,
      },
      sowId: {
        required,
      },
      target: {
        required,
      },
      value: {
        required,
      },
    },
  },
  created() {
    this.isFormEnabled = true;
  },
  methods: {
    detectBatchInput(e) {
      if (
        e.id == "expenseApplication" ||
        e.id == "packageNo" ||
        e.id == "poNo" ||
        e.id == "pernetInvoiceNo"
      ) {
        this.batchInputType = "text";
        this.model.value = null;
      }
      if (e.id == "sentForApproval") {
        this.batchInputType = "bool";
        this.model.value = 0;
      }
    },
    async save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.saving = true;
      try {
        let response = await this.update(
          "put",
          "/expensereports/batchUpdate",
          this.model
        );
        if (response.status == 200) {
          this.isFormEnabled = false;
          this.affectedRows = response.data.data.affectedRows;
          if (this.affectedRows > 0) {
            this.$store.dispatch("api/refresh", "expensereports");
          }
        }
      } finally {
        this.saving = false;
      }
    },
    async update(method, url, data) {
      let response = null;
      try {
        response = await this.$admin.http({ method, url, data });
      } catch (e) {
        this.$snotify.warning(e.message);
      }
      return response;
    },
  },
};
</script>
