<template>
  <div>
    <!-- @slot Default slot if you need to replace default VaFileField or VaImageField. -->
    <slot>
      <component v-if="preview"
        :is="`va-${preview ? 'image' : 'file'}-field`"
        :source="source"
        :item="getItem"
        v-bind="$attrs"
        :item-value="itemValue"
      ></component>
    </slot>
    <!-- multiple as v-bind because of vuetify v-model bug forced to array -->

<!--     <v-row style="min-width:250px">
      <v-file-input
        v-bind="multiple ? { ...commonProps, multiple: true } : commonProps"
        :filled="filled"
        :chips="chips"
        clearable
        :small-chips="smallChips"
        :accept="accept"
        @change="updateFile"
      >           
          <template slot="selection" slot-scope="data">
            <v-chip
              small
              @click.native="clickMe($event, data.file.fileId)"
            >
              {{ data.file.fileName }}
            </v-chip>
          </template>  
      </v-file-input>
    </v-row> -->

    <v-row style="min-width:250px">
      <v-file-input
        v-bind="multiple ? { ...commonProps, multiple: true } : commonProps"
        :filled="filled"
        :chips="chips"
        clearable
        show-size
        :small-chips="smallChips"
        :accept="accept"
        @change="updateFile"
      >
          <template v-slot:selection="{ text, index }">
            <v-chip
              x-small
              @click.native="clickMeIndex($event, index)"
            >
              {{ text ? text : (typeof value[index].name == "undefined" ? "null" : value[index].name) }}
            </v-chip>
          </template>
      </v-file-input>
    </v-row>
  </div>
</template>

<script>
// import isEmpty from "lodash/isEmpty";
import Input from "../../../mixins/input";
import { generateUid } from "../../../mixins/common";
import Multiple from "../../../mixins/multiple";

/**
 * Allow row file uploads. Can be multiple.
 * Current files will be shown as simple link or thumbnail image and can be deleted.
 * Use VaFileField or VaImageField under the hood.
 * No Ajax support.
 */
export default {
  mixins: [Input, Multiple],
  props: {
    /**
     * Show file input in data grid.
     */
    gridable: {
      type: Boolean,
      default: false,
    },
    /**
     * Show file thumbnail image instead of simple link.
     */
    preview: {
      type: Boolean,
      default: false,
    },
    /**
     * Attribute where taking the id value for identify files to delete.
     */
    itemValue: {
      type: String,
      default: "id",
    },
    /**
     * Add HTML5 `accept` attribute for simple client-side validation.
     */
    accept: String,
    /**
     * Error status for form validation input focus feature
     */
    status: {
      type: Boolean,
    },
    /**
     * base64 upload option
     */
    base64: {
      type: Boolean,
      default: false,
    },
    /**
     * Set tablename
     */
    tableName: String,
    /**
     * Filled
     */
    filled: {
      type: Boolean,
      default: false,
    },
    /**
     * Download is enabled
     */
    downloadEnabled: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      clear: false,
      files: [],
      acceptValue: false,
    };
  },
  methods: {
    updateFile(val) {
      if (typeof val == "undefined" || (Array.isArray(val) && val.length == 0)) {
        this.files = []
        this.update(val)
        this.clear = true
        return
      }
      this.clear = false
      if (this.base64) {
        this.createBase64Files(val)
      } else {
        this.update(val)
      }
      this.change(val)
    },
    createBase64Files(value) {
      if (this.multiple) {
        for (var i=0;i<value.length;i++) {
          this.createFile(i, value[i]) 
        }
      } else {
        this.createFile(0, value) 
      }
    },
    createFile(index, fileObject) {
      const reader = new FileReader;
      reader.onload = (event) => {
        this.files[index] = { id: null, size: null, name: null, data: null, type: null }
        if (typeof fileObject["id"] == "undefined") {
          this.files[index]["id"] = generateUid()
        }
        this.files[index]["size"] = fileObject.size
        this.files[index]["name"] = fileObject.name
        this.files[index]["type"] = fileObject.type
        this.files[index]["data"] = event.target.result
        this.update(this.files)
      }
      // check file is exists
      // https://stackoverflow.com/questions/32508191/uncaught-typeerror-failed-to-execute-readasdataurl-on-filereader-parameter
      if (typeof event.target.files[index] != 'undefined') {
        reader.readAsDataURL(fileObject)
      }
    },
    // remove(index) {
    //   this.files.splice(index, 1)
    //   this.update(this.files)
    // },
    getItem() {
      if (Object.prototype.hasOwnProperty.call(this.formState, "item")) {
        return this.formState.item
      }
      return null
    },
    clickMeIndex(e, index){
      this.downloadFileWithIndex(index)
      e.stopPropagation();
    },
    downloadFileWithIndex(index) {
      let url = this.$admin.apiUrl + this.$admin.downloadUrl + String(this.value[index].id) + '?tableName=' + this.tableName
      this.testFile(url, this.value[index].id)
    },
    async testFile(url, fileId) {
      let response = null
      try {
        response = await this.$admin.http.get(url)
        if (response.status == 200 && typeof fileId != "undefined") {
          location.href = url
        }
      } catch (e) {
        console.log(e)
        this.$snotify.warning(this.$t("error.fileNotFound"));
      }
    },
  }
};
</script>