<template>
  <v-app>
    <v-container
      fluid
      fill-height
      id="forgot-password-page"
      :style="{ backgroundColor: themeColor }"
    >
      <v-layout align-center justify-center>
        <v-flex :style="{ 'max-width': resetCode ? '350px' : '600px' }">
          <template v-if="resetCode">
            <v-card>
              <v-card-text>
                <v-row>
                  <v-col sm="12" class="pa-3"></v-col>
                </v-row>
                <v-form ref="form" @submit.prevent="validate">
                  <v-row>
                    <v-col sm="12">
                      <va-text-input
                        :label="$t('profile.newPassword')"
                        type="password"
                        prepend-icon="mdi-lock"
                        :filled="false"
                        v-model="newPassword"
                        :error-messages="newPasswordErrors"
                      ></va-text-input>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col sm="12">
                      <va-text-input
                        :label="$t('profile.newPasswordConfirmation')"
                        type="password"
                        prepend-icon="mdi-lock"
                        :filled="false"
                        v-model="newPasswordConfirmation"
                        :error-messages="newPasswordConfirmationErrors"
                      ></va-text-input>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col sm="12" class="pa-3"></v-col>
                  </v-row>
                  <v-row>
                    <v-col sm="12">
                      <div class="text-right">
                        <v-btn
                          :loading="loading"
                          color="primary"
                          large
                          type="submit"
                          text
                          rounded
                          >{{ $t("auth.resetMyPassword") }}</v-btn
                        >
                      </div>
                    </v-col>
                  </v-row>
                </v-form>
              </v-card-text>
            </v-card>
          </template>
          <template v-else>
            <v-alert border="bottom" colored-border type="error" elevation="2">
              {{ $t("auth.passwordResetCodeCannotBeEmpty") }}
            </v-alert>
          </template>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
import { getQueryParam } from "../js/common";
import {
  required,
  maxLength,
  minLength,
  sameAs,
} from "vuelidate/lib/validators";

export default {
  data() {
    return {
      resetCode: null,
      newPassword: null,
      newPasswordConfirmation: null,
      loading: false,
      themeColor: localStorage.themeColor ? localStorage.themeColor : "#F26522",
    };
  },
  validations: {
    newPassword: {
      required,
      minLength: minLength(8),
      maxLength: maxLength(16),
    },
    newPasswordConfirmation: {
      required,
      sameAs: sameAs("newPassword"),
    },
  },
  created() {
    this.resetCode = getQueryParam("resetCode");
  },
  computed: {
    newPasswordErrors() {
      const errors = [];
      if (!this.$v.newPassword.$dirty) return errors;
      !this.$v.newPassword.required && errors.push(this.$t("v.text.required"));
      !this.$v.newPassword.minLength &&
        errors.push(this.$t("v.string.minLength", { min: "8" }));
      !this.$v.newPassword.maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "16" }));
      return errors;
    },
    newPasswordConfirmationErrors() {
      const errors = [];
      if (!this.$v.newPasswordConfirmation.$dirty) return errors;
      !this.$v.newPasswordConfirmation.required &&
        errors.push(this.$t("v.text.required"));
      !this.$v.newPasswordConfirmation.sameAs &&
        errors.push(this.$t("v.password.sameAs"));
      return errors;
    },
  },
  methods: {
    async validate() {
      this.$v.$touch();
      this.loading = false;
      if (this.$v.$invalid) {
        return;
      }
      let result = false;
      result = await this.http("post", "/auth/resetPassword", {
        resetCode: this.resetCode,
        newPassword: this.newPassword,
        newPasswordConfirmation: this.newPasswordConfirmation,
      });
      if (result) {
        this.$snotify.success(this.$t("auth.passwordResetEmail"));
        this.$router.push({ name: "login" });
      }
    },
    clear() {
      this.$v.$reset();
      this.username = "";
    },
    async http(method, url, data) {
      try {
        this.loading = true;
        await this.$admin.http({ method, url, data });
        return true;
      } catch (e) {
        this.loading = false;
        console.log(e.message);
        // this.$snotify.warning(e.message);
      }
      return false;
    },
  },
};
</script>
