<template>
  <va-form :id="id" :item="item" v-model="model">
    <v-row justify="center">
      <v-col sm="12">
        <v-card flat>
          <v-card-text>
            <v-row>
              <v-col sm="3">
                <va-boolean-input source="active"></va-boolean-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="3">
                <va-text-input
                  source="notifyName"
                  :error-messages="notifyNameErrors"
                ></va-text-input>
              </v-col>
              <v-col sm="3">
                <va-select-input
                  source="moduleId"
                  reference="notifyModules"
                  :error-messages="moduleIdErrors"
                ></va-select-input>
              </v-col>
            </v-row>

            <v-row>
              <v-col sm="3">
                <va-select-input
                  source="dateId"
                  reference="notifyDates"
                  :filter="{ moduleId: model.moduleId }"
                  :key="model.moduleId"
                  :error-messages="dateIdErrors"
                ></va-select-input>
              </v-col>
              <v-col sm="1" v-if="!model.sameDay">
                <va-number-input source="days"></va-number-input>
              </v-col>
              <v-col sm="2" v-if="!model.sameDay">
                <va-select-input source="dayType"></va-select-input>
              </v-col>
              <v-col sm="3">
                <va-boolean-input source="sameDay"></va-boolean-input>
              </v-col>
            </v-row>

            <!-- time picker start -->
            <v-row>
              <v-col cols="11" sm="3">
                <v-menu
                  ref="menu"
                  v-model="menu2"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  :return-value.sync="time"
                  transition="scale-transition"
                  offset-y
                  max-width="290px"
                  min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="model.atTime"
                      :label="$t('notifications.atTime')"
                      prepend-icon="mdi-alarm"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-time-picker
                    v-if="menu2"
                    v-model="model.atTime"
                    full-width
                    @click:minute="$refs.menu.save(time)"
                  ></v-time-picker>
                </v-menu>
              </v-col>

              <v-col sm="3">
                <va-select-input source="notifyType"></va-select-input>
              </v-col>
            </v-row>
            <!-- time picker end -->

            <v-row>
              <v-col sm="6">
                <va-autocomplete-input
                  source="users"
                  reference="users"
                  multiple
                  clearable
                  :error-messages="usersErrors"
                ></va-autocomplete-input>
              </v-col>
            </v-row>

            <v-row>
              <v-col sm="6">
                <va-rich-text-input
                  source="message"
                  height="200"
                  :label="$t('notifications.message')"
                  :error-messages="messageErrors"
                ></va-rich-text-input>
              </v-col>
            </v-row>
            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>
<script>
import { generateId } from "../../js/common";
import { required, minLength } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      time: null,
      dialog: false,
      menu2: false,
      modal2: false,
      model: {
        id: null,
        notifyName: null,
        moduleId: null,
        dateId: null,
        days: 30,
        dayType: null,
        sameDay: 0,
        atTime: this.item ? this.item.atTime : null,
        notifyType: "email",
        users: null,
        message: null,
        active: 0,
      },
    };
  },
  validations: {
    model: {
      notifyName: {
        required,
      },
      moduleId: {
        required,
      },
      dateId: {
        required,
      },
      notifyType: {
        required,
      },
      users: {
        required,
      },
      message: {
        required,
        minLength: minLength(10),
      },
    },
  },
  computed: {
    notifyNameErrors() {
      const errors = [];
      const field = "notifyName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    moduleIdErrors() {
      const errors = [];
      const field = "moduleId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    dateIdErrors() {
      const errors = [];
      const field = "dateId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    notifyTypeErrors() {
      const errors = [];
      const field = "notifyType";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    usersErrors() {
      const errors = [];
      const field = "users";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    messageErrors() {
      const errors = [];
      const field = "message";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "10" }));
      return errors;
    },
  },
  created() {
    this.model.id = generateId(this);
  },
  methods: {},
};
</script>
