<template>
  <va-form :id="id" :item="item" v-model="model">
    <v-row justify="center">
      <v-col sm="12">
        <v-card flat>
          <v-card-text>
            <v-row>
              <v-col sm="3">
                <va-text-input
                  source="firstname"
                  :error-messages="firstnameErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="3">
                <va-text-input
                  source="lastname"
                  :error-messages="lastnameErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="3">
                <va-text-input
                  source="email"
                  :error-messages="emailErrors"
                ></va-text-input>
              </v-col>
            </v-row>

            <v-row class="mb-6" v-if="id">
              <v-col sm="3">
                <v-dialog v-model="dialog" persistent max-width="400px">
                  <template v-slot:activator="{ on, attrs }" v-if="id">
                    <v-btn color="primary" dark v-bind="attrs" v-on="on">
                      {{ $t("users.changePasswordButton") }}
                    </v-btn>
                  </template>
                  <v-card>
                    <v-card-title>
                      <span class="text-h5">{{
                        $t("users.changePasswordTitle")
                      }}</span>
                    </v-card-title>
                    <v-card-text>
                      <v-container>
                        <users-change-password
                          :key="renderKeyChangePassword"
                          :id="model.id"
                          @save="changePasswordModalSave"
                        >
                        </users-change-password>
                      </v-container>
                    </v-card-text>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="dialogClose">
                        {{ $t("dialog.closeButton") }}
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="3">
                <va-text-input
                  v-if="!id"
                  source="password"
                  :label="$t('users.password')"
                  :error-messages="passwordErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="3">
                <va-select-input
                  source="userRoles"
                  reference="roles"
                  :error-messages="userRoleErrors"
                  multiple
                ></va-select-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="2">
                <va-boolean-input
                  source="active"
                  hide-details
                ></va-boolean-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="2">
                <va-boolean-input source="emailActivation"></va-boolean-input>
              </v-col>
            </v-row>
            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>
<script>
import { generateId, generatePassword } from "../../js/common";
import {
  required,
  maxLength,
  minLength,
  email,
} from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      dialog: false,
      renderKeyChangePassword: 1001,
      model: {
        id: null,
        firstname: null,
        lastname: null,
        email: null,
        password: null,
        active: null,
        emailActivation: null,
        userRoles: null,
      },
    };
  },
  validations: {
    model: {
      firstname: {
        required,
        minLength: minLength(2),
        maxLength: maxLength(120),
      },
      lastname: {
        required,
        minLength: minLength(2),
        maxLength: maxLength(120),
      },
      userRoles: {
        required,
      },
      email: {
        required,
        email,
      },
      password: {
        minLength: minLength(8),
        maxLength: maxLength(16),
      },
    },
  },
  computed: {
    firstnameErrors() {
      const errors = [];
      const field = "firstname";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "2" }));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "120" }));
      return errors;
    },
    lastnameErrors() {
      const errors = [];
      const field = "lastname";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "2" }));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "120" }));
      return errors;
    },
    emailErrors() {
      const errors = [];
      const field = "email";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.email.required"));
      !this.$v["model"][field].email && errors.push(this.$t("v.email.invalid"));
      return errors;
    },
    passwordErrors() {
      const errors = [];
      const field = "password";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "8" }));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "16" }));
      return errors;
    },
    userRoleErrors() {
      const errors = [];
      const field = "userRoles";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  created() {
    this.model.id = generateId(this);
    if (!this.id) {
      this.model.password = generatePassword(8);
    }
  },
  methods: {
    toggleShowPassword() {
      if (this.showPassword) {
        this.showPassword = false;
      } else {
        this.showPassword = true;
      }
    },
    dialogClose() {
      this.dialog = false;
    },
    changePasswordModalSave() {
      this.dialogClose();
    },
  },
};
</script>
