<template>
  <div>
    <div class="d-flex justify-end"></div>
    <v-data-table
      :headers="headers"
      :items="getChildren"
      class="v-data-table__editable_wrapper no_top_border"
      :hide-default-footer="true"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>{{
            $t("employees.employeeChildren")
          }}</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn @click="createRowForm()" color="success" icon v-on="on">
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </template>
            <span>{{ $t("va.actions.create") }}</span>
          </v-tooltip>
        </v-toolbar>
      </template>
      <template
        v-for="field in getFields"
        v-slot:[`item.${field.source}`]="{ item }"
      >
        <template v-if="item._new || item.childId === editRowId">
          <template v-if="field.source == 'childId'">
            <va-text-input
              v-show="false"
              :key="field.source"
              v-model="form.childId"
              :source="field.source"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'childNameSurname'">
            <va-text-input
              :key="field.source"
              v-model="form.childNameSurname"
              :source="field.source"
              :error-messages="childNameSurnameErrors"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'childTckn'">
            <va-text-input
              :key="field.source"
              v-model="form.childTckn"
              :source="field.source"
              :error-messages="childTcknErrors"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'childBirthdate'">
            <va-date-input
              :key="field.source"
              v-model="form.childBirthdate"
              :source="field.source"
              format="short"
              :filled="false"
              :error-messages="childBirthDateErrors"
              disableFormState
            ></va-date-input>
          </template>
          <template v-if="field.source == 'childSchoolName'">
            <va-text-input
              :key="field.source"
              v-model="form.childSchoolName"
              :source="field.source"
              :filled="false"
              :error-messages="childSchoolNameErrors"
              disableFormState
            ></va-text-input>
          </template>
        </template>
        <template v-else>
          <span
            v-if="field.type == 'select'"
            :key="item.childId + field.source + '_label'"
            >{{ item[field.sourceLabel] }}</span
          >
          <span
            v-else-if="field.type == 'date'"
            :key="item.childId + field.source + '_label'"
          >
            {{
              item[field.source]
                ? new Date(item[field.source]).toLocaleDateString("tr")
                : ""
            }}
          </span>
          <span v-else :key="item.childId + field.source + '_label'">
            {{ item[field.source] }}
          </span>
        </template>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          small
          class="mr-2"
          v-if="item._new || item.childId === editRowId"
          @click="saveItem()"
        >
          mdi-content-save
        </v-icon>
        <v-icon
          small
          v-if="item._new || item.childId === editRowId"
          @click="cancel"
        >
          mdi-close
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.childId != editRowId"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.childId != editRowId"
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>

    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">{{
          $t("dialog.deleteConfirm")
        }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">{{
            $t("dialog.closeDeleteButton")
          }}</v-btn>
          <v-btn color="blue darken-1" text @click="deleteConfirm">{{
            $t("dialog.confirmDeleteButton")
          }}</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { required, minLength, maxLength } from "vuelidate/lib/validators";
import { generateUid } from "../../js/common";

export default {
  props: ["items"],
  data() {
    return {
      dialogDelete: false,
      form: null,
      editedIndex: -1,
      editRowId: null,
      children: null,
      headers: [
        {
          text: this.$t("employees.childNameSurname"),
          value: "childNameSurname",
        },
        {
          text: this.$t("employees.childTckn"),
          value: "childTckn",
        },
        {
          text: this.$t("employees.childBirthdate"),
          value: "childBirthdate",
        },
        {
          text: this.$t("employees.childSchoolName"),
          value: "childSchoolName",
        },
        {
          text: this.$t("employees.employeeChildrenActions"),
          value: "actions",
          sortable: false,
        },
      ],
      fields: [
        {
          source: "childNameSurname",
          label: this.$t("employees.childNameSurname"),
        },
        {
          source: "childTckn",
          label: this.$t("employees.childTckn"),
        },
        {
          source: "childBirthdate",
          label: this.$t("employees.childBirthdate"),
          type: "date",
        },
        {
          source: "childSchoolName",
          label: this.$t("employees.childSchoolName"),
        },
      ],
    };
  },
  computed: {
    getChildren() {
      if (this.form && !this.editRowId) {
        let items = [{ _new: true }, ...this.children];
        return items;
      }
      return this.children;
    },
    getFields() {
      let fields = this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label: f.label,
          };
        });
      fields.childId = null;
      return fields;
    },
    childNameSurnameErrors() {
      const errors = [];
      const field = "childNameSurname";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    childTcknErrors() {
      const errors = [];
      const field = "childTckn";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "11" }));
      !this.$v["form"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "11" }));
      return errors;
    },
    childBirthDateErrors() {
      const errors = [];
      const field = "childBirthdate";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    childSchoolNameErrors() {
      const errors = [];
      const field = "childSchoolName";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "160" }));
      return errors;
    },
  },
  validations: {
    form: {
      childNameSurname: {
        required,
      },
      childTckn: {
        minLength: minLength(11),
        maxLength: maxLength(11),
      },
      childBirthdate: {
        required,
      },
      childSchoolName: {
        maxLength: maxLength(160),
      },
    },
  },
  async created() {
    this.children = this.items;
  },
  watch: {
    dialogDelete(val) {
      val || this.close();
    },
  },
  methods: {
    editItem(item) {
      this.createRowForm("edit", item);
      this.editedIndex = this.children.indexOf(item);
    },
    createRowForm(action = "new", item = null) {
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
      if (action == "new") {
        this.editRowId = null;
        this.form.childId = generateUid();
      } else {
        this.editRowId = item ? item.childId : null;
      }
    },
    saveItem() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      if (this.editedIndex > -1) {
        Object.assign(this.children[this.editedIndex], this.form);
      } else {
        this.children.push(this.form);
      }
      this.$emit("save", this.children);
      this.close();
    },
    deleteItem(item) {
      this.editedIndex = this.children.indexOf(item);
      this.dialogDelete = true;
    },
    deleteConfirm() {
      this.children.splice(this.editedIndex, 1);
      this.$emit("save", this.children);
      this.close();
    },
    cancel() {
      this.close();
    },
    close() {
      this.$nextTick(async () => {
        this.editedIndex = -1;
        this.$v.$reset();
      });
      this.dialogDelete = false;
      this.form = null;
      this.editRowId = null;
    },
  },
};
</script>
