<template>
  <va-form :id="id" :item="item" disable-redirect v-model="model">
    <v-row>
      <v-col>
        <v-card>
          <v-card-text>
            <va-text-input
              source="companyName"
              :error-messages="companyNameErrors"
            ></va-text-input>
            <va-text-input
              source="taxOffice"
              :error-messages="taxOfficeErrors"
            ></va-text-input>
            <va-text-input
              source="taxNumber"
              :error-messages="taxNumberErrors"
            ></va-text-input>
            <va-text-input
              source="address"
              :error-messages="addressErrors"
            ></va-text-input>
            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>

<script>
/// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components

import { generateId } from "../../js/common";
import { required, maxLength, numeric } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      model: {
        id: null,
        companyName: null,
        taxOffice: null,
        taxNumber: null,
        address: null,
      },
    };
  },
  validations: {
    model: {
      companyName: {
        required,
        maxLength: maxLength(100),
      },
      taxOffice: {
        maxLength: maxLength(255),
      },
      taxNumber: {
        numeric,
        maxLength: maxLength(30),
      },
      address: {
        maxLength: maxLength(255),
      },
    },
  },
  computed: {
    companyNameErrors() {
      const errors = [];
      const field = "companyName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    taxOfficeErrors() {
      const errors = [];
      const field = "taxOffice";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    taxNumberErrors() {
      const errors = [];
      const field = "taxNumber";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].numeric &&
        errors.push(this.$t("v.number.numeric"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "60" }));
      return errors;
    },
    addressErrors() {
      const errors = [];
      const field = "address";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "255" }));
      return errors;
    },
  },
  created() {
    this.model.id = generateId(this);
  },
  methods: {},
};
</script>
