<template>
  <v-breadcrumbs :items="items" ></v-breadcrumbs>
</template>

<script>
/**
 * Default admin component for breadcrumbs, will generate automatically hierarchical links from current route.
 * Support hierarchical CRUD structure.
 */
export default {
  computed: {
    items() {
      let crumbs = []
      return this.$route.matched.map((route) => {
        var length = 0
        var myArray;
        if (route.meta) {
            myArray = route.meta.title.split(" ")
            if (route.meta.title.indexOf("#") > 0) {
                myArray.pop()  // remome #id source 
            }
        } else {
            myArray = route.name.split(" ")
            if (route.name.indexOf("#") > 0) {
                myArray.pop()  // remome #id source 
            }
        }
        var breadCrumb = myArray.join(" ")
        var item = breadCrumb.toLowerCase()
        crumbs.push(item)

        crumbs.forEach(function(val, index){
           if (val == crumbs[index -1]) {
             length = length + 1
           }
        })
        var text = route.meta ? this.$i18n.t("crumbs." + item) : this.$i18n.t("crumbs." + item);
        if (length == 1) {  // do not repat index list
            text = this.$i18n.t("crumbs.list")
        }
        if (route.meta.title && (route.meta.title.indexOf("failed") > 0 || route.meta.title.indexOf("Not found") == 0)) {
            text = ""
        }
        return {
          text: text,
          exact: true,
          to: route.path === "" ? "/" : route,
        };
      });
    },
  },
};
</script>

<style>
.v-breadcrumbs {
  background-color: #fff;
}
</style>
