<template>
  <va-form :id="id" :item="item" v-model="model">
    <v-row justify="center">
      <v-col sm="12">
        <v-card>
          <v-card-text>
            <v-row>
              <v-col sm="3">
                <va-select-input
                  source="customerId"
                  reference="customers"
                  :error-messages="customerIdErrors"
                ></va-select-input>
              </v-col>
              <v-col sm="6">
                <va-select-input
                  source="sowId"
                  reference="sows"
                  :filter="{ customerId: model.customerId }"
                  :key="model.customerId"
                  :error-messages="sowIdErrors"
                ></va-select-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="4">
                <va-autocomplete-input
                  source="employeeId"
                  reference="employees"
                  :filter="{ customerId: model.customerId }"
                  :key="model.customerId"
                  :error-messages="employeeIdErrors"
                ></va-autocomplete-input>
              </v-col>
              <v-col sm="2">
                <va-text-input
                  source="customerPersonelNo"
                  :error-messages="customerPersonelNoErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="2">
                <va-select-input
                  source="departmentId"
                  reference="departments"
                  :filter="{ customerId: model.customerId }"
                  :key="model.customerId"
                  :error-messages="departmentIdErrors"
                ></va-select-input>
              </v-col>
              <v-col sm="2">
                <va-select-input
                  v-if="model.departmentId"
                  source="subDepartmentId"
                  reference="subDepartments"
                  :filter="{ departmentId: model.departmentId }"
                  :key="model.departmentId"
                  :error-messages="subDepartmentIdErrors"
                ></va-select-input>
              </v-col>
              <v-col sm="2">
                <va-select-input
                  source="agreementTypeId"
                  reference="agreementTypes"
                  :error-messages="agreementTypeIdErrors"
                ></va-select-input>
              </v-col>
              <v-col sm="3">
                <va-text-input
                  source="jobTitle"
                  :error-messages="jobTitleErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="2">
                <va-date-input
                  source="startDate"
                  format="short"
                  :error-messages="startDateErrors"
                ></va-date-input>
              </v-col>
              <v-col sm="2">
                <va-date-input
                  source="endDate"
                  format="short"
                  :error-messages="endDateErrors"
                ></va-date-input>
              </v-col>
              <v-col sm="2">
                <va-boolean-input source="isEnd"></va-boolean-input>
              </v-col>
              <v-col sm="3">
                <va-text-input
                  source="endReason"
                  :error-messages="endReasonErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="2">
                <va-date-input
                  source="earlyEndDate"
                  format="short"
                ></va-date-input>
              </v-col>
              <v-col sm="2">
                <va-select-input
                  source="currencyId"
                  @change="detectCurrencyInput"
                  :error-messages="currencyIdErrors"
                ></va-select-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="2">
                <va-text-input
                  source="grossSalary"
                  lazy="true"
                  v-model="model.grossSalary"
                  v-money="money"
                ></va-text-input>
              </v-col>
              <v-col sm="2">
                <va-text-input
                  source="netSalary"
                  lazy="true"
                  v-model="model.netSalary"
                  v-money="money"
                ></va-text-input>
              </v-col>
              <v-col sm="2">
                <va-text-input
                  source="bonus"
                  lazy="true"
                  v-model="model.bonus"
                  v-money="money"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="2">
                <va-boolean-input source="oss"></va-boolean-input>
              </v-col>
              <v-col sm="2">
                <va-boolean-input source="car"></va-boolean-input>
              </v-col>
              <v-col sm="2">
                <va-boolean-input source="ticket"></va-boolean-input>
              </v-col>
              <v-col sm="2">
                <va-boolean-input source="transportation"></va-boolean-input>
              </v-col>
            </v-row>
            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>

<script>
/// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components
///
import { generateId } from "../../js/common";
import { required, maxLength } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      model: {
        id: null,
        employeeId: null,
        customerId: null,
        sowId: null,
        customerPersonelNo: null,
        departmentId: null,
        subDepartmentId: null,
        agreementTypeId: null,
        jobTitle: null,
        startDate: null,
        endDate: null,
        isEnd: false,
        earlyEndDate: null,
        endReason: null,
        currencyId: null,
        oss: false,
        car: false,
        ticket: false,
        transportation: false,
        grossSalary: "0",
        netSalary: "0",
        bonus: "0",
      },
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "",
        suffix: " TL",
        precision: 2,
        masked: false,
      } /* doesn't work with directive */,
    };
  },
  validations: {
    model: {
      employeeId: {
        required,
        maxLength: maxLength(36),
      },
      customerId: {
        required,
        maxLength: maxLength(36),
      },
      customerPersonelNo: {
        maxLength: maxLength(40),
      },
      sowId: {
        required,
        maxLength: maxLength(36),
      },
      departmentId: {
        required,
        maxLength: maxLength(36),
      },
      subDepartmentId: {
        maxLength: maxLength(36),
      },
      agreementTypeId: {
        required,
        maxLength: maxLength(36),
      },
      jobTitle: {
        required,
        maxLength: maxLength(100),
      },
      startDate: {
        required,
      },
      endDate: {
        required,
      },
      endReason: {
        maxLength: maxLength(255),
      },
      currencyId: {
        required,
        maxLength: maxLength(3),
      },
    },
  },
  computed: {
    employeeIdErrors() {
      const errors = [];
      const field = "employeeId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    customerIdErrors() {
      const errors = [];
      const field = "customerId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    customerPersonelNoErrors() {
      const errors = [];
      const field = "customerPersonelNo";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "40" }));
      return errors;
    },
    sowIdErrors() {
      const errors = [];
      const field = "sowId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    departmentIdErrors() {
      const errors = [];
      const field = "departmentId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    subDepartmentIdErrors() {
      const errors = [];
      const field = "subDepartmentId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    agreementTypeIdErrors() {
      const errors = [];
      const field = "agreementTypeId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    jobTitleErrors() {
      const errors = [];
      const field = "jobTitle";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    startDateErrors() {
      const errors = [];
      const field = "startDate";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    endDateErrors() {
      const errors = [];
      const field = "endDate";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    endReasonErrors() {
      const errors = [];
      const field = "endReason";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "255" }));
      return errors;
    },
    currencyIdErrors() {
      const errors = [];
      const field = "currencyId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "3" }));
      return errors;
    },
  },
  created() {
    this.model.id = generateId(this);
    if (!this.model.currencyId) {
      this.model.currencyId = "TRY";
    }
  },
  mounted() {
    if (this.item == null) {
      return;
    }
    if (this.item.currencyId == "TRY") {
      this.money.suffix = " TL";
    }
    if (this.item.currencyId == "EUR") {
      this.money.suffix = " EUR";
    }
    if (this.item.currencyId == "USD") {
      this.money.suffix = " USD";
    }
  },
  methods: {
    detectCurrencyInput() {
      if (this.model.currencyId == "TRY") {
        this.money.suffix = " TL";
      }
      if (this.model.currencyId == "EUR") {
        this.money.suffix = " EUR";
      }
      if (this.model.currencyId == "USD") {
        this.money.suffix = " USD";
      }
    },
  },
};
</script>
