<template>
  <div>
    <v-card>
      <v-card-text>
        <va-list
          :filters="filters"
          :items-per-page="10"
          :disableExport="true"
          :disableCreate="true"
          :disable-items-per-page="false"
          :disable-global-search="true"
          @update:filter="onFilterChange"
          @selected="onSelect"
        >
          <va-data-table
            v-if="customerId"
            :fields="fields"
            :disableEdit="true"
            row-create
            row-edit
            disable-create-redirect
            class="v-data-table__editable_wrapper"
          >
            <template v-slot:[`field.customerId`]="{ item }">
              <v-chip :color="item.customerColor" text-color="white">
                {{ item.customerShortName }}
              </v-chip>
            </template>
            <template v-slot:[`field.amount`]="{ item }">
              {{ item.amount | formatMoney(item.currencyId, 2) }}
            </template>
            <template v-slot:[`field.tax`]="{ item }">
              {{ item.tax | formatMoney("TRY", 2) }}
            </template>
            <template v-slot:[`field.totalAmount`]="{ item }">
              {{ item.totalAmount | formatMoney(item.currencyId, 2) }}
            </template>
            <template v-slot:[`field.whoPaidId`]="{ item }">
              {{ item.whoPaidId == "pernet" ? "Pernet" : "Çalışan" }}
            </template>
          </va-data-table>
          <template v-if="!customerId">
            <v-alert dense outlined type="error">
              {{ $t("alerts.firstChooseCustomer") }}
            </v-alert>
          </template>
          <template v-slot:actions>
            <va-action-button
              :label="$t('expenses.exportExcel')"
              icon="mdi-export"
              color="success"
              text
              @click="excelExport"
            ></va-action-button>
          </template>
        </va-list>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import isEmpty from "lodash/isEmpty";
import { required, minLength, maxLength } from "vuelidate/lib/validators";

export default {
  props: ["resource", "title"],
  provide() {
    return {
      validations: {
        customerId: { required },
        workplaceId: { required },
        sowId: { required },
        employeeId: { required },
        currencyId: { required },
        expenseTypeId: { required },
        whoPaidId: { required },
        docNumber: {
          minLength: minLength(3),
          maxLength: maxLength(100),
        },
      },
      errorMessages: {
        customerIdErrors() {
          return [{ required: null }];
        },
        workplaceIdErrors() {
          return [{ required: null }];
        },
        sowIdErrors() {
          return [{ required: null }];
        },
        currencyIdErrors() {
          return [{ required: null }];
        },
        employeeIdErrors() {
          return [{ required: null }];
        },
        expenseTypeIdErrors() {
          return [{ required: null }];
        },
        whoPaidIdErrors() {
          return [{ required: null }];
        },
        docNumberErrors() {
          return [{ minLength: 3 }, { maxLength: 100 }];
        },
      },
    };
  },
  data() {
    return {
      renderKey: 0,
      customerId: null,
      filters: [
        {
          source: "customerId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "customers",
          },
          filterId: "customerId",
          label: this.$t("expensetracking.customerId"),
          alwaysOn: true,
        },
        {
          source: "sowId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "sows",
          },
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expensetracking.sowId"),
          alwaysOn: true,
        },
        {
          source: "workplaceId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "workplaces",
          },
          label: this.$t("expensetracking.workplaceId"),
        },
        {
          source: "employeeId",
          type: "autocompletefilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "employees",
          },
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expensetracking.employeeComplete"),
          alwaysOn: true,
        },
        {
          source: "expenseTypeId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "expenseTypes",
          },
          label: this.$t("expensetracking.expenseTypeId"),
        },
        {
          source: "paymentTypeId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "paymentTypes",
          },
          label: this.$t("expensetracking.paymentTypeId"),
        },
        {
          source: "docNumber",
          type: "text",
          label: this.$t("expensetracking.docNumber"),
          alwaysOn: false,
        },
        {
          source: "expenseFormReceived",
          type: "boolean",
          label: this.$t("expensetracking.expenseFormReceived"),
          alwaysOn: true,
        },
        {
          source: "startDate",
          type: "date",
          label: this.$t("expensetracking.startDate"),
          alwaysOn: true,
        },
        {
          source: "endDate",
          type: "date",
          label: this.$t("expensetracking.endDate"),
          alwaysOn: true,
        },
        {
          source: "invoiceDateStart",
          type: "date",
          label: this.$t("expensetracking.invoiceDateStart"),
          alwaysOn: true,
        },
        {
          source: "invoiceDateEnd",
          type: "date",
          label: this.$t("expensetracking.invoiceDateEnd"),
          alwaysOn: true,
        },
      ],
      fields: [
        {
          source: "customerId",
          sourceLabel: "customerShortName",
          type: "select",
          reference: "customers",
          filterId: "customerId",
          label: this.$t("expensetracking.customerName"),
          sortable: true,
        },
        {
          source: "workplaceId",
          sourceLabel: "workplaceName",
          type: "select",
          reference: "workplaces",
          label: this.$t("expensetracking.workplaceName"),
          sortable: true,
        },
        {
          source: "sowId",
          sourceLabel: "sowName",
          type: "select",
          reference: "sows",
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expensetracking.sowName"),
          sortable: true,
        },
        {
          source: "departmentId",
          sourceLabel: "departmentName",
          type: "select",
          reference: "departments",
          filterId: "departmentId",
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expensetracking.departmentName"),
          sortable: true,
        },
        {
          source: "subDepartmentId",
          sourceLabel: "subDepartmentName",
          type: "select",
          reference: "subDepartments",
          label: this.$t("expensetracking.subDepartmentName"),
          key: "departmentId",
          filters: ["departmentId"],
          sortable: true,
        },
        {
          source: "employeeId",
          sourceLabel: "employeeName",
          type: "select",
          reference: "employees",
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expensetracking.employeeName"),
          sortable: true,
        },
        {
          source: "expenseTypeId",
          sourceLabel: "expenseTypeName",
          type: "select",
          reference: "expenseTypes",
          label: this.$t("expensetracking.expenseTypeId"),
          sortable: true,
        },
        {
          source: "paymentTypeId",
          sourceLabel: "paymentTypeName",
          type: "select",
          reference: "paymentTypes",
          label: this.$t("expensetracking.paymentTypeId"),
          sortable: true,
        },
        {
          source: "description",
          label: this.$t("expensetracking.description"),
          sortable: true,
        },
        {
          source: "startDate",
          type: "date",
          label: this.$t("expensetracking.startDate"),
          sortable: true,
        },
        {
          source: "endDate",
          type: "date",
          label: this.$t("expensetracking.endDate"),
          sortable: true,
        },
        {
          source: "invoiceDate",
          type: "date",
          label: this.$t("expensetracking.invoiceDate"),
          sortable: true,
        },
        {
          source: "docNumber",
          label: this.$t("expensetracking.docNumber"),
          sortable: true,
        },
        {
          source: "currencyId",
          type: "select",
          label: this.$t("expensetracking.currencyId"),
          sortable: true,
        },
        {
          source: "amount",
          type: "currency",
          options: {
            id: "currency",
            source: "currencyId",
            default: "TRY",
            extra: { precision: 2 },
          },
          label: this.$t("expensetracking.amount"),
          sortable: true,
        },
        {
          source: "tax",
          type: "currency",
          options: {
            id: "currency",
            source: "currencyId",
            default: "TRY",
            extra: { precision: 2 },
          },
          label: this.$t("expensetracking.tax"),
          sortable: true,
        },
        {
          source: "totalAmount",
          type: "currency",
          options: {
            id: "currency",
            source: "currencyId",
            default: "TRY",
            extra: { precision: 2 },
          },
          label: this.$t("expensetracking.totalAmount"),
          sortable: true,
        },
        {
          source: "whoPaidId",
          sourceLabel: "whoPaidId",
          type: "select",
          label: this.$t("expensetracking.whoPaidId"),
          sortable: true,
        },
        {
          source: "expenseFormReceived",
          label: this.$t("expensetracking.expenseFormReceived"),
          type: "boolean",
          sortable: true,
        },
      ],
    };
  },
  methods: {
    onFilterChange(newVal) {
      this.$store.commit("expenses/setFilterForm", newVal);
      let customerSelected = Object.prototype.hasOwnProperty.call(
        newVal,
        "customerId"
      );
      if (customerSelected && !isEmpty(newVal.customerId)) {
        this.customerId = true;
      } else {
        this.customerId = false;
      }
    },
    onSelect(selected) {
      this.selected = selected;
    },
    async excelExport() {
      let exportUrl = this.$admin.apiUrl + "/expensetracking/export";
      location.href = exportUrl;
    },
  },
};
</script>
