<template>
  <div>
    <div class="d-flex justify-end"></div>
    <v-data-table
      :headers="headers"
      :items="getNotes"
      class="v-data-table__editable_wrapper no_top_border"
      :hide-default-footer="true"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <!-- <v-toolbar-title>{{ $t("employees.notes") }}</v-toolbar-title> -->
          <!-- <v-spacer></v-spacer> -->
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn @click="createRowForm()" color="success" icon v-on="on">
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </template>
            <span>{{ $t("va.actions.create") }}</span>
          </v-tooltip>
        </v-toolbar>
      </template>
      <template
        v-for="field in getFields"
        v-slot:[`item.${field.source}`]="{ item }"
      >
        <template v-if="item._new || item.noteId === editRowId">
          <template v-if="field.source == 'subjectId'">
            <v-select
              :key="field.source"
              :source="field.source"
              v-model="form.subjectId"
              :item-text="'name'"
              :item-value="'id'"
              :items="noteSubjects"
              @change="setSubjectId($event)"
              :filled="false"
              :error-messages="subjectIdErrors"
              return-object
            ></v-select>
          </template>
          <template v-if="field.source == 'noteBody'">
            <va-text-input
              :key="field.source"
              :source="field.source"
              v-model="form.noteBody"
              :error-messages="noteBodyErrors"
              :filled="false"
              label=" "
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'noteStatus'">
            <v-select
              :key="field.source"
              :source="field.source"
              v-model="form.noteStatus"
              :item-text="'name'"
              :item-value="'id'"
              :items="noteStatuses"
              @change="setStatusId($event)"
              :filled="false"
              :error-messages="noteStatusErrors"
              return-object
            ></v-select>
          </template>
        </template>
        <template v-else>
          <span
            v-if="field.type == 'select' && field.source != 'noteStatus'"
            :key="field.source + '_label'"
          >
            {{ item[field.sourceLabel] }}
          </span>
          <span
            v-else-if="field.type == 'select' && field.source == 'noteStatus'"
            :key="field.source + '_label'"
          >
            <v-icon v-if="item[field.source] == 'solved'" color="green"
              >mdi-emoticon-happy</v-icon
            >
            <v-icon v-else-if="item[field.source] == 'postponed'" color="orange"
              >mdi-clock</v-icon
            >
            <v-icon v-else-if="item[field.source] == 'rejected'" color="red"
              >mdi-close</v-icon
            >
            <v-icon v-else color="red">mdi-emoticon-sad</v-icon>
          </span>
          <span v-else-if="field.type == 'date'" :key="field.source + '_label'">
            {{ item[field.source] ? dateFormat(item[field.source]) : "" }}
          </span>
          <span
            v-else-if="field.type == 'boolean'"
            :key="field.source + '_label'"
          >
            <v-icon v-if="item[field.source]" color="green">mdi-check</v-icon>
            <v-icon v-else color="red">mdi-close</v-icon>
          </span>
          <span v-else :key="field.source + '_label'">
            {{ item[field.source] }}
          </span>
        </template>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          small
          class="mr-2"
          v-if="item._new || item.noteId === editRowId"
          @click="saveItem()"
        >
          mdi-content-save
        </v-icon>
        <v-icon
          small
          v-if="item._new || item.noteId === editRowId"
          @click="cancel"
        >
          mdi-close
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.noteId != editRowId"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.noteId != editRowId"
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">{{
          $t("dialog.deleteConfirm")
        }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">{{
            $t("dialog.closeDeleteButton")
          }}</v-btn>
          <v-btn color="blue darken-1" text @click="deleteConfirm">{{
            $t("dialog.confirmDeleteButton")
          }}</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { generateUid } from "../../js/common";
import { required } from "vuelidate/lib/validators";

export default {
  props: ["items"],
  data() {
    return {
      dialogDelete: false,
      noteSubjects: null,
      noteStatuses: [],
      form: null,
      editedIndex: -1,
      editRowId: null,
      notes: null,
      headers: [
        {
          text: this.$t("employees.subjectId"),
          value: "subjectId",
        },
        {
          text: this.$t("employees.noteBody"),
          value: "noteBody",
          width: "40%",
        },
        {
          text: this.$t("employees.noteStatus"),
          value: "noteStatus",
        },
        {
          text: this.$t("employees.createdBy"),
          value: "createdByName",
          width: "12%",
        },
        {
          text: this.$t("employees.createdAt"),
          value: "createdAt",
        },
        {
          text: this.$t("employees.closedBy"),
          value: "closedByName",
          width: "12%",
        },
        {
          text: this.$t("employees.closedAt"),
          value: "closedAt",
        },
        {
          text: this.$t("employees.noteActions"),
          value: "actions",
          sortable: false,
        },
      ],
      fields: [
        {
          text: this.$t("employees.subjectId"),
          source: "subjectId",
          sourceLabel: "subjectName",
          type: "select",
        },
        {
          text: this.$t("employees.noteBody"),
          source: "noteBody",
        },
        {
          text: this.$t("employees.noteStatus"),
          source: "noteStatus",
          type: "select",
        },
        {
          text: this.$t("employees.createdBy"),
          source: "createdByName",
          type: "text",
        },
        {
          text: this.$t("employees.createdAt"),
          source: "createdAt",
          type: "date",
        },
        {
          text: this.$t("employees.closedBy"),
          source: "closedByName",
          type: "text",
        },
        {
          text: this.$t("employees.closedAt"),
          source: "closedAt",
          type: "date",
        },
      ],
    };
  },
  computed: {
    getNotes() {
      if (this.form && !this.editRowId) {
        let items = [{ _new: true }, ...this.notes];
        return items;
      }
      return this.notes;
    },
    getFields() {
      let fields = this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label: f.label,
          };
        });
      fields.notesId = null;
      return fields;
    },
    subjectIdErrors() {
      const errors = [];
      const field = "subjectId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    noteBodyErrors() {
      const errors = [];
      const field = "noteBody";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    noteStatusErrors() {
      const errors = [];
      const field = "noteStatus";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  async created() {
    this.notes = this.items;
    let response = await this.$admin.http.get("/noteSubjects/findAll");
    this.noteSubjects = response.data.data;
    let i = 0;
    for (const [key, value] of Object.entries(
      this.$t("resources.employees.enums.noteStatusId")
    )) {
      this.noteStatuses[i] = { id: key, name: value };
      ++i;
    }
  },
  validations: {
    form: {
      subjectId: {
        required,
      },
      noteBody: {
        required,
      },
      noteStatus: {
        required,
      },
    },
  },
  methods: {
    dateFormat(val) {
      var m = new Date(val);
      var dateString =
        m.getUTCDate() +
        "." +
        (m.getUTCMonth() + 1) +
        "." +
        m.getUTCFullYear() +
        " " +
        m.getUTCHours() +
        ":" +
        m.getUTCMinutes();
      return dateString;
    },
    setSubjectId(event) {
      this.form.subjectId = event.id;
      this.form.subjectName = event.name;
    },
    setStatusId(event) {
      this.form.noteStatus = event.id;
      this.form.noteStatusName = event.name;
    },
    createRowForm(action = "new", item = null) {
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
      if (action == "new") {
        this.editRowId = null;
        this.form.noteId = generateUid();
      } else {
        this.editRowId = item ? item.noteId : null;
      }
    },
    editItem(item) {
      this.createRowForm("edit", item);
      this.editedIndex = this.notes.indexOf(item);
    },
    saveItem() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      if (this.editedIndex > -1) {
        Object.assign(this.notes[this.editedIndex], this.form);
      } else {
        this.notes.push(this.form);
      }
      this.$emit("save", this.notes);
      this.close();
    },
    deleteItem(item) {
      this.editedIndex = this.notes.indexOf(item);
      this.dialogDelete = true;
    },
    deleteConfirm() {
      this.notes.splice(this.editedIndex, 1);
      this.$emit("save", this.notes);
      this.close();
    },
    cancel() {
      this.close();
    },
    close() {
      this.$nextTick(async () => {
        this.editedIndex = -1;
        this.$v.$reset();
      });
      this.form = null;
      this.dialogDelete = false;
      this.editRowId = null;
    },
  },
};
</script>
