<template>
  <div>
    <div class="d-flex align-center mb-2" v-if="show">
      <h3 class="display-0">{{ $t("profile.passwordChange") }}</h3>
    </div>
    <div>
      <v-row>
        <v-col sm="4">
          <v-form @submit.prevent="changePassword" v-if="show">
            <v-card>
              <v-card-text>
                <v-row>
                  <v-col>
                    <va-text-input
                      :label="$t('profile.currentPassword')"
                      type="password"
                      v-model="oldPassword"
                      :error-messages="oldPasswordErrors"
                    ></va-text-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col>
                    <va-text-input
                      :label="$t('profile.password')"
                      type="password"
                      v-model="newPassword"
                      :error-messages="newPasswordErrors"
                    ></va-text-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col>
                    <va-text-input
                      :label="$t('profile.passwordConfirmation')"
                      type="password"
                      v-model="passwordConfirmation"
                      :error-messages="passwordConfirmationErrors"
                    ></va-text-input>
                  </v-col>
                </v-row>
                <v-btn
                  color="primary"
                  :loading="passwordChanging"
                  type="submit"
                  >{{ $t("profile.update") }}</v-btn
                >
              </v-card-text>
            </v-card>
          </v-form>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
import {
  required,
  minLength,
  maxLength,
  sameAs,
} from "vuelidate/lib/validators";

export default {
  data() {
    return {
      show: true,
      passwordChanging: false,
      oldPassword: null,
      newPassword: null,
      passwordConfirmation: null,
    };
  },
  validations: {
    oldPassword: {
      required,
      minLength: minLength(8),
    },
    newPassword: {
      required,
      minLength: minLength(8),
      maxLength: maxLength(16),
    },
    passwordConfirmation: {
      required,
      sameAs: sameAs("newPassword"),
    },
  },
  computed: {
    oldPasswordErrors() {
      const errors = [];
      const field = "oldPassword";
      if (!this.$v[field].$dirty) return errors;
      !this.$v[field].required && errors.push(this.$t("v.text.required"));
      !this.$v[field].maxLength &&
        errors.push(this.$t("v.string.minLength", { min: "8" }));
      return errors;
    },
    newPasswordErrors() {
      const errors = [];
      const field = "newPassword";
      if (!this.$v[field].$dirty) return errors;
      !this.$v[field].required && errors.push(this.$t("v.text.required"));
      !this.$v[field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "8" }));
      !this.$v[field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "16" }));
      return errors;
    },
    passwordConfirmationErrors() {
      const errors = [];
      const field = "passwordConfirmation";
      if (!this.$v[field].$dirty) return errors;
      !this.$v[field].required && errors.push(this.$t("v.text.required"));
      !this.$v[field].sameAs && errors.push(this.$t("v.password.sameAs"));
      return errors;
    },
  },
  methods: {
    async update(method, url, data) {
      try {
        await this.$admin.http({ method, url, data });
        return true;
      } catch (e) {
        this.$snotify.warning(e.message);
      }
      return false;
    },
    async changePassword() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return false;
      }
      this.passwordChanging = true;
      try {
        await this.update("put", "/account/updatePassword", {
          oldPassword: this.oldPassword,
          newPassword: this.newPassword,
          passwordConfirmation: this.passwordConfirmation,
        });
        this.oldPassword = "";
        this.newPassword = "";
        this.passwordConfirmation = "";
        this.show = false;
        this.$router.push({ name: "dashboard" });
      } finally {
        this.passwordChanging = false;
      }
    },
  },
};
</script>
