<template>
  <div>
    <div v-if="isFormEnabled">
      <v-row>
        <v-col sm="12">
          <v-row>
            <v-col sm="12">
              <va-select-input
                v-model="model.target"
                @change="detectBatchInput($event)"
                :choices="choices"
                :label="$t('expensereports.target')"
                :error-messages="targetErrors"
              ></va-select-input>
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="12">
              <va-text-input
                v-if="batchInputType == 'text'"
                v-model="model.value"
                :label="$t('expensereports.value')"
              ></va-text-input>
              <va-boolean-input
                v-if="batchInputType == 'bool'"
                v-model="model.value"
                :label="$t('expensereports.value')"
              ></va-boolean-input>
            </v-col>
          </v-row>

          <v-row>
            <v-col sm="12">
              <va-text-input
                v-if="
                  batchInputType == 'bool' && model.target == 'sentForApproval'
                "
                v-model="model.extra.confirmDescription"
                :label="$t('expensereports.confirmDescription')"
              ></va-text-input>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col sm="12">
          <v-btn
            color="primary"
            :loading="saving"
            dark
            class="mb-2"
            @click="save"
          >
            {{ $t("dialog.batchUpdateButton") }}
          </v-btn>
        </v-col>
      </v-row>
    </div>
    <div v-else>
      <v-alert v-if="affectedRows > 0" dense text type="success"
        >{{ affectedRows }} adet kayıt başarıyla güncellendi</v-alert
      >
      <v-alert v-else dense text type="warning"
        >Seçilen kriterlerle herhangi bir kayıt eşleşmediğinden güncelleme
        gerçekleşmedi</v-alert
      >
    </div>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";

export default {
  props: [],
  data() {
    return {
      isFormEnabled: false,
      saving: false,
      affectedRows: 0,
      batchInputType: "text",
      choices: [
        {
          id: "expenseApplication",
          name: this.$t("expensereports.expenseApplication"),
        },
        { id: "reportNo", name: this.$t("expensereports.reportNo") },
        { id: "poNo", name: this.$t("expensereports.poNo") },
        {
          id: "pernetInvoiceNo",
          name: this.$t("expensereports.pernetInvoiceNo"),
        },
        {
          id: "sentForApproval",
          name: this.$t("expensereports.sentForApproval"),
        },
        { id: "isPaidOff", name: this.$t("expensereports.isPaidOff") },
      ],
      model: {
        target: null,
        value: null,
        extra: {
          confirmDescription: null,
        },
      },
    };
  },
  computed: {
    targetErrors() {
      const errors = [];
      const field = "target";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  validations: {
    model: {
      target: {
        required,
      },
    },
  },
  created() {
    this.isFormEnabled = true;
  },
  methods: {
    detectBatchInput(e) {
      if (
        e.id == "expenseApplication" ||
        e.id == "reportNo" ||
        e.id == "poNo" ||
        e.id == "pernetInvoiceNo"
      ) {
        this.batchInputType = "text";
        this.model.value = null;
      }
      if (e.id == "sentForApproval" || e.id == "isPaidOff") {
        this.batchInputType = "bool";
        this.model.value = 0;
      }
    },
    async save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.saving = true;
      try {
        let response = await this.update(
          "put",
          "/expensereports/batchUpdate",
          this.model
        );
        if (response.status == 200) {
          this.isFormEnabled = false;
          this.affectedRows = response.data.data.affectedRows;
          if (this.affectedRows > 0) {
            this.$store.dispatch("api/refresh", "expensereports");
          }
        }
      } finally {
        this.saving = false;
      }
    },
    async update(method, url, data) {
      let response = null;
      try {
        response = await this.$admin.http({ method, url, data });
      } catch (e) {
        this.$snotify.warning(e.message);
      }
      return response;
    },
  },
};
</script>
