<template>
  <v-app>
    <v-container
      fluid
      fill-height
      id="forgot-password-page"
      :style="{ backgroundColor: themeColor }"
    >
      <v-layout align-center justify-center>
        <v-flex :style="{ 'max-width': '350px' }">
          <v-card>
            <v-card-text>
              <v-row>
                <v-col sm="12" class="pa-3"></v-col>
              </v-row>
              <v-form ref="form" @submit.prevent="validate">
                <v-text-field
                  :label="$t('auth.username')"
                  prepend-icon="mdi-account"
                  v-model="username"
                  required
                  :error-messages="emailErrors"
                  @input="$v.username.$touch()"
                ></v-text-field>
                <v-row>
                  <v-col sm="12" class="pa-3"></v-col>
                </v-row>
                <v-row>
                  <v-col sm="12">
                    <div class="text-right">
                      <v-btn
                        :loading="loading"
                        color="primary"
                        large
                        type="submit"
                        text
                        rounded
                        >{{ $t("auth.resetMyPassword") }}</v-btn
                      >
                    </div>
                  </v-col>
                </v-row>
              </v-form>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
import { required, email } from "vuelidate/lib/validators";

export default {
  data() {
    return {
      username: null,
      loading: false,
      themeColor: localStorage.themeColor ? localStorage.themeColor : "#F26522",
    };
  },
  validations: {
    username: {
      required,
      email,
    },
  },
  computed: {
    emailErrors() {
      const errors = [];
      if (!this.$v.username.$dirty) return errors;
      !this.$v.username.email && errors.push(this.$t("v.email.invalid"));
      !this.$v.username.required && errors.push(this.$t("v.email.required"));
      return errors;
    },
  },
  methods: {
    async validate() {
      this.$v.$touch();
      this.loading = false;
      if (this.$v.$invalid) {
        return;
      }
      let result = false;
      result = await this.http("post", "/auth/sendResetPassword", {
        username: this.username,
      });
      if (result) {
        this.$snotify.success(this.$t("auth.passwordResetEmail"));
        this.$router.push({ name: "login" });
      }
    },
    clear() {
      this.$v.$reset();
      this.username = "";
    },
    async http(method, url, data) {
      try {
        this.loading = true;
        await this.$admin.http({ method, url, data });
        return true;
      } catch (e) {
        this.loading = false;
        console.log(e.message);
        // this.$snotify.warning(e.message);
      }
      return false;
    },
  },
};
</script>
