export function generateUid() {
  return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function (c) {
    var r = (Math.random() * 16) | 0,
      v = c == "x" ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
}
export function generateId(object) {
  return object.$route.name.slice(-6) == "create" ? generateUid() : object.id;
}
export function generateKey(keyValue, index = 0) {
  if (Array.isArray(keyValue)) {
    let ids = [];
    for (let i = 0; i < keyValue.length; i++) {
      ids.push(keyValue[i].id);
    }
    return ids.join() + "-" + String(index);
  }
  return keyValue + "-" + String(index);
}
export function findTotalMonths(startDate, endDate) {
  startDate = new Date(startDate);
  endDate = new Date(endDate);
  var totalMonth = Math.max(
    (endDate.getFullYear() - startDate.getFullYear()) * 12 +
      endDate.getMonth() -
      startDate.getMonth(),
    0
  );
  totalMonth = totalMonth + 1;
  return totalMonth;
}
export function dateAddMonth(date, numberOfMonth) {
  var currentDate = new Date(date);
  var newDate = currentDate.setMonth(currentDate.getMonth() + numberOfMonth);
  return new Date(newDate).toJSON().slice(0, 10);
}
export function generateColumnName(length) {
  var result = "";
  var characters = "abcdefghijklmnopqrstuvwxyz";
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}
export function dayDiff(firstDate, secondDate = null) {
  let date1 = new Date(firstDate);
  let date2 = new Date();
  if (secondDate) {
    date2 = new Date(secondDate);
  }
  const diffTime = Math.abs(date2 - date1);
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  return diffDays;
}
export function isAlpha(str) {
  return /[a-zA-Zğüçşıö]+$/.test(str);
}
export function arrayGroupBy(xs, key) {
  return xs.reduce(function (rv, x) {
    (rv[x[key]] = rv[x[key]] || []).push(x);
    return rv;
  }, {});
}
export function generatePassword(size) {
  // let characters = "a-z,A-Z,0-9,#"
  let characters = "a-z,0-9,$";
  let charactersArray = characters.split(",");
  let CharacterSet = "";
  let password = "";
  if (charactersArray.indexOf("a-z") >= 0) {
    CharacterSet += "abcdefghijklmnpqrstuvwxyz";
  }
  // if (charactersArray.indexOf('A-Z') >= 0) {
  //     CharacterSet += 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
  // }
  if (charactersArray.indexOf("0-9") >= 0) {
    CharacterSet += "123456789";
  }
  if (charactersArray.indexOf("$") >= 0) {
    CharacterSet += "#@_-?";
  }
  for (let i = 0; i < size; i++) {
    password += CharacterSet.charAt(
      Math.floor(Math.random() * CharacterSet.length)
    );
  }
  return password;
}
export function getQueryParam(name, url = window.location.href) {
  name = name.replace(/[[]]/g, "\\$&");
  var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
    results = regex.exec(url);
  if (!results) return null;
  if (!results[2]) return "";
  return decodeURIComponent(results[2].replace(/\+/g, " "));
}
export function formatFileSize(value) {
  if (value >= 1073741824) {
    value = value / 1073741824;
    value = Number.parseFloat(value).toFixed(2) + " gb";
  } else if (value >= 1048576) {
    value = value / 1048576;
    value = Number.parseFloat(value).toFixed(2) + " mb";
  } else if (value >= 1024) {
    value = value / 1024;
    value = Number.parseFloat(value).toFixed(2) + " kb";
  } else if (value > 1) {
    value = value + " b";
  } else if (value == 1) {
    value = value + " b";
  } else {
    value = "0 b";
  }
  return value;
}
export function requiredArray(value) {
  let array = JSON.parse(value);
  if (!Array.isArray(array)) {
    return false;
  }
  if (array.length > 0) {
    return true;
  }
  return false;
}

export const CommonMixin = {
  methods: {
    formatFileSize,
    arrayGroupBy,
    dayDiff,
    getQueryParam,
    generatePassword,
    isAlpha,
    generateId,
    generateKey,
    generateUid,
    findTotalMonths,
    generateColumnName,
    requiredArray,
  },
};
