<template>
  <div>
    <div class="d-flex justify-end"></div>
    <v-data-table
      :headers="headers"
      :items="getJobTitles"
      class="v-data-table__editable_wrapper no_top_border"
      :hide-default-footer="true"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn @click="createRowForm()" color="success" icon v-on="on">
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </template>
            <span>{{ $t("va.actions.create") }}</span>
          </v-tooltip>
        </v-toolbar>
      </template>
      <template
        v-for="field in getFields"
        v-slot:[`item.${field.source}`]="{ item }"
      >
        <template v-if="item._new || item.jobTitleId === editRowId">
          <template v-if="field.source == 'jobTitleId'">
            <v-select
              :key="field.source"
              :source="field.source"
              v-model="form.jobTitleId"
              :item-text="'name'"
              :item-value="'id'"
              :items="jobTitles"
              @change="setJobTitle($event)"
              :filled="false"
              :error-messages="jobTitleIdErrors"
              return-object
            ></v-select>
          </template>
        </template>
        <template v-else>
          <span v-if="field.type == 'select'" :key="field.source + '_label'">
            {{ item[field.sourceLabel] }}
          </span>
          <span v-else :key="field.source + '_label'">
            {{ item[field.source] }}
          </span>
        </template>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          small
          class="mr-2"
          v-if="item._new || item.jobTitleId === editRowId"
          @click="saveItem()"
        >
          mdi-content-save
        </v-icon>
        <v-icon
          small
          v-if="item._new || item.jobTitleId === editRowId"
          @click="cancel"
        >
          mdi-close
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.jobTitleId != editRowId"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.jobTitleId != editRowId"
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">{{
          $t("dialog.deleteConfirm")
        }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">{{
            $t("dialog.closeDeleteButton")
          }}</v-btn>
          <v-btn color="blue darken-1" text @click="deleteConfirm">{{
            $t("dialog.confirmDeleteButton")
          }}</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";

export default {
  props: ["items", "healthDocId"],
  data() {
    return {
      dialogDelete: false,
      healthDocJobTitles: null,
      form: null,
      editedIndex: -1,
      editRowId: null,
      jobTitles: [],
      headers: [
        {
          text: this.$t("healthdocs.jobTitleId"),
          value: "jobTitleId",
        },
        {
          text: this.$t("healthdocs.actions"),
          value: "actions",
          sortable: false,
        },
      ],
      fields: [
        {
          text: this.$t("healthdocs.jobTitleId"),
          source: "jobTitleId",
          sourceLabel: "jobTitleName",
          reference: "jobTitles",
          type: "select",
        },
      ],
    };
  },
  computed: {
    getJobTitles() {
      if (this.form && !this.editRowId) {
        let items = [{ _new: true }, ...this.healthDocJobTitles];
        return items;
      }
      return this.healthDocJobTitles;
    },
    getFields() {
      let fields = this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label: f.label,
          };
        });
      fields.healthDocId = this.healthDocId;
      return fields;
    },
    jobTitleIdErrors() {
      const errors = [];
      const field = "jobTitleId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  async created() {
    this.healthDocJobTitles = this.items;
    let response = await this.$admin.http.get("/jobTitles/findAll");
    this.jobTitles = response.data.data;
  },
  validations: {
    form: {
      jobTitleId: {
        required,
      },
    },
  },
  methods: {
    setJobTitle(event) {
      this.form.jobTitleId = event.id;
      this.form.jobTitleName = event.name;
    },
    createRowForm(action = "new", item = null) {
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
      if (action == "new") {
        this.editRowId = null;
        this.form.healthDocId = this.healthDocId;
      } else {
        this.editRowId = item ? item.jobTitleId : null;
      }
    },
    editItem(item) {
      this.createRowForm("edit", item);
      this.editedIndex = this.healthDocJobTitles.indexOf(item);
    },
    saveItem() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      if (this.editedIndex > -1) {
        Object.assign(this.healthDocJobTitles[this.editedIndex], this.form);
      } else {
        this.healthDocJobTitles.push(this.form);
      }
      this.$emit("save", this.healthDocJobTitles);
      this.close();
    },
    deleteItem(item) {
      this.editedIndex = this.healthDocJobTitles.indexOf(item);
      this.dialogDelete = true;
    },
    deleteConfirm() {
      this.healthDocJobTitles.splice(this.editedIndex, 1);
      this.$emit("save", this.healthDocJobTitles);
      this.close();
    },
    cancel() {
      this.close();
    },
    close() {
      this.$nextTick(async () => {
        this.editedIndex = -1;
        this.$v.$reset();
      });
      this.form = null;
      this.dialogDelete = false;
      this.editRowId = null;
    },
  },
};
</script>
