<template>
  <va-form :id="id" :item="item" v-model="model">
    <v-row>
      <v-col>
        <v-card>
          <v-card-text>
            <v-row>
              <v-col sm="3">
                <va-text-input
                  source="roleKey"
                  :error-messages="roleKeyErrors"
                ></va-text-input>
                <va-text-input
                  source="roleName"
                  :error-messages="roleNameErrors"
                ></va-text-input>
                <va-text-input
                  source="roleLevel"
                  :error-messages="roleLevelErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="6">
                <h2 class="display-0 pb-4">
                  {{ $t("menu.permissions") }}
                </h2>
                <va-text-input
                  v-show="false"
                  :label="$t('roles.permissions')"
                  v-model="model.rolePermissions"
                  hide-details
                ></va-text-input>
                <va-check-list-table
                  itemKey="permId"
                  itemsPerPage="30"
                  dataKey="rolePermissions"
                  groupBy="moduleName"
                  :initUrl="getInitUrl"
                  :item="item"
                  :headers="headers"
                  :fields="fields"
                  @selected="onSelectedItems"
                >
                </va-check-list-table>
              </v-col>
            </v-row>
            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>

<script>
/// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components

import { generateId } from "../../js/common";
import { required, maxLength, numeric } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      rolePermissions: [],
      permissions: [],
      model: {
        id: null,
        roleKey: null,
        roleName: null,
        roleLevel: null,
      },
      showError: false,
      errorMessage: "",
      fields: [
        { source: "route", label: this.$t("roles.route") },
        { source: "method", label: this.$t("roles.method") },
      ],
    };
  },
  validations: {
    model: {
      roleKey: {
        required,
        maxLength: maxLength(60),
      },
      roleName: {
        required,
        maxLength: maxLength(100),
      },
      roleLevel: {
        required,
        numeric,
        maxLength: maxLength(2),
      },
    },
  },
  computed: {
    headers() {
      return [
        {
          value: "groupName",
          sortable: false,
        },
        {
          text: this.$t("permissions.moduleName"),
          value: "moduleName",
          sortable: false,
        },
        {
          value: "route",
          text: this.$t("permissions.route"),
          sortable: false,
        },
        {
          value: "method",
          text: this.$t("permissions.method"),
          sortable: false,
        },
      ];
    },
    roleKeyErrors() {
      const errors = [];
      const field = "roleKey";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "60" }));
      return errors;
    },
    roleNameErrors() {
      const errors = [];
      const field = "roleName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    roleLevelErrors() {
      const errors = [];
      const field = "roleLevel";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].numeric &&
        errors.push(this.$t("v.number.numeric"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "2" }));
      return errors;
    },
  },
  created() {
    this.model.id = generateId(this);
    this.initializeRolePermissions();
  },
  methods: {
    getInitUrl() {
      let url = "/permissions/findAll"
      return url
    },
    initializeRolePermissions() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "rolePermissions")
      ) {
        this.rolePermissions = JSON.parse(this.item.rolePermissions)
        this.model.rolePermissions = this.item.rolePermissions
      }
    },
    onSelectedItems(selected) {
      this.model.rolePermissions = JSON.stringify(selected)
    },

  },

}
</script>
