<template>
  <div>
    <div class="d-flex align-center mb-2">
      <h3 class="display-0">{{ $t("profile.account") }}</h3>
    </div>
    <div>
      <v-row>
        <v-col sm="12">
          <v-form @submit.prevent="updateAccount">
            <v-card>
              <v-card-text>
                <v-row>
                  <v-col sm="3">
                    <va-text-input
                      :label="$t('profile.firstname')"
                      v-model="firstname"
                      :error-messages="firstnameErrors"
                    ></va-text-input>
                    <va-text-input
                      :label="$t('profile.lastname')"
                      v-model="lastname"
                      :error-messages="lastnameErrors"
                    ></va-text-input>
                    <va-text-input
                      :label="$t('profile.email')"
                      v-model="email"
                      type="email"
                      :error-messages="emailErrors"
                    ></va-text-input>
                    <va-color-picker-input
                      dot-size="25"
                      mode="hexa"
                      show-swatches
                      swatches-max-height="150"
                      v-model="themeColor"
                      :label="$t('profile.themeColor')"
                      :error-messages="themeColorErrors"
                    ></va-color-picker-input>
                  </v-col>
                </v-row>
                <!--                 <v-row class="mt-10"></v-row> -->
                <v-btn
                  color="primary"
                  :loading="accountUpdating"
                  type="submit"
                  >{{ $t("profile.update") }}</v-btn
                >
              </v-card-text>
            </v-card>
          </v-form>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import {
  required,
  email,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";

export default {
  data() {
    return {
      accountUpdating: false,
      themeColor: "#F26522",
      firstname: null,
      lastname: null,
      email: null,
    };
  },
  validations: {
    firstname: {
      required,
      maxLength: maxLength(120),
    },
    lastname: {
      required,
      maxLength: maxLength(120),
    },
    email: {
      required,
      email,
    },
    themeColor: {
      required,
      minLength: minLength(7),
      maxLength: maxLength(7),
    },
  },
  computed: {
    // color: {
    //   get() {
    //     return this.themeColor;
    //   },
    //   set(v) {
    //     this.themeColor = v;
    //   },
    // },
    firstnameErrors() {
      const errors = [];
      const field = "firstname";
      if (!this.$v[field].$dirty) return errors;
      !this.$v[field].required && errors.push(this.$t("v.text.required"));
      !this.$v[field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "120" }));
      return errors;
    },
    lastnameErrors() {
      const errors = [];
      const field = "lastname";
      if (!this.$v[field].$dirty) return errors;
      !this.$v[field].required && errors.push(this.$t("v.text.required"));
      !this.$v[field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "120" }));
      return errors;
    },
    emailErrors() {
      const errors = [];
      const field = "email";
      if (!this.$v[field].$dirty) return errors;
      !this.$v[field].required && errors.push(this.$t("v.email.required"));
      !this.$v[field].email && errors.push(this.$t("v.email.invalid"));
      return errors;
    },
    themeColorErrors() {
      const errors = [];
      const field = "themeColor";
      if (!this.$v[field].$dirty) return errors;
      !this.$v[field].required && errors.push(this.$t("v.text.required"));
      !this.$v[field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "7" }));
      !this.$v[field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "7" }));
      return errors;
    },
  },
  created() {
    this.initializeAccount();
  },
  methods: {
    ...mapActions({
      checkAuth: "auth/checkAuth",
    }),
    async initializeAccount() {
      let response = await this.$admin.http.get("/account/findMe");
      let data = response.data.data;
      this.firstname = data.firstname;
      this.lastname = data.lastname;
      this.email = data.email;
      this.themeColor = data.themeColor ? data.themeColor : "#039BE5";
    },
    async update(method, url, data) {
      try {
        await this.$admin.http({ method, url, data });
        this.$vuetify.theme.themes.light.primary = this.themeColor;
        localStorage.setItem("themeColor", this.themeColor);
        return true;
      } catch (e) {
        this.$snotify.warning(e.message);
      }
      return false;
    },
    async updateAccount() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return false;
      }
      this.accountUpdating = true;
      try {
        if (
          await this.update("put", "/account/update", {
            firstname: this.firstname,
            lastname: this.lastname,
            email: this.email,
            themeColor: this.themeColor,
          })
        ) {
          /**
           * Recheck auth
           */
          this.checkAuth();
        }
      } finally {
        this.accountUpdating = false;
      }
    },
  },
};
</script>
