<template>
  <va-form :id="id" :item="item" v-model="model">
    <v-row justify="center">
      <v-col sm="12">
        <v-card>
          <v-card-text>
            <v-row>
              <v-col sm="4">
                <va-select-input
                  source="customerId"
                  reference="customers"
                  :error-messages="customerIdErrors"
                ></va-select-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="6">
                <va-select-input
                  source="departments"
                  reference="departments"
                  :filter="{ customerId: model.customerId }"
                  :key="model.customerId"
                  :error-messages="departmentsErrors"
                  multiple
                ></va-select-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="2">
                <va-text-input
                  source="purchaserName"
                  :error-messages="purchaserNameErrors"
                ></va-text-input>
              </v-col>
              <v-col sm="2">
                <va-text-input
                  source="purchaserSurname"
                  :error-messages="purchaserSurnameErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="2">
                <va-select-input
                  source="phoneAreaCodeId"
                  reference="areaCodes"
                ></va-select-input>
              </v-col>
              <v-col sm="2">
                <va-text-input
                  source="contactPhone"
                  :error-messages="contactPhoneErrors"
                ></va-text-input>
              </v-col>
              <v-col sm="2">
                <va-select-input
                  source="mobileAreaCodeId"
                  reference="areaCodes"
                ></va-select-input>
              </v-col>
              <v-col sm="2">
                <va-text-input
                  source="contactMobile"
                  :error-messages="contactMobileErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="3">
                <va-text-input
                  source="contactEmail"
                  :error-messages="contactEmailErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>

<script>
/// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components

import { generateId } from "../../js/common";
import {
  required,
  minLength,
  maxLength,
  email,
} from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      itemLocal: this.item,
      model: {
        id: null,
        customerId: null,
        departments: null,
        purchaserName: null,
        purchaserSurname: null,
        phoneAreaCodeId: null,
        contactPhone: null,
        mobileAreaCodeId: null,
        contactMobile: null,
        contactEmail: null,
      },
    };
  },
  validations: {
    model: {
      customerId: {
        required,
        maxLength: maxLength(36),
      },
      departments: {
        required,
      },
      purchaserName: {
        required,
        maxLength: maxLength(100),
      },
      purchaserSurname: {
        required,
        maxLength: maxLength(255),
      },
      contactPhone: {
        minLength: minLength(10),
      },
      contactMobile: {
        minLength: minLength(10),
      },
      contactEmail: {
        email,
      },
    },
  },
  computed: {
    customerIdErrors() {
      const errors = [];
      const field = "customerId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    departmentsErrors() {
      const errors = [];
      const field = "departments";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    purchaserNameErrors() {
      const errors = [];
      const field = "purchaserName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    purchaserSurnameErrors() {
      const errors = [];
      const field = "purchaserSurname";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    contactSurnameErrors() {
      const errors = [];
      const field = "contactSurname";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    contactEmailErrors() {
      const errors = [];
      const field = "contactEmail";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].email && errors.push(this.$t("v.email.invalid"));
      return errors;
    },
    contactPhoneErrors() {
      const errors = [];
      const field = "contactPhone";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "10" }));
      return errors;
    },
    contactMobileErrors() {
      const errors = [];
      const field = "contactMobile";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "10" }));
      return errors;
    },
  },
  created() {
    this.model.id = generateId(this);
  },
  methods: {},
};
</script>
