  created() {
    this.model.id = generateId(this);
    this.initializeEmployeeChildren();
    this.initializeEmployeeInfoSheet();
    this.initializeEmployeeAgreements();
    this.initializeEmployeeEducations();
    this.initializeEmployeeHealthDocs();
    this.initializeEmployeeDebits();
    this.initializeEmployeeNotes();
    if (this.item == null) {
      this.model.countryId = "TR"; // default selected
    }
  },
  async mounted() {
    if (this.item == null) {
      let response = await this.$admin.http.get(
        "/employees/findInfoSheetFields"
      );
      this.employeeInfoSheet = response.data.data;
    }
  },
  methods: {
    renderComponent(name) {
      let variableName = "renderKey" + name;
      this[variableName] = this[variableName] + 1;
    },
    docFormTitle() {
      if (this.docEditedIndex === -1) {
        this.docDialogAction = "Add";
        return this.$t("dialog.addNew");
      }
      this.docDialogAction = "Update";
      return this.docEditedItem.fieldLabel;
    },
    detectCurrencyInput() {
      if (this.model.currencyId == "TRY") {
        this.currencyId = "TRY";
      }
      if (this.model.currencyId == "EUR") {
        this.currencyId = "EUR";
      }
      if (this.model.currencyId == "USD") {
        this.currencyId = "USD";
      }
    },
    setCustomerName(event) {
      this.model.customerName = event.name;
      this.renderKeyAgreements = this.renderKeyAgreements + 1;
    },
    initializeEmployeeChildren() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "employeeChildren")
      ) {
        this.model.employeeChildren = this.item.employeeChildren;
        this.employeeChildren = JSON.parse(this.item.employeeChildren);
      }
    },
    initializeEmployeeInfoSheet() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "employeeInfoSheet")
      ) {
        this.model.employeeInfoSheet = this.item.employeeInfoSheet;
        this.employeeInfoSheet = JSON.parse(this.item.employeeInfoSheet);
      }
    },
    initializeEmployeeAgreements() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "employeeAgreements")
      ) {
        this.model.employeeAgreements = this.item.employeeAgreements;
        this.employeeAgreements = JSON.parse(this.item.employeeAgreements);
      }
    },
    initializeEmployeeEducations() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "employeeEducations")
      ) {
        this.model.employeeEducations = this.item.employeeEducations;
        this.employeeEducations = JSON.parse(this.item.employeeEducations);
      }
    },
    initializeEmployeeHealthDocs() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "employeeHealthDocs")
      ) {
        this.model.employeeHealthDocs = this.item.employeeHealthDocs;
        this.employeeHealthDocs = JSON.parse(this.item.employeeHealthDocs);
      }
    },
    initializeEmployeeDebits() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "employeeDebits")
      ) {
        this.model.employeeDebits = this.item.employeeDebits;
        this.employeeDebits = JSON.parse(this.item.employeeDebits);
      }
    },
    initializeEmployeeNotes() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "employeeNotes")
      ) {
        this.model.employeeNotes = this.item.employeeNotes;
        this.employeeNotes = JSON.parse(this.item.employeeNotes);
      }
    },
    onValidate() {
      this.tabEducationFields = Object.keys(this.model.employeeEducation);
      this.tabPersonalFields = Object.keys(this.model.employeePersonal);
      this.tabEmployeeFields = Object.keys(this.model);
      let v = this.$v;
      let tabEmployeeIsInvalid = false;
      let employeeProps = this.tabEmployeeFields;
      for (let e = 0; e < employeeProps.length; e++) {
        let eKey = employeeProps[e];
        if (
          typeof v["model"][eKey] != "undefined" &&
          v["model"][eKey].$invalid
        ) {
          tabEmployeeIsInvalid = true;
        }
      }
      let tabEducationIsInvalid = false;
      let educationProps = this.tabEducationFields;
      for (let ed = 0; ed < educationProps.length; ed++) {
        let edKey = educationProps[ed];
        if (
          typeof v["model"]["employeeEducation"][edKey] != "undefined" &&
          v["model"]["employeeEducation"][edKey].$invalid
        ) {
          tabEducationIsInvalid = true;
        }
      }
      let tabPersonalIsInvalid = false;
      let personalProps = this.tabPersonalFields;
      for (let p = 0; p < personalProps.length; p++) {
        let pKey = personalProps[p];
        if (
          typeof v["model"]["employeePersonal"][pKey] != "undefined" &&
          v["model"]["employeePersonal"][pKey].$invalid
        ) {
          tabPersonalIsInvalid = true;
        }
      }
      if (tabEmployeeIsInvalid) {
        this.tabs = 0;
      }
      if (tabEducationIsInvalid) {
        this.tabs = 1;
      }
      if (tabPersonalIsInvalid) {
        this.tabs = 2;
      }
    },
    saveChildren(employeeChildren) {
      this.employeeChildren = employeeChildren;
      this.model.employeeChildren = JSON.stringify(employeeChildren);
    },
    saveInfoSheet(employeeInfoSheet) {
      this.employeeInfoSheet = employeeInfoSheet;
      this.model.employeeInfoSheet = JSON.stringify(employeeInfoSheet);
    },
    saveAgreements(employeeAgreements) {
      this.employeeAgreements = employeeAgreements;
      this.model.employeeAgreements = JSON.stringify(employeeAgreements);
    },
    saveEducations(employeeEducations) {
      this.employeeEducations = employeeEducations;
      this.model.employeeEducations = JSON.stringify(employeeEducations);
    },
    saveHealthdocs(employeeHealthDocs) {
      this.employeeHealthDocs = employeeHealthDocs;
      this.model.employeeHealthDocs = JSON.stringify(employeeHealthDocs);
    },
    saveDebits(employeeDebits) {
      this.employeeDebits = employeeDebits;
      this.model.employeeDebits = JSON.stringify(employeeDebits);
    },
    saveNotes(employeeNotes) {
      this.employeeNotes = employeeNotes;
      this.model.employeeNotes = JSON.stringify(employeeNotes);
    },
  },
};
