<template>
	<div>
		<v-row>
			<v-col :sm="col">
			  <v-data-table
			    v-model="selectedItems"
			    :item-key="itemKey"
			    :headers="headers"
			    :loading="loading"
			    :items="items"
			    :items-per-page="Number(itemsPerPage)"
			    :group-by="groupBy"
			    :custom-filter="filterText"
			    hide-default-footer
			  >
			    <template v-slot:top>
			      <v-text-field
			        v-model="search"
			        clearable
			        @keyup="searchDb"
			        @click:clear="clearSearchData"
			        :label="$t('va.actions.q')"
			        class="mx-4"
			      ></v-text-field>
			    </template>
			    <template
			      v-slot:[`group.header`]="{
			        isOpen,
			        toggle,
			        group,
			        isMobile,
			      }"
			    >
			      <td
			        class="text-start"
			        :colspan="isMobile ? undefined : headers.length"
			      >
			        <div style="display: flex; justify: center">
			          <div style="max-width: 10%">
			            <v-btn class="ma-0" icon small @click="toggle">
			              <v-icon>{{ isOpen ? "$minus" : "$plus" }}</v-icon>
			            </v-btn>
			          </div>
			          <div>
			            <v-checkbox
			              v-model="parentChecked[group]"
			              :label="group"
			              style="margin-top: 0px"
			              @change="toggleChildren(group, $event, toggle)"
			              hide-details="true"
			            ></v-checkbox>
			          </div>
			        </div>
			      </td>
			    </template>
			    <template v-slot:item="{ item, isSelected, select }">
			      <tr
			        style="cursor: pointer"
			        :class="isSelected ? 'v-data-table__selected' : ''"
			        @click="
			          toggle(
			            isSelected,
			            select,
			            $event,
			            item[groupBy]
			          )
			        "
			      >
			        <td class="d-flex align-center">
			          <v-icon size="16" color="green" v-if="isSelected"
			            >mdi-check</v-icon
			          >
			          <div v-if="!isSelected" style="width: 25px">
			            &nbsp;
			          </div>
			        </td>
							<td v-for="(field) in getFields" :key="field.source">
									{{ item[field.source] }}
							</td>
			      </tr>
			    </template>
			  </v-data-table>
			  <v-row class="text-right">
			    <v-col>
						<v-btn
			        class="mb-2"
			        small
			        v-if="
			          items.length > 0 && selectedItems.length > 0
			        "
			        @click="addItems"
			      >
			        {{ $t("sows.addEmployeeButton") }}
			      </v-btn>
			    </v-col>
			  </v-row>
			</v-col>
		</v-row>
		<v-row>
			<v-col :sm="col">
			  <template>
			    <v-data-table
			      v-model="selectedSelected"
			      :fields="selectedFields"
			      :headers="selectedHeaders"
			      :items="selectedDataItems"
			      :single-select="false"
			      class="v-data-table__editable_wrapper no_top_border"
			      show-select
			      hide-default-footer
			    >
			      <template v-slot:top>
			        <v-toolbar
			          flat
			          color="blue lighten-5"
			          style="padding-left: 16px; padding-right: 16px"
			          v-if="
			            selectedSelected.length > 0 &&
			            selectedDataItems.length > 0
			          "
			        >
			          {{
			            $tc(
			              "va.datatable.selected_items",
			              selectedSelected.length
			            )
			          }}
			          <v-spacer></v-spacer>
			          <div>
									<v-btn
			              :color="'red'"
			              small
			              text
			              @click="deleteItems"
			            >
			              <v-icon small>mdi-delete-forever</v-icon>
			              <span class="ml-2">
			                {{ $t("va.actions.delete") }}
			              </span>
			            </v-btn>
			          </div>
			        </v-toolbar>
			      </template>
			    </v-data-table>
			  </template>
			</v-col>
		</v-row>
	</div>
</template>

<script>
import { arrayGroupBy } from "../../../mixins/common";

export default {
  props: {
    col: {
      type: [String, Number],
      default: () => 6,
    },
    item: {
      type: Object,
      default: () => {},
    },
    itemsPerPage: {
      type: [String, Number],
      default: () => 50,
    },
    itemKey: {
      type: String
    },
    searchUrl: {
      type: [Function],
    },
    headers: {
      type: Array,
      default: () => [],
    },
    selectedHeaders: {
      type: Array,
      default: () => [],
    },
    fields: {
      type: Array,
      default: () => [],
    },
    selectedFields: {
      type: Array,
      default: () => [],
    },
    groupBy: {
      type: String
    },
    dataKey: {
      type: String
    },
  },
  data() {
    return {
      items: [],
      selected: [],
      search: "",
      loading: false,
      parentChecked: [],
      selectedSelected: [],
      selectedItems: [],
      groupedItems: [],
      selectedDataItems: [],
    }
  },
  created() {
  	this.initializeParentCheckboxes()
    if (
      typeof this.item === "object" &&
      this.item !== null &&
      Object.prototype.hasOwnProperty.call(this.item, this.dataKey)
    ) {
			this.selectedDataItems = JSON.parse(this.item[this.dataKey])
    }	
  },
  computed: {
    getFields() {
      return this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            // type: f.type,
            label: f.label
          };
        });
    },
  },
  methods: {
    clearSearchData() {
      this.items = [];
    },
    async searchDb() {
      await this.searchItems(this.search);
    },
    async searchItems(q) {
      this.loading = true;
      let url = this.searchUrl()
      if (q) {
        url = url + "&q=" + q;
      }
      let response = await this.$admin.http.get(url)
      this.items = response.data.data
      this.groupedItems = arrayGroupBy(this.items, this.groupBy)
      this.loading = false
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, this.dataKey)
      ) {
        this.selectedItems = JSON.parse(this.item[this.dataKey])
        this.initializeParentCheckboxes()
      }
    },
    filterText(value, search) {
      return (
        value != null &&
        search != null &&
        typeof value === "string" &&
        value.toString().toLocaleLowerCase().indexOf(search) !== -1
      );
    },
    initializeParentCheckboxes() {
      let selectedGroupItems = arrayGroupBy(
        this.selectedItems,
        this.groupBy
      );
      let Self = this;
      Object.keys(this.groupedItems).forEach(function (group) {
        let childrenCount = Self.groupedItems[group].length;
        if (
          typeof selectedGroupItems[group] != "undefined" &&
          selectedGroupItems[group].length == childrenCount
        ) {
          Self.parentChecked[group] = true;
        }
      });
    },
    addItems() {
      let Self = this;
      if (
        Array.isArray(this.selectedItems) &&
        this.selectedItems.length > 0
      ) {
        this.selectedItems.forEach(function (val) {
          let doesExist = Self.selectedDataItems.some(function (ele) {
            return ele.id === val.id;
          });
          // do not add same record two times
          //
          if (!doesExist) {
            Self.selectedDataItems.push(val);
          }
        });
      }
      this.$emit("selected", this.selectedDataItems)
    },
    deleteItems() {
      let Self = this;
      if (
        Array.isArray(this.selectedSelected) &&
        this.selectedSelected.length > 0
      ) {
        this.selectedSelected.forEach(function (val) {
          Self.selectedDataItems = Self.selectedDataItems.filter(function (ele) {
            return ele.id !== val.id;
          });
        });
      }
      this.$emit("selected", this.selectedDataItems)
    },
 		toggle(isSelected, select, e, group) {
      select(!isSelected);
      let selectedChildrenCount = this.groupedItems[group].length;
      let selectedGroupCount = 1;
      let Self = this
      this.selectedItems.forEach(function (val) {
        if (val[Self.groupBy] == group) {
          if (isSelected) {
            --selectedGroupCount;
          } else {
            ++selectedGroupCount;
          }
        }
      });
      if (selectedGroupCount == selectedChildrenCount) {
        this.parentChecked[group] = true;
      } else {
        this.parentChecked[group] = false;
      }
    },
    toggleChildren(group, e) {
      const Self = this;
      if (e) {
        // mark child checkboxes
        let selectedIds = [];
        this.selectedItems.forEach(function (val) {
          selectedIds.push(val[Self.itemKey]);
        });
        if (typeof this.groupedItems[group] != "undefined") {
          this.groupedItems[group].forEach(function (gVal) {
            if (!selectedIds.includes(gVal[Self.itemKey])) {
              Self.selectedItems.push(gVal);
            }
          });
          // toggleParent()
        }
      } else {
        // unmark child checkboxes
        if (typeof this.groupedItems[group] != "undefined") {
          this.groupedItems[group].forEach(function (gVal) {
            Self.selectedItems = Self.selectedItems.filter(
              (item) => item[Self.itemKey] != gVal[Self.itemKey]
            );
          });
          // toggleParent()
        }
      }
    },


  }

}
</script>