<template>
  <va-form :id="id" :item="item" v-model="model">
    <v-row justify="center">
      <v-col sm="12">
        <v-card flat>
          <v-card-text>
            <v-row>
              <v-col sm="4">
                <va-text-input
                  source="educationName"
                  :error-messages="educationNameErrors"
                ></va-text-input>
              </v-col>
              <v-col sm="2">
                <va-text-input
                  source="educationValidity"
                  :error-messages="educationValidityErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="6">
                <va-text-input
                  v-show="false"
                  :label="$t('educations.educationJobTitles')"
                  source="model.educationJobTitles"
                  v-model="model.educationJobTitles"
                ></va-text-input>
                <template>
                  <v-data-table
                    :headers="headers"
                    :items="educationJobTitles"
                    sort-by="name"
                    hide-default-footer
                    class="elevation-1"
                  >
                    <template v-slot:top>
                      <v-toolbar flat>
                        <v-toolbar-title>{{
                          $t("educations.educationJobTitles")
                        }}</v-toolbar-title>
                        <v-divider class="mx-4" inset vertical></v-divider>
                        <v-spacer></v-spacer>
                        <v-dialog v-model="dialog" max-width="500px">
                          <template v-slot:activator="{ on, attrs }">
                            <v-btn
                              color="primary"
                              dark
                              class="mb-2"
                              v-bind="attrs"
                              v-on="on"
                            >
                              {{ $t("educations.educationJobTitleAddButton") }}
                            </v-btn>
                          </template>
                          <v-card>
                            <v-card-title>
                              <span class="text-h5">{{ formTitle }}</span>
                            </v-card-title>
                            <v-card-text>
                              <v-container>
                                <v-row>
                                  <v-col sm="12" md="12">
                                    <va-text-input
                                      v-show="false"
                                      source="editedItem.jobTitleName"
                                      :label="$t('educations.jobTitleName')"
                                    ></va-text-input>
                                    <va-select-input
                                      v-model="editedItem.jobTitleId"
                                      reference="jobTitles"
                                      @change="setJobTitleName($event)"
                                      :label="$t('educations.jobTitleName')"
                                    ></va-select-input>
                                  </v-col>
                                </v-row>
                              </v-container>
                            </v-card-text>
                            <v-card-actions>
                              <v-spacer></v-spacer>
                              <v-btn color="blue darken-1" text @click="close">
                                {{ $t("dialog.cancelButton") }}
                              </v-btn>
                              <v-btn color="blue darken-1" text @click="save">
                                {{ $t("dialog.saveButton") }}
                              </v-btn>
                            </v-card-actions>
                          </v-card>
                        </v-dialog>
                        <v-dialog v-model="dialogDelete" max-width="500px">
                          <v-card>
                            <v-card-title class="text-h5">{{
                              $t("dialog.deleteConfirm")
                            }}</v-card-title>
                            <v-card-actions>
                              <v-spacer></v-spacer>
                              <v-btn
                                color="blue darken-1"
                                text
                                @click="closeDelete"
                                >{{ $t("dialog.closeDeleteButton") }}</v-btn
                              >
                              <v-btn
                                color="blue darken-1"
                                text
                                @click="deleteItemConfirm"
                                >{{ $t("dialog.confirmDeleteButton") }}</v-btn
                              >
                              <v-spacer></v-spacer>
                            </v-card-actions>
                          </v-card>
                        </v-dialog>
                      </v-toolbar>
                    </template>
                    <template v-slot:item.actions="{ item }">
                      <v-icon small class="mr-2" @click="editItem(item)">
                        mdi-pencil
                      </v-icon>
                      <v-icon small @click="deleteItem(item)">
                        mdi-delete
                      </v-icon>
                    </template>
                    <!-- <template v-slot:no-data></template> -->
                  </v-data-table>
                </template>
              </v-col>
            </v-row>
            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>

<script>
import { generateId } from "../../js/common";
import {
  required,
  maxLength,
  minLength,
  numeric,
} from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      model: {
        id: null,
        educationName: null,
        educationValidity: null,
        educationJobTitles: null,
      },
      educationJobTitles: [],
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: this.$t("educations.jobTitleName"),
          value: "jobTitleName",
        },
        {
          text: this.$t("educations.actions"),
          value: "actions",
          sortable: false,
        },
      ],
      editedIndex: -1,
      editedItem: {
        jobTitleId: "",
        jobTitleName: "",
      },
      defaultItem: {
        jobTitleId: "",
        jobTitleName: "",
      },
    };
  },
  validations: {
    model: {
      educationName: {
        required,
        minLength: minLength(2),
        maxLength: maxLength(160),
      },
      educationValidity: {
        required,
        numeric,
        maxLength: maxLength(4),
      },
    },
  },
  watch: {
    dialog(val) {
      // if (val == true) {
      //   // dialog open
      // }
      val || this.close();
    },
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    educationNameErrors() {
      const errors = [];
      const field = "educationName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "2" }));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "160" }));
      return errors;
    },
    educationValidityErrors() {
      const errors = [];
      const field = "educationValidity";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].numeric &&
        errors.push(this.$t("v.number.numeric"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "4" }));
      return errors;
    },
  },
  created() {
    this.initializeEducationJobTitles();
    this.model.id = generateId(this);
  },
  methods: {
    initializeEducationJobTitles() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "educationJobTitles")
      ) {
        this.model.educationJobTitles = this.item.educationJobTitles;
        this.educationJobTitles = JSON.parse(this.item.educationJobTitles);
      }
    },
    setJobTitleName(event) {
      this.editedItem.jobTitleName = event.name;
    },
    editItem(item) {
      this.editedIndex = this.educationJobTitles.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.model.educationJobTitles = JSON.stringify(this.educationJobTitles);
      this.dialog = true;
    },
    deleteItem(item) {
      this.editedIndex = this.educationJobTitles.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },
    deleteItemConfirm() {
      this.educationJobTitles.splice(this.editedIndex, 1);
      this.model.employeChildren = JSON.stringify(this.educationJobTitles);
      this.closeDelete();
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    save() {
      if (this.editedIndex > -1) {
        Object.assign(
          this.educationJobTitles[this.editedIndex],
          this.editedItem
        );
      } else {
        this.educationJobTitles.push(this.editedItem);
      }
      this.model.educationJobTitles = JSON.stringify(this.educationJobTitles);
      this.close();
    },
  },
};
</script>
