<template>
  <va-form :id="id" :item="item" disable-redirect v-model="model">
    <v-row>
      <v-col>
        <v-card>
          <v-card-text>
            <va-select-input
              source="companyId"
              reference="companies"
              :error-messages="companyIdErrors"
            ></va-select-input>
            <va-text-input
              source="workplaceName"
              :error-messages="workplaceNameErrors"
            ></va-text-input>
            <va-text-input
              source="registrationNumber"
              :error-messages="registrationNumberErrors"
            ></va-text-input>
            <va-text-input
              source="address"
              :error-messages="addressErrors"
            ></va-text-input>
            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>

<script>
/// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components

import { generateId } from "../../js/common";
import { required, maxLength } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      model: {
        id: null,
        companyId: null,
        workplaceName: null,
        registrationNumber: null,
        address: null,
      },
    };
  },
  validations: {
    model: {
      companyId: {
        required,
        maxLength: maxLength(36),
      },
      workplaceName: {
        required,
        maxLength: maxLength(100),
      },
      registrationNumber: {
        required,
        maxLength: maxLength(30),
      },
      address: {
        maxLength: maxLength(255),
      },
    },
  },
  computed: {
    companyIdErrors() {
      const errors = [];
      const field = "companyId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    workplaceNameErrors() {
      const errors = [];
      const field = "workplaceName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "255" }));
      return errors;
    },
    registrationNumberErrors() {
      const errors = [];
      const field = "registrationNumber";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    addressErrors() {
      const errors = [];
      const field = "address";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "255" }));
      return errors;
    },
  },
  created() {
    this.model.id = generateId(this);
  },
  methods: {},
};
</script>
