<template>
  <div>
    <div class="d-flex justify-end"></div>
    <v-data-table
      :headers="headers"
      :items="getInfoSheet"
      class="v-data-table__editable_wrapper no_top_border"
      :hide-default-footer="true"
      :hide-default-header="true"
      style="border: 1px"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-btn class="mb-2" small @click="printInfoSheetOpen">
            {{ $t("dialog.printWindow") }}
          </v-btn>

          <v-spacer></v-spacer>
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn @click="createRowForm()" color="success" icon v-on="on">
                <v-icon>mdi-plus</v-icon>
              </v-btn>

              <v-dialog max-width="600px" v-model="docDialogPrintInfoSheet">
                <v-card>
                  <v-card-text>
                    <v-container>
                      <employees-print-info-sheet ref="infoSheet" :item="model">
                      </employees-print-info-sheet>
                    </v-container>
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                      color="blue darken-1"
                      text
                      @click="printInfoSheetClose"
                    >
                      {{ $t("dialog.cancelButton") }}
                    </v-btn>
                    <v-btn color="blue darken-1" text @click="printInfoSheet">
                      {{ $t("dialog.print") }}
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </template>
            <span>{{ $t("va.actions.create") }}</span>
          </v-tooltip>
        </v-toolbar>
      </template>
      <template
        v-for="field in getFields"
        v-slot:[`item.${field.source}`]="{ item }"
      >
        <template v-if="item._new || item.fieldId === editRowId">
          <template v-if="field.source == 'fieldId'">
            <va-text-input
              v-show="false"
              :key="field.source"
              v-model="form.fieldId"
              :source="field.source"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'fieldLabel'">
            <va-text-input
              :key="field.source"
              v-model="form.fieldLabel"
              :source="field.source"
              :error-messages="fieldLabelErrors"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'fieldValue'">
            <va-boolean-input
              :key="field.source"
              v-model="form.fieldValue"
              :source="field.source"
              :filled="false"
              disableFormState
            ></va-boolean-input>
          </template>
          <template v-if="field.source == 'fieldNew' && !item._new">
            <va-boolean-input
              :key="field.source"
              v-model="form.fieldNew"
              :source="field.source"
              :filled="false"
              disableFormState
            ></va-boolean-input>
          </template>
        </template>
        <template v-else>
          <span
            v-if="field.type == 'boolean' && field.source == 'fieldValue'"
            :key="field.source + '_label'"
          >
            <v-icon v-if="item[field.source]" color="green">mdi-check</v-icon>
            <v-icon v-else color="red">mdi-close</v-icon>
          </span>
          <span v-else :key="field.source + '_label'">
            {{ item[field.source] }}
          </span>
        </template>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          small
          class="mr-2"
          v-if="item._new || item.fieldId === editRowId"
          @click="saveItem()"
        >
          mdi-content-save
        </v-icon>
        <v-icon
          small
          v-if="item._new || item.fieldId === editRowId"
          @click="cancel"
        >
          mdi-close
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.fieldId != editRowId"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.fieldId != editRowId"
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>

    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">{{
          $t("dialog.deleteConfirm")
        }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">{{
            $t("dialog.closeDeleteButton")
          }}</v-btn>
          <v-btn color="blue darken-1" text @click="deleteConfirm">{{
            $t("dialog.confirmDeleteButton")
          }}</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { required } from "vuelidate/lib/validators";
import { generateColumnName } from "../../js/common";

export default {
  props: ["items", "model"],
  data() {
    return {
      dialogDelete: false,
      docDialogPrintInfoSheet: false,
      form: null,
      editedIndex: -1,
      editRowId: null,
      infoSheet: null,
      headers: [
        {
          value: "fieldLabel",
        },
        {
          value: "fieldValue",
        },
        {
          text: this.$t("employees.childrenActions"),
          value: "actions",
          sortable: false,
        },
      ],
      fields: [
        {
          source: "fieldLabel",
          label: this.$t("employees.documentName"),
        },
        {
          source: "fieldValue",
          label: this.$t("employees.documentValue"),
          type: "boolean",
        },
      ],
    };
  },
  computed: {
    getInfoSheet() {
      if (this.form && !this.editRowId) {
        let items = [{ _new: true }, ...this.infoSheet];
        return items;
      }
      return this.infoSheet;
    },
    getFields() {
      let fields = this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label: f.label,
          };
        });
      fields.fieldId = null;
      return fields;
    },
    fieldLabelErrors() {
      const errors = [];
      const field = "fieldLabel";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  validations: {
    form: {
      fieldLabel: {
        required,
      },
    },
  },
  async created() {
    this.infoSheet = this.items;
  },
  watch: {
    dialogDelete(val) {
      val || this.close();
    },
  },
  methods: {
    editItem(item) {
      this.createRowForm("edit", item);
      this.editedIndex = this.infoSheet.indexOf(item);
    },
    createRowForm(action = "new", item = null) {
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
      if (action == "new") {
        this.editRowId = null;
        this.form.fieldId = generateColumnName(6);
      } else {
        this.editRowId = item ? item.fieldId : null;
      }
    },
    saveItem() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      if (this.editedIndex > -1) {
        Object.assign(this.infoSheet[this.editedIndex], this.form);
      } else {
        this.infoSheet.push(this.form);
      }
      this.$emit("save", this.infoSheet);
      this.close();
    },
    deleteItem(item) {
      this.editedIndex = this.infoSheet.indexOf(item);
      this.dialogDelete = true;
    },
    deleteConfirm() {
      this.infoSheet.splice(this.editedIndex, 1);
      this.$emit("save", this.infoSheet);
      this.close();
    },
    cancel() {
      this.close();
    },
    close() {
      this.$nextTick(async () => {
        this.editedIndex = -1;
        this.$v.$reset();
      });
      this.dialogDelete = false;
      this.form = null;
      this.editRowId = null;
    },
    printInfoSheet() {
      // console.error(this.$refs.infoSheet)
      this.$refs.infoSheet.printMe();
    },
    printInfoSheetOpen() {
      this.docDialogPrintInfoSheet = true;
    },
    printInfoSheetClose() {
      this.docDialogPrintInfoSheet = false;
    },
  },
};
</script>
