<template>
  <div>
    <va-form :id="id" :item="item" @validate="onValidate" v-model="model">
      <v-card>
        <v-tabs v-model="tabs">
          <v-tab>{{ $t("orders.tab1") }}</v-tab>
          <v-tab
            >{{ $t("orders.tab3") }} ({{ invoiceOrderItems.length }})</v-tab
          >
        </v-tabs>

        <v-tabs-items v-model="tabs">
          <v-tab-item>
            <v-card flat>
              <v-card-text>
                <v-row>
                  <v-col sm="3">
                    <va-select-input
                      source="customerId"
                      reference="customers"
                      @change="setCustomerId($event)"
                      :error-messages="customerIdErrors"
                    ></va-select-input>
                    <va-text-input
                      v-show="false"
                      source="id"
                      :label="$t('orders.id')"
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3">
                    <va-text-input
                      source="purchaseOrderNo"
                      :error-messages="purchaseOrderNoErrors"
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3">
                    <va-text-input
                      source="customerOrderNo"
                      :error-messages="customerOrderNoErrors"
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3"> </v-col>
                </v-row>
                <v-row>
                  <v-col sm="9">
                    <va-select-input
                      source="sows"
                      reference="sows"
                      :filter="{ customerId: model.customerId }"
                      :key="model.customerId"
                      :error-messages="sowsErrors"
                      multiple
                    >
                    </va-select-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="2">
                    <va-select-input
                      source="startYearId"
                      @change="detectTotalMonths"
                      reference="years"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="2">
                    <va-select-input
                      source="endYearId"
                      @change="detectTotalMonths"
                      reference="years"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="2">
                    <va-select-input
                      source="startMonthId"
                      @change="detectTotalMonths"
                      reference="months"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="2">
                    <va-select-input
                      source="endMonthId"
                      @change="detectTotalMonths"
                      reference="months"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="2">
                    <va-text-input
                      source="totalMonth"
                      v-model="model.totalMonth"
                    ></va-text-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="6">
                    <va-text-input
                      source="description"
                      :error-messages="descriptionErrors"
                      multiline
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3"> </v-col>
                </v-row>
                <v-row>
                  <v-col sm="2">
                    <va-date-hand-input
                      source="startDate"
                      format="short"
                    ></va-date-hand-input>
                  </v-col>
                  <v-col sm="2">
                    <va-date-hand-input
                      source="endDate"
                      format="short"
                    ></va-date-hand-input>
                  </v-col>
                  <v-col sm="2">
                    <va-boolean-input source="grConsent"></va-boolean-input>
                  </v-col>
                  <v-col sm="2">
                    <va-boolean-input source="flexyForce"></va-boolean-input>
                  </v-col>
                </v-row>

                <v-alert
                  v-if="$v['model']['purchaseOrderItems'].$invalid"
                  dense
                  text
                  type="error"
                >
                  {{ $t("orders.purchaseOrderItemsRequired") }}
                </v-alert>

                <v-row>
                  <v-col sm="12">
                    <va-text-input
                      v-show="false"
                      :label="$t('orders.purchaseOrderItems')"
                      v-model="model.purchaseOrderItems"
                    ></va-text-input>
                    <orders-orderitems
                      :items="purchaseOrderItems"
                      :key="renderKeyOrderItems + model.customerId"
                      :customerId="model.customerId"
                      @save="savePurchaseOrderItems"
                    >
                    </orders-orderitems>
                  </v-col>
                </v-row>
                <va-save-button></va-save-button>
              </v-card-text>
            </v-card>
          </v-tab-item>

          <v-tab-item>
            <v-card flat>
              <v-card-text>
                <v-row>
                  <v-col sm="12">
                    <va-text-input
                      v-show="false"
                      :label="$t('orders.invoiceOrderItems')"
                      v-model="model.invoiceOrderItems"
                    ></va-text-input>
                    <template>
                      <v-data-table
                        :headers="headersInvoiceOrderItems"
                        :items="invoiceOrderItems"
                        sort-by="name"
                        class="v-data-table__editable_wrapper no_top_border"
                        hide-default-footer
                      >
                        <template v-slot:top>
                          <v-toolbar flat>
                            <v-spacer></v-spacer>
                            <v-dialog
                              v-model="dialogIIDelete"
                              max-width="500px"
                            >
                              <v-card>
                                <v-card-title class="text-h5">{{
                                  $t("dialog.deleteConfirm")
                                }}</v-card-title>
                                <v-card-actions>
                                  <v-spacer></v-spacer>
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="closeIIDelete"
                                    >{{ $t("dialog.closeDeleteButton") }}</v-btn
                                  >
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="deleteIIitemConfirm"
                                    >{{
                                      $t("dialog.confirmDeleteButton")
                                    }}</v-btn
                                  >
                                  <v-spacer></v-spacer>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>
                          </v-toolbar>
                        </template>
                        <template v-slot:item.actions="{ item }">
                          <v-icon small @click="deleteIIitem(item)">
                            mdi-delete
                          </v-icon>
                        </template>
                        <!-- <template v-slot:no-data></template> -->

                        <!--                   <template v-slot:[`field.itemDepartmentId`]="{ editedItem }">
                    <span>{{ new Date(item.calculationDate).toLocaleDateString("tr") }}</span>
                  </template> -->
                      </v-data-table>
                    </template>
                  </v-col>
                </v-row>
                <va-save-button></va-save-button>
              </v-card-text>
            </v-card>
          </v-tab-item>
        </v-tabs-items>
      </v-card>
    </va-form>
  </div>
</template>

<script>
/// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components
///
import { generateId, findTotalMonths, requiredArray } from "../../js/common";
import { required, maxLength } from "vuelidate/lib/validators";
import moment from "moment";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      tabs: null,
      renderKeyOrderItems: 1001,
      orderItemsEditWindow: false,
      date_input: moment().format("YYYY-MM-DD"),
      valid_date: moment().format("YYYY-MM-DD"),
      model: {
        id: "",
        customerId: "",
        sows: null,
        purchaseOrderNo: null,
        customerOrderNo: null,
        description: null,
        startDate: null,
        endDate: null,
        startYearId: null,
        endYearId: null,
        startMonthId: null,
        endMonthId: null,
        totalMonth: null,
        grConsent: false,
        flexyForce: false,
        invoiceOrderItems: "",
        purchaseOrderItems: "",
      },
      currencies: [
        { id: "TRY", name: "Türk Lirası" },
        { id: "USD", name: "Dolar" },
        { id: "EUR", name: "Euro" },
      ],
      departments: [],
      invoiceOrderItems: [],
      purchaseOrderItems: [],
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "",
        suffix: " TL",
        precision: 2,
        masked: false,
      },
      showExchangeRate: false,
      exchangeRate: {
        decimal: ",",
        thousands: ".",
        prefix: "",
        suffix: " TL",
        precision: 4,
        masked: false,
      },
      dialog: false,
      dialogDelete: false,
      dialogIIDelete: false,
      headersInvoiceOrderItems: [
        { text: this.$t("orders.invoiceNo"), value: "invoiceNo" },
        { text: this.$t("orders.eInvoiceNo"), value: "eInvoiceNo" },
        { text: this.$t("orders.departmentName"), value: "itemDepartmentName" },
        { text: this.$t("orders.startDate"), value: "startDate" },
        { text: this.$t("orders.endDate"), value: "endDate" },
        { text: this.$t("orders.description"), value: "itemDescription" },
        { text: this.$t("orders.currencyId"), value: "itemCurrencyId" },
        { text: this.$t("orders.totalAmount"), value: "itemTotalAmount" },
        { text: this.$t("orders.orderActions"), value: "actions" },
      ],
    };
  },
  validations: {
    model: {
      purchaseOrderNo: {
        required,
        maxLength: maxLength(100),
      },
      customerOrderNo: {
        maxLength: maxLength(100),
      },
      customerId: {
        required,
        maxLength: maxLength(36),
      },
      sows: {
        required,
        maxLength: maxLength(36),
      },
      description: {
        maxLength: maxLength(255),
      },
      purchaseOrderItems: {
        required,
        requiredArray,
      },
    },
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    purchaseOrderNoErrors() {
      const errors = [];
      const field = "purchaseOrderNo";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    customerOrderNoErrors() {
      const errors = [];
      const field = "customerOrderNo";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    customerIdErrors() {
      const errors = [];
      const field = "customerId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    sowsErrors() {
      const errors = [];
      const field = "sows";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    descriptionErrors() {
      const errors = [];
      const field = "description";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "255" }));
      return errors;
    },
  },
  created() {
    this.model.id = generateId(this);
    this.initializeInvoiceOrderItems();
    this.initializePurchaseOrderItems();
    this.initializeDepartments();
  },
  mounted() {
    this.detectCurrencyInput();
  },
  methods: {
    onInput(val) {
      const newDate = moment(val);
      if (newDate.isValid()) {
        this.valid_date = newDate.format("YYYY-MM-DD");
      }
    },
    dateChange(date) {
      this.date_input = date;
    },
    savePurchaseOrderItems(orderItems) {
      this.purchaseOrderItems = orderItems;
      this.model.purchaseOrderItems = JSON.stringify(orderItems);
    },
    setCustomerId(event) {
      this.model.customerId = event.id;
      this.initializeDepartments(event.id);
    },
    getCustomerId() {
      return this.editedItem.itemCustomerId;
    },
    detectTotalMonths() {
      if (
        this.model.startYearId &&
        this.model.startMonthId &&
        this.model.endYearId &&
        this.model.endMonthId
      ) {
        let startDate =
          String(this.model.startYearId) +
          "-" +
          String(this.model.startMonthId) +
          "-01";
        let endDate =
          String(this.model.endYearId) +
          "-" +
          String(this.model.endMonthId) +
          "-01";
        let totalMonth = findTotalMonths(startDate, endDate);
        this.model.totalMonth = String(totalMonth);
      }
    },
    detectCurrencyInput() {
      if (this.model.itemCurrencyId) {
        this.currencyId = this.model.itemCurrencyId;
      }
    },
    onValidate() {
      if (
        this.$v["model"]["customerId"].$invalid ||
        this.$v["model"]["purchaseOrderNo"].$invalid ||
        this.$v["model"]["customerOrderNo"].$invalid
      ) {
        this.tabs = 0;
      }
    },
    async initializeDepartments(customerId = null) {
      var qs = "?";
      if (customerId) {
        qs = "?customerId=" + customerId;
      } else if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "customerId")
      ) {
        qs = "?customerId=" + this.item.customerId;
      }
      let response = await this.$admin.http.get("departments/findAll" + qs);
      if (response.status == 200) {
        this.departments = response.data.data;
      }
    },
    initializeInvoiceOrderItems() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "invoiceOrderItems")
      ) {
        this.invoiceOrderItems = JSON.parse(this.item.invoiceOrderItems);
        this.model.invoiceOrderItems = this.item.invoiceOrderItems;
      }
    },
    initializePurchaseOrderItems() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "purchaseOrderItems")
      ) {
        this.purchaseOrderItems = JSON.parse(this.item.purchaseOrderItems);
        this.model.purchaseOrderItems = this.item.purchaseOrderItems;
      }
    },
    orderItemsRenderModalComponent() {
      this.renderKeyOrderItems = this.renderKeyOrderItems + 1;
    },
    deleteIIitem(item) {
      this.IIeditedIndex = this.model.invoiceOrderItems.indexOf(item);
      this.dialogIIDelete = true;
    },
    deleteIIitemConfirm() {
      this.invoiceOrderItems.splice(this.IIeditedIndex, 1);
      this.model.invoiceOrderItems = JSON.stringify(this.invoiceOrderItems);
      this.closeIIDelete();
    },
    closeIIDelete() {
      this.dialogIIDelete = false;
    },
  },
};
</script>
