<template>
  <div>
    <div class="d-flex justify-end"></div>
    <v-data-table
      :headers="headers"
      :items="getEducations"
      class="v-data-table__editable_wrapper no_top_border"
      :hide-default-footer="true"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <!-- <v-toolbar-title>{{ $t("employees.educations") }}</v-toolbar-title> -->
          <!-- <v-spacer></v-spacer> -->
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn @click="createRowForm()" color="success" icon v-on="on">
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </template>
            <span>{{ $t("va.actions.create") }}</span>
          </v-tooltip>
        </v-toolbar>
      </template>
      <template
        v-for="field in getFields"
        v-slot:[`item.${field.source}`]="{ item }"
      >
        <template v-if="item._new || item.educationId === editRowId">
          <template v-if="field.source == 'jobTitleId'">
            <v-select
              :key="field.source"
              :source="field.source"
              v-model="form.jobTitleId"
              :item-text="'name'"
              :item-value="'id'"
              :items="jobTitles"
              @change="setJobTitle($event)"
              :filled="false"
              :error-messages="jobTitleIdErrors"
              return-object
            ></v-select>
          </template>
          <template v-if="field.source == 'educationItemId'">
            <v-select
              :key="field.source"
              :source="field.source"
              v-model="form.educationItemId"
              :item-text="'name'"
              :item-value="'id'"
              :items="educationItems"
              @change="setEducation($event)"
              :filled="false"
              :error-messages="educationItemIdErrors"
              return-object
            ></v-select>
          </template>
          <template v-if="field.source == 'educationStartDate'">
            <va-date-input
              :key="field.source"
              :source="field.source"
              v-model="form.educationStartDate"
              @change="setValidityDate"
              format="short"
              :error-messages="educationStartDateErrors"
              :filled="false"
              disableFormState
            ></va-date-input>
          </template>
          <template v-if="field.source == 'educationEndDate'">
            <va-date-input
              :key="field.source"
              :source="field.source"
              v-model="form.educationEndDate"
              format="short"
              :filled="false"
              :error-messages="educationEndDateErrors"
              disableFormState
            ></va-date-input>
          </template>
          <template v-if="field.source == 'educationValidityEnd'">
            <va-text-input
              :key="field.source"
              :source="field.source"
              v-model="form.educationValidityEnd"
              v-mask="'####-##-##'"
              :filled="false"
              :error-messages="educationValidityEndErrors"
              disableFormState
            ></va-text-input>
          </template>
        </template>
        <template v-else>
          <span v-if="field.type == 'select'" :key="field.source + '_label'">{{
            item[field.sourceLabel]
          }}</span>
          <span v-else-if="field.type == 'date'" :key="field.source + '_label'">
            {{
              item[field.source]
                ? new Date(item[field.source]).toLocaleDateString("tr")
                : ""
            }}
          </span>
          <span
            v-else-if="field.type == 'boolean'"
            :key="field.source + '_label'"
          >
            <v-icon v-if="item[field.source]" color="green">mdi-check</v-icon>
            <v-icon v-else color="red">mdi-close</v-icon>
          </span>
          <span v-else :key="field.source + '_label'">
            {{ item[field.source] }}
          </span>
        </template>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          small
          class="mr-2"
          v-if="item._new || item.educationId === editRowId"
          @click="saveItem()"
        >
          mdi-content-save
        </v-icon>
        <v-icon
          small
          v-if="item._new || item.educationId === editRowId"
          @click="cancel"
        >
          mdi-close
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.educationId != editRowId"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.educationId != editRowId"
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>

    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">{{
          $t("dialog.deleteConfirm")
        }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">{{
            $t("dialog.closeDeleteButton")
          }}</v-btn>
          <v-btn color="blue darken-1" text @click="deleteConfirm">{{
            $t("dialog.confirmDeleteButton")
          }}</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { generateUid, dateAddMonth } from "../../js/common";
import { required } from "vuelidate/lib/validators";

export default {
  props: ["items"],
  data() {
    return {
      dialogDelete: false,
      jobTitles: null,
      form: null,
      editedIndex: -1,
      editRowId: null,
      educations: null,
      educationItems: null,
      headers: [
        {
          text: this.$t("employees.jobTitleId"),
          value: "jobTitleId",
        },
        {
          text: this.$t("employees.educationItemId"),
          value: "educationItemId",
        },
        {
          text: this.$t("employees.educationStartDate"),
          value: "educationStartDate",
        },
        {
          text: this.$t("employees.educationEndDate"),
          value: "educationEndDate",
        },
        {
          text: this.$t("employees.educationValidityEnd"),
          value: "educationValidityEnd",
        },
        {
          text: this.$t("employees.actions"),
          value: "actions",
          sortable: false,
        },
      ],
      fields: [
        {
          text: this.$t("employees.jobTitleId"),
          source: "jobTitleId",
          sourceLabel: "jobTitleName",
          type: "select",
        },
        {
          text: this.$t("employees.educationItemId"),
          source: "educationItemId",
          sourceLabel: "educationName",
          type: "select",
        },
        {
          text: this.$t("employees.educationStartDate"),
          type: "date",
          source: "educationStartDate",
        },
        {
          text: this.$t("employees.educationEndDate"),
          type: "date",
          source: "educationEndDate",
        },
        {
          text: this.$t("employees.educationValidityEnd"),
          type: "date",
          source: "educationValidityEnd",
        },
      ],
    };
  },
  computed: {
    getEducations() {
      if (this.form && !this.editRowId) {
        let items = [{ _new: true }, ...this.educations];
        return items;
      }
      return this.educations;
    },
    getFields() {
      let fields = this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label: f.label,
          };
        });
      fields.educationId = null;
      return fields;
    },
    jobTitleIdErrors() {
      const errors = [];
      const field = "jobTitleId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    educationItemIdErrors() {
      const errors = [];
      const field = "educationItemId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    educationStartDateErrors() {
      const errors = [];
      const field = "educationStartDate";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    educationEndDateErrors() {
      const errors = [];
      const field = "educationEndDate";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    educationValidityEndErrors() {
      const errors = [];
      const field = "educationValidityEnd";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  async created() {
    this.educations = this.items;
    let response = await this.$admin.http.get("/educations/findAll");
    this.educationItems = response.data.data;

    response = await this.$admin.http.get("/jobTitles/findAll");
    this.jobTitles = response.data.data;
  },
  validations: {
    form: {
      jobTitleId: {
        required,
      },
      educationItemId: {
        required,
      },
      educationStartDate: {
        required,
      },
      educationEndDate: {
        required,
      },
      educationValidityEnd: {
        required,
      },
    },
  },
  methods: {
    setJobTitle(event) {
      this.form.jobTitleId = event.id;
      this.form.jobTitleName = event.name;
    },
    setEducation(event) {
      this.form.educationItemId = event.id;
      this.form.educationName = event.name;
      this.form.educationValidity = event.validity;
    },
    setValidityDate() {
      this.form.educationValidityEnd = dateAddMonth(
        this.form.educationStartDate,
        this.form.educationValidity
      );
    },
    createRowForm(action = "new", item = null) {
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
      if (action == "new") {
        this.editRowId = null;
        this.form.educationId = generateUid();
      } else {
        this.editRowId = item ? item.educationId : null;
        this.form.educationValidity = item ? item.educationValidity : 0;
      }
    },
    editItem(item) {
      this.createRowForm("edit", item);
      this.editedIndex = this.educations.indexOf(item);
    },
    saveItem() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      if (this.editedIndex > -1) {
        Object.assign(this.educations[this.editedIndex], this.form);
      } else {
        this.educations.push(this.form);
      }
      this.$emit("save", this.educations);
      this.close();
    },
    deleteItem(item) {
      this.editedIndex = this.educations.indexOf(item);
      this.dialogDelete = true;
    },
    deleteConfirm() {
      this.educations.splice(this.editedIndex, 1);
      this.$emit("save", this.educations);
      this.close();
    },
    cancel() {
      this.close();
    },
    close() {
      this.$nextTick(async () => {
        this.editedIndex = -1;
        this.$v.$reset();
      });
      this.form = null;
      this.dialogDelete = false;
      this.editRowId = null;
    },
  },
};
</script>
