<template>
  <div>
    <div class="d-flex justify-end"></div>
    <v-data-table
      :headers="headers"
      :items="getJobTitles"
      class="v-data-table__editable_wrapper no_top_border"
      :hide-default-footer="true"
      :hide-default-header="true"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn @click="createRowForm()" color="success" icon v-on="on">
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </template>
            <span>{{ $t("va.actions.create") }}</span>
          </v-tooltip>
        </v-toolbar>
      </template>
      <template
        v-for="field in getFields"
        v-slot:[`item.${field.source}`]="{ item }"
      >
        <template v-if="item._new || item.jobTitleId === editRowId">
          <template v-if="field.source == 'jobTitleId'">
            <v-select
              :key="field.source"
              :source="field.source"
              :item-text="'name'"
              :item-value="'id'"
              :items="jobTitles"
              v-model="form.jobTitleId"
              @change="setJobTitle($event)"
              :filled="false"
              :error-messages="jobTitleIdErrors"
              return-object
            >
            </v-select>
          </template>
        </template>
        <template v-else>
          <span
            v-if="field.type == 'select'"
            :key="item.jobTitleId + field.source + '_label'"
            >{{ item[field.sourceLabel] }}</span
          >
          <span v-else :key="item.jobTitleId + field.source + '_label'">
            {{ item[field.source] }}
          </span>
        </template>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          small
          class="mr-2"
          v-if="item._new || item.jobTitleId === editRowId"
          @click="saveItem()"
        >
          mdi-content-save
        </v-icon>
        <v-icon
          small
          v-if="item._new || item.jobTitleId === editRowId"
          @click="cancel"
        >
          mdi-close
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.jobTitleId != editRowId"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.jobTitleId != editRowId"
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>

    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">{{
          $t("dialog.deleteConfirm")
        }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">{{
            $t("dialog.closeDeleteButton")
          }}</v-btn>
          <v-btn color="blue darken-1" text @click="deleteConfirm">{{
            $t("dialog.confirmDeleteButton")
          }}</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { required } from "vuelidate/lib/validators";
import { generateUid } from "../../js/common";

export default {
  props: ["items"],
  data() {
    return {
      dialogDelete: false,
      form: null,
      editedIndex: -1,
      editRowId: null,
      customerJobTitles: null,
      jobTitles: [],
      headers: [
        {
          text: this.$t("customers.jobTitleId"),
          value: "jobTitleId",
        },
        {
          text: this.$t("customers.jobTitleActions"),
          value: "actions",
          sortable: false,
        },
      ],
      fields: [
        {
          text: this.$t("customers.jobTitleId"),
          source: "jobTitleId",
          sourceLabel: "jobTitleName",
          type: "select",
        },
      ],
    };
  },
  computed: {
    getJobTitles() {
      if (this.form && !this.editRowId) {
        let items = [{ _new: true }, ...this.customerJobTitles];
        return items;
      }
      return this.customerJobTitles;
    },
    getFields() {
      let fields = this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label: f.label,
          };
        });
      fields.jobTitleId = null;
      return fields;
    },
    jobTitleIdErrors() {
      const errors = [];
      const field = "jobTitleId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  validations: {
    form: {
      jobTitleId: {
        required,
      },
    },
  },
  async created() {
    this.customerJobTitles = this.items;
    let response = await this.$admin.http.get("/jobTitles/findAll");
    this.jobTitles = response.data.data;
  },
  watch: {
    dialogDelete(val) {
      val || this.close();
    },
  },
  methods: {
    setJobTitle(event) {
      this.form.jobTitleId = event.id;
      this.form.jobTitleName = event.name;
    },
    editItem(item) {
      this.createRowForm("edit", item);
      this.editedIndex = this.customerJobTitles.indexOf(item);
    },
    createRowForm(action = "new", item = null) {
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
      if (action == "new") {
        this.editRowId = null;
        this.form.jobTitleId = generateUid();
      } else {
        this.editRowId = item ? item.jobTitleId : null;
      }
    },
    saveItem() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      if (this.editedIndex > -1) {
        Object.assign(this.customerJobTitles[this.editedIndex], this.form);
      } else {
        this.customerJobTitles.push(this.form);
      }
      this.$emit("save", this.customerJobTitles);
      this.close();
    },
    deleteItem(item) {
      this.editedIndex = this.customerJobTitles.indexOf(item);
      this.dialogDelete = true;
    },
    deleteConfirm() {
      this.customerJobTitles.splice(this.editedIndex, 1);
      this.$emit("save", this.customerJobTitles);
      this.close();
    },
    cancel() {
      this.close();
    },
    close() {
      this.$nextTick(async () => {
        this.editedIndex = -1;
        this.$v.$reset();
      });
      this.dialogDelete = false;
      this.form = null;
      this.editRowId = null;
    },
  },
};
</script>
