<template>
  <form>
    <v-row>
      <v-col sm="auto" v-for="(item, index) in filters" :key="index">

        <div class="d-flex align-center">
          <v-btn
            small
            text
            icon
            class="mr-2"
            v-if="!item.alwaysOn"
            @click="remove(item)"
          >
            <v-icon>mdi-close-circle-outline</v-icon>
          </v-btn>
          <div class="mb-2">
          <slot
            :name="item.source"
            :source="item.source"
            :label="item.label"
            v-bind="item.attributes"
            :value="value[item.source]" 
          >
            <input-filter
              :type="item.type"
              :key="value[item.key] ? value[item.key].join() : item.source"
              :source="item.source"
              :label="item.label"
              v-bind="item.attributes"
              :filter="getFilters(item.filters)"
              :value="value[item.source]"
              @input="(val) => update(item, val)"
            >
            </input-filter>
          </slot>
        </div>
        </div>
      </v-col>
    </v-row>
  </form>
</template>

<script>
import InputFilter from "./InputFilter";
import size from "lodash/size";

export default {
  components: {
    InputFilter,
  },
  props: {
    value: {
      type: Object,
      default: () => {},
    },
    filters: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      filterIds: {},
    };
  },
  methods: {
    // @change="setFilter($event, field.filterId, source)"
    // :filter="getFilters(field.filters)"
    setFilter(value, filterId){
      if (filterId) {
        this.filterIds[filterId] = value
      }
      return
    },
    getFilters(filters) {
      let filterObject = {}
      if (filters) {
        if (size(this.filterIds) > 0) {
          for (var key in this.filterIds) {
              if (filters.includes(key)) {
                  filterObject[key] = Array.isArray(this.filterIds[key]) ? this.filterIds[key].join() : this.filterIds[key]
              }
          }
        }
      }
      // console.error(filterObject)
      return filterObject;
    },

    remove(filter) {
      let value = { ...this.value };
      delete value[filter.source];

      this.$emit("remove", filter);
      this.$emit("input", value);
    },
    update(item, value) {
      if (typeof item.filterId != "undefined" && item.filterId != "") {
        this.setFilter(value, item.filterId)
      }
      this.$emit("input", {
        ...this.value,
        [item.source]: value,
      });
    },
  },
};
</script>
