<template>
  <div>
    <va-aside-layout :title="asideTitle">
      <companies-show v-if="action === 'show'" :item="item"></companies-show>
      <companies-form
        v-else
        :id="id"
        :item="item"
        @saved="onSaved"
      ></companies-form>
    </va-aside-layout>
    <v-card>
      <v-card-text>
        <va-list
          :items-per-page="10"
          :disableExport="true"
          :disable-global-search="false"
          :disable-items-per-page="false"
          disable-create-redirect
          @action="onAction"
        >
          <va-data-table
            :fields="fields"
            disable-create-redirect
            disable-show-redirect
            disable-edit-redirect
            disable-clone
            @item-action="onAction"
          >
            <!--           <template v-slot:[`item.actions`]="{ resource, item }">
            <impersonate-button
              :resource="resource"
              :item="item"
              icon
            ></impersonate-button>
          </template> -->
          </va-data-table>
        </va-list>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
// import ImpersonateButton from "@/components/buttons/ImpersonateButton";

export default {
  // components: {
  //   ImpersonateButton,
  // },
  props: ["resource", "opened"],
  data() {
    return {
      fields: [
        // { source: "id", label: "", sortable: true },
        {
          source: "companyName",
          label: this.$t("companies.companyName"),
          sortable: true,
        },
        {
          source: "taxOffice",
          label: this.$t("companies.taxOffice"),
          sortable: true,
        },
        {
          source: "taxNumber",
          label: this.$t("companies.taxNumber"),
          sortable: true,
        },
        {
          source: "address",
          label: this.$t("companies.address"),
          sortable: true,
        },
        {
          source: "createdAt",
          label: this.$t("companies.createdAt"),
          sortable: true,
        },
      ],
      asideTitle: null,
      id: null,
      action: null,
      item: null,
    };
  },
  methods: {
    async onAction({ action, title, id, item }) {
      this.asideTitle = title;
      this.id = id;
      this.item = item;
      this.action = action;
      if (action === "create") {
        this.action = null;
      }
    },
    async onSaved() {
      this.asideTitle = null;
      // this.$refs.list.fetchData();
      // this.$router.push({ name: `${this.resource.name}_list` });
      this.$store.dispatch("api/refresh", `${this.resource.name}`);
    },
  },
};
</script>
