<script>
import { generateId } from "../../js/common";
import { required } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      model: {
        id: null,
        customerId: null,
        workplaceId: null,
        sowId: null,
        currencyId: this.item.currencyId ? this.item.currencyId : "TRY",
        departmentId: null,
        subDepartmentId: null,
        employeeId: null,
        expenseTypeId: null,
        paymentTypeId: null,
        employeePaidDate: null,
