        amount: null,
        tax: null,
        totalAmount: null,
        description: null,
        whoPaidId: null,
        pernetInvoiceNo: null,
        sentForApproval: null,
        poNo: null,
        file: this.item.file,
      },
    };
  },
  validations: {
    model: {
      customerId: {
        required,
      },
      workplaceId: {
        required,
      },
      sowId: {
        required,
      },
      currencyId: {
        required,
      },
      employeeId: {
        required,
      },
      expenseTypeId: {
        required,
      },
      paymentTypeId: {
        required,
      },
      whoPaidId: {
