<template>
  <div>
    <v-data-table
      v-model="selected"
      :item-key="itemKey"
      :headers="headers"
      :items="items"
      :items-per-page="Number(itemsPerPage)"
      :group-by="groupBy"
      :search="search"
      :custom-filter="filterText"
    >
      <template v-slot:top>
        <v-text-field
          v-model="search"
          clearable
          :label="$t('va.actions.q')"
          class="mx-4"
        ></v-text-field>
      </template>
      <template
        v-slot:[`group.header`]="{
          isOpen,
          toggle,
          group,
          isMobile,
        }"
      >
        <td
          class="text-start"
          :colspan="isMobile ? undefined : headers.length"
        >
          <div style="display: flex; justify: center">
            <div style="max-width: 10%">
              <v-btn class="ma-0" icon small @click="toggle">
                <v-icon>{{ isOpen ? "$minus" : "$plus" }}</v-icon>
              </v-btn>
            </div>
            <div>
              <v-checkbox
                v-model="parentChecked[group]"
                :label="group"
                style="margin-top: 0px"
                @change="toggleChildren(group, $event, toggle)"
                hide-details="true"
              ></v-checkbox>
            </div>
          </div>
        </td>
      </template>
      <template v-slot:item="{ item, isSelected, select }">
        <tr
          style="cursor: pointer"
          :class="isSelected ? 'v-data-table__selected' : ''"
          @click="
            toggle(
              isSelected,
              select,
              $event,
              item[groupBy]
            )
          "
        >
          <td class="d-flex align-center">
            <v-icon size="16" color="green" v-if="isSelected"
              >mdi-check</v-icon
            >
            <div v-if="!isSelected" style="width: 25px">&nbsp;</div>
          </td>
          <td v-for="(field) in getFields" :key="field.source">
              {{ item[field.source] }}
          </td>
          <!-- 
          <td>{{ item.route }}</td>
          <td>{{ item.method }}</td> -->
        </tr>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import { arrayGroupBy } from "../../../mixins/common";

export default {
  props: {
    item: {
      type: Object,
      default: () => {},
    },
    itemKey: {
      type: String
    },
    itemsPerPage: {
      type: [String, Number],
      default: () => 30,
    },
    headers: {
      type: Array,
      default: () => [],
    },
    fields: {
      type: Array,
      default: () => [],
    },
    initUrl: {
      type: [Function],
    },
    groupBy: {
      type: String
    },
    dataKey: {
      type: String
    },
  },
  data() {
    return {
      items: [],
      search: "",
      selected: [],
      groupedItems: [],
      parentChecked: [],
    }
  },
  created() {
    if (
      typeof this.item === "object" &&
      this.item !== null &&
      Object.prototype.hasOwnProperty.call(this.item, this.dataKey)
    ) {
      this.selected = JSON.parse(this.item[this.dataKey])
    }
    this.initializeItems()
  },
  computed: {
    getFields() {
      return this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            // type: f.type,
            label: f.label
          };
        });
    },
  },
  methods: {
    filterText(value, search) {
      return (
        value != null &&
        search != null &&
        typeof value === "string" &&
        value.toString().toLocaleLowerCase().indexOf(search) !== -1
      );
    },
    async initializeItems() {
      let url = this.initUrl()
      let response = await this.$admin.http.get(url)
      this.items = response.data.data
      this.groupedItems = arrayGroupBy(this.items, this.groupBy)
      // console.error(this.groupedItems)
      this.initializeParentCheckboxes()
    },
    initializeParentCheckboxes() {
      let selectedGroupItems = arrayGroupBy(
        this.selected,
        this.groupBy
      );
      let Self = this;
      Object.keys(this.groupedItems).forEach(function (group) {
        let childrenCount = Self.groupedItems[group].length;
        if (
          typeof selectedGroupItems[group] != "undefined" &&
          selectedGroupItems[group].length == childrenCount
        ) {
          Self.parentChecked[group] = true;
        }
      });
    },
    toggle(isSelected, select, e, group) {
      select(!isSelected);
      let selectedChildrenCount = this.groupedItems[group].length;
      let selectedGroupCount = 1;
      this.selected.forEach(function (val) {
        if (val.moduleName == group) {
          if (isSelected) {
            --selectedGroupCount;
          } else {
            ++selectedGroupCount;
          }
        }
      });
      if (selectedGroupCount == selectedChildrenCount) {
        this.parentChecked[group] = true;
      } else {
        this.parentChecked[group] = false;
      }
      let Self = this;
      setTimeout(function () {
        Self.$emit("selected", Self.selected)
      }, 1000);
    },
    toggleChildren(group, e) {
      const Self = this;
      if (e) {
        // mark child checkboxes
        let selectedIds = [];
        this.selected.forEach(function (val) {
          selectedIds.push(val.permId);
        });
        if (typeof this.groupedItems[group] != "undefined") {
          this.groupedItems[group].forEach(function (gVal) {
            if (!selectedIds.includes(gVal.permId)) {
              Self.selected.push(gVal);
            }
          });
          // toggleParent()
        }
      } else {
        // unmark child checkboxes
        if (typeof this.groupedItems[group] != "undefined") {
          this.groupedItems[group].forEach(function (gVal) {
            Self.selected = Self.selected.filter(
              (item) => item.permId != gVal.permId
            );
          });
          // toggleParent()
        }
      }
      this.$emit("selected", this.selected)
    },

  }

}
</script>