<template>
  <v-text-field :filled="filled" readonly v-bind="commonProps" @change="changeState">
    <template v-slot:append>
      <v-menu v-model="menu" top nudge-bottom="105" nudge-left="16" :close-on-content-click="false">
        <template v-slot:activator="{ on }">
          <div :style="swatchStyle" v-on="on" />
        </template>
        <v-card>
          <v-card-text class="pa-0">
            <v-color-picker
              :dot-size="dotSize"
              :mode="mode"
              :show-swatches="showSwatches"
              :hide-inputs="hideInputs"
              :swatches-max-height="swatchesMaxHeight"
              :flat="flat"
              :value="input"
              @input="updateColor"
            ></v-color-picker> 
          </v-card-text>
        </v-card>
      </v-menu>
    </template>
  </v-text-field>
</template>

<script>
import Input from "../../../mixins/input";
import Editable from "../../../mixins/editable";

/**
 * Edit value as boolean. Rendered as a switch.
 */
export default {
  mixins: [Input, Editable],
  props: {
    value: String,
    dotSize: String,
    mode: String,
    hideInputs: Boolean,
    showSwatches: Boolean,
    swatchesMaxHeight: String,
    flat: String,

    /**
     * Use full filled background color style.
     */
    filled: {
      type: Boolean,
      default: true,
    },
    /**
     * Error status for form validation input focus feature
     */
    status: {
      type: Boolean,
    }
  },
  data() {
     return {
        menu: false,
        color: null,
        type: "text",
     }
  },
  methods: {
    changeState(value) {
      this.change(value);
      this.update(value);
    },
    updateColor(val) {
      this.menu = false
      this.update(val);
    }
  },
  computed: {
    swatchStyle() {
      return {
        backgroundColor: this.input,
        cursor: 'pointer',
        height: '25px',
        width: '25px',
        borderRadius: '50%',
        transition: 'border-radius 200ms ease-in-out'
      }
    },
  }
};
</script>
