<script>
import i18n from "@/i18n";

export default {
  data() {
    return {
      langs: [
        { title: "Türkçe", value: "tr" },
        { title: "English", value: "en" },
      ],
      activeLang: "Türkçe",
      activeLangValue: "tr",
      languageMenu: false,
      themeColor: localStorage.themeColor ? localStorage.themeColor : "#F26522",
    };
  },
  created() {
    let lang = localStorage.getItem("lang");
    if (lang) {
      lang = JSON.parse(lang);
      this.activeLang = lang.title;
      this.activeLangValue = lang.value;
    } else {
      localStorage.setItem(
        "lang",
        JSON.stringify({ title: this.activeLang, value: this.activeLangValue })
      );
    }
  },
  methods: {
    setLocale(lang) {
      this.activeLang = lang.title;
      this.activeLangValue = lang.value;
      localStorage.setItem("lang", JSON.stringify(lang));
      i18n.locale = this.activeLangValue;
    },
  },
};
</script>
