<template>
  <div>
    <div class="d-flex justify-end"></div>
    <v-data-table
      :headers="headers"
      :items="getDepartmentItems"
      class="v-data-table__editable_wrapper no_top_border"
      :hide-default-footer="true"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>{{
            $t("departments.subDepartments")
          }}</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn @click="createRowForm()" color="success" icon v-on="on">
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </template>
            <span>{{ $t("va.actions.create") }}</span>
          </v-tooltip>
        </v-toolbar>
      </template>
      <template
        v-for="field in getFields"
        v-slot:[`item.${field.source}`]="{ item }"
      >
        <template v-if="item._new || item.subDepartmentId === editRowId">
          <template v-if="field.source == 'subDepartmentName'">
            <va-text-input
              :key="field.source"
              :source="field.source"
              v-model="form.subDepartmentName"
              :error-messages="subDepartmentNameErrors"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'subDepartmentManagerName'">
            <va-text-input
              :key="field.source"
              :source="field.source"
              v-model="form.subDepartmentManagerName"
              :filled="false"
              :error-messages="subDepartmentManagerNameErrors"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'subDepartmentManagerSurname'">
            <va-text-input
              :key="field.source"
              :source="field.source"
              v-model="form.subDepartmentManagerSurname"
              :filled="false"
              :error-messages="subDepartmentManagerSurnameErrors"
              disableFormState
            ></va-text-input>
          </template>
          <template
            v-if="field.source == 'subDepartmentManagerPhoneAreaCodeId'"
          >
            <v-select
              :key="field.source"
              :source="field.source"
              v-model="form.subDepartmentManagerPhoneAreaCodeId"
              :item-text="'name'"
              :item-value="'id'"
              :items="areaCodes"
              @change="setPhoneAreaCode($event)"
              :filled="false"
              return-object
            ></v-select>
          </template>
          <template v-if="field.source == 'subDepartmentManagerPhone'">
            <va-text-input
              :key="field.source"
              :source="field.source"
              v-model="form.subDepartmentManagerPhone"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template
            v-if="field.source == 'subDepartmentManagerMobileAreaCodeId'"
          >
            <v-select
              :key="field.source"
              :source="field.source"
              v-model="form.subDepartmentManagerMobileAreaCodeId"
              :item-text="'name'"
              :item-value="'id'"
              :items="areaCodes"
              @change="setMobileAreaCode($event)"
              :filled="false"
              return-object
            ></v-select>
          </template>
          <template v-if="field.source == 'subDepartmentManagerMobile'">
            <va-text-input
              :key="field.source"
              :source="field.source"
              v-model="form.subDepartmentManagerMobile"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'subDepartmentManagerEmail'">
            <va-text-input
              :key="field.source"
              :source="field.source"
              v-model="form.subDepartmentManagerEmail"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
        </template>
        <template v-else>
          <span v-if="field.type == 'select'" :key="field.source + '_label'">{{
            item[field.sourceLabel]
          }}</span>
          <span v-else-if="field.type == 'date'" :key="field.source + '_label'">
            {{
              item[field.source]
                ? new Date(item[field.source]).toLocaleDateString("tr")
                : ""
            }}
          </span>
          <span
            v-else-if="field.type == 'boolean'"
            :key="field.source + '_label'"
          >
            <v-icon v-if="item[field.source]" color="green">mdi-check</v-icon>
            <v-icon v-else color="red">mdi-close</v-icon>
          </span>
          <span v-else :key="field.source + '_label'">
            {{ item[field.source] }}
          </span>
        </template>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          small
          class="mr-2"
          v-if="item._new || item.subDepartmentId === editRowId"
          @click="saveItem()"
        >
          mdi-content-save
        </v-icon>
        <v-icon
          small
          v-if="item._new || item.subDepartmentId === editRowId"
          @click="cancel"
        >
          mdi-close
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.subDepartmentId != editRowId"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.subDepartmentId != editRowId"
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>

    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">{{
          $t("dialog.deleteConfirm")
        }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">{{
            $t("dialog.closeDeleteButton")
          }}</v-btn>
          <v-btn color="blue darken-1" text @click="deleteConfirm">{{
            $t("dialog.confirmDeleteButton")
          }}</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { generateUid } from "../../js/common";
import { required } from "vuelidate/lib/validators";

export default {
  props: ["items", "employmentEndDate"],
  data() {
    return {
      dialogDelete: false,
      jobTitles: null,
      form: null,
      editedIndex: -1,
      editRowId: null,
      areaCodes: null,
      subDepartments: null,
      headers: [
        {
          text: this.$t("departments.subDepartmentName"),
          value: "subDepartmentName",
        },
        {
          text: this.$t("departments.subDepartmentManagerName"),
          value: "subDepartmentManagerName",
        },
        {
          text: this.$t("departments.subDepartmentManagerSurname"),
          value: "subDepartmentManagerSurname",
        },
        {
          text: this.$t("departments.subDepartmentManagerPhoneAreaCodeId"),
          value: "subDepartmentManagerPhoneAreaCodeId",
        },
        {
          text: this.$t("departments.subDepartmentManagerPhone"),
          value: "subDepartmentManagerPhone",
        },
        {
          text: this.$t("departments.subDepartmentManagerMobileAreaCodeId"),
          value: "subDepartmentManagerMobileAreaCodeId",
        },
        {
          text: this.$t("departments.subDepartmentManagerMobile"),
          value: "subDepartmentManagerMobile",
        },
        {
          text: this.$t("departments.subDepartmentManagerEmail"),
          value: "subDepartmentManagerEmail",
        },
        {
          text: this.$t("departments.subDepartmentActions"),
          value: "actions",
          sortable: false,
        },
      ],
      fields: [
        {
          text: this.$t("departments.subDepartmentName"),
          source: "subDepartmentName",
        },
        {
          text: this.$t("departments.subDepartmentManagerName"),
          source: "subDepartmentManagerName",
        },
        {
          text: this.$t("departments.subDepartmentManagerSurname"),
          source: "subDepartmentManagerSurname",
        },
        {
          text: this.$t("departments.subDepartmentManagerPhoneAreaCodeId"),
          source: "subDepartmentManagerPhoneAreaCodeId",
          sourceLabel: "subDepartmentManagerPhoneAreaCodeName",
          type: "select",
        },
        {
          text: this.$t("departments.subDepartmentManagerPhone"),
          source: "subDepartmentManagerPhone",
        },
        {
          text: this.$t("departments.subDepartmentManagerMobileAreaCodeId"),
          source: "subDepartmentManagerMobileAreaCodeId",
          sourceLabel: "subDepartmentManagerMobileAreaCodeName",
          type: "select",
        },
        {
          text: this.$t("departments.subDepartmentManagerMobile"),
          source: "subDepartmentManagerMobile",
        },
        {
          text: this.$t("departments.subDepartmentManagerEmail"),
          source: "subDepartmentManagerEmail",
        },
      ],
    };
  },
  computed: {
    getDepartmentItems() {
      if (this.form && !this.editRowId) {
        let items = [{ _new: true }, ...this.subDepartments];
        return items;
      }
      return this.subDepartments;
    },
    getFields() {
      let fields = this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label: f.label,
          };
        });
      fields.subDepartmentId = null;
      fields.subDepartmentManagerPhoneAreaCodeName = null;
      fields.subDepartmentManagerMobileAreaCodeName = null;
      return fields;
    },
    subDepartmentNameErrors() {
      const errors = [];
      const field = "subDepartmentName";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    subDepartmentManagerNameErrors() {
      const errors = [];
      const field = "subDepartmentManagerName";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    subDepartmentManagerSurnameErrors() {
      const errors = [];
      const field = "subDepartmentManagerSurname";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  async created() {
    this.subDepartments = this.items;
    let response = await this.$admin.http.get("/areaCodes/findAll");
    this.areaCodes = response.data.data;
  },
  validations: {
    form: {
      subDepartmentName: {
        required,
      },
      subDepartmentManagerName: {
        required,
      },
      subDepartmentManagerSurname: {
        required,
      },
    },
  },
  methods: {
    setPhoneAreaCode(event) {
      this.form.subDepartmentManagerPhoneAreaCodeId = event.id;
      this.form.subDepartmentManagerPhoneAreaCodeName = event.name;
    },
    setMobileAreaCode(event) {
      this.form.subDepartmentManagerMobileAreaCodeId = event.id;
      this.form.subDepartmentManagerMobileAreaCodeName = event.name;
    },
    createRowForm(action = "new", item = null) {
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
      if (action == "new") {
        this.editRowId = null;
        this.form.subDepartmentId = generateUid();
        this.form.subDepartmentManagerPhoneAreaCodeId = "TR";
        this.form.subDepartmentManagerPhoneAreaCodeName = "TR";
        this.form.subDepartmentManagerMobileAreaCodeId = "TR";
        this.form.subDepartmentManagerMobileAreaCodeName = "TR";
      } else {
        this.editRowId = item ? item.subDepartmentId : null;
      }
    },
    editItem(item) {
      this.createRowForm("edit", item);
      this.editedIndex = this.subDepartments.indexOf(item);
    },
    saveItem() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      if (this.editedIndex > -1) {
        Object.assign(this.subDepartments[this.editedIndex], this.form);
      } else {
        this.subDepartments.push(this.form);
      }
      this.$emit("save", this.subDepartments);
      this.close();
    },
    deleteItem(item) {
      this.editedIndex = this.subDepartments.indexOf(item);
      this.dialogDelete = true;
    },
    deleteConfirm() {
      this.subDepartments.splice(this.editedIndex, 1);
      this.$emit("save", this.subDepartments);
      this.close();
    },
    cancel() {
      this.close();
    },
    close() {
      this.$nextTick(async () => {
        this.editedIndex = -1;
        this.$v.$reset();
      });
      this.form = null;
      this.dialogDelete = false;
      this.editRowId = null;
    },
  },
};
</script>
