import {
  LOGIN,
  LOGOUT,
  CHECK_AUTH,
  CHECK_ERROR,
  GET_FIRSTNAME,
  GET_LASTNAME,
  GET_EMAIL,
  GET_PERMISSIONS,
  GET_ROLE_PERMISSIONS,
} from "./actions";

import FetchHydra from "../utils/fetchHydra";
// import {isAuthenticated} from "../utils/auth";

export default (httpClient, params = {}) => {
  if (typeof httpClient === "string") {
    httpClient = new FetchHydra(httpClient);
  }

  params = {
    routes: {
      login: "auth/token",
      logout: "auth/logout",
      refresh: "auth/refresh",
      user: "auth/me",
    },
    storageKey: "token",
    getToken: (r) => r.token,
    getCredentials: ({ username, password }) => {
      return {
        username,
        password,
      };
    },
    getFirstName: (u) => u.firstname,
    getLastName: (u) => u.lastname,
    getEmail: (u) => u.email,
    getPermissions: (u) => u.roles,
    getRolePermissions: (u) => u.permissions,
    ...params,
  };

  let {
    routes,
    storageKey,
    getCredentials,
    getFirstName,
    getLastName,
    getEmail,
    getPermissions,
    getRolePermissions,
    getToken,
  } = params;

  return {
    [LOGIN]: async ({ username, password }) => {
      let response = await httpClient.post(
        routes.login,
        getCredentials({ username, password })
      );
      // console.error(response)

      if (response.status < 200 || response.status >= 300) {
        throw new Error(response.statusText);
      }
      localStorage.setItem('user', JSON.stringify(response.data.data.user));
      localStorage.setItem(storageKey, getToken(response.data.data));
      return Promise.resolve();
    },
    [LOGOUT]: async () => {
      if (routes.logout) {
        await httpClient.get(routes.logout);
      }
      localStorage.removeItem("user");
      localStorage.removeItem(storageKey);
      return Promise.resolve();
    },
    [CHECK_AUTH]: async () => {

        var token = localStorage.getItem(storageKey)
        if (typeof token == "undefined" || token == "undefined" || token == "") {
            return Promise.reject()
        }
        // if (false == isAuthenticated(token)) {
        //     return Promise.reject()
        // }
        let user = JSON.parse(localStorage.getItem("user"))
        if (user) {
            return Promise.resolve({
                data: {
                    firstname: user.firstname,
                    lastname: user.lastname,
                    email: user.email
                },
            })
        } else {
            return Promise.reject()
        }
        // console.log(data)

      /**
       * Refresh token
       */
      // if (routes.refresh) {
      //   let response = await httpClient.post(routes.refresh);
      //   localStorage.setItem(storageKey, getToken(response.data.data));
      // }
      /**
       * Get user infos
       */
      // if (routes.user) {
      //   let response = await httpClient.post(routes.user);

      //   if (response.status < 200 || response.status >= 300) {
      //     throw new Error(response.statusText);
      //   }
      //   return response.data;
      // }
    },
    [CHECK_ERROR]: ({ status }) => {
      if (status === 401 || status === 403) {
        localStorage.removeItem(storageKey);
        return Promise.reject();
      }
      return Promise.resolve();
    },
    [GET_FIRSTNAME]: (user) => getFirstName(user),
    [GET_LASTNAME]: (user) => getLastName(user),
    [GET_EMAIL]: (user) => getEmail(user),
    [GET_PERMISSIONS]: (user) => getPermissions(user),
    [GET_ROLE_PERMISSIONS]: (user) => getRolePermissions(user),
  };
};
