import camelCase from "lodash/camelCase";
import kebabCase from "lodash/kebabCase";
import upperFirst from "lodash/upperFirst";

export default ({ store, i18n, resource, title }) => {
  let { name, include, routes, translatable, getTitle, pluralName } = resource;

  let isSameErrors = []

  const setTitle = (to, action, item = null) => {
    to.meta.title = getTitle(action, item);

    /**
     * Set main and document title
     */
    document.title = `${to.meta.title} | ${title}`;

    return title;
  }
  const isObject = (object) => {
      return object != null && typeof object === 'object';
  }
  const isNullish = (object) => {
      return Object.values(object).every(value => {
          if (value === null) {
            return true;
          }
          return false;
      });
  }
  /**
   * @author Ersin Güvenç (c) 2022
   */
  const isSame = async (obj1, obj2) => {
        let result = true
        if (typeof obj1 == 'undefined' || obj1 == null || typeof obj2 == 'undefined' || obj2 == null) {
            return true
        }
        let finalObject = {}
        let defaultObject = {}
        let props1 = Object.keys(obj1)

        props1.forEach(function(key) {

            // when the page has multi tabs every object values will come as null value if
            // tab not clicked so we should ignore these objects
            if (! Array.isArray(obj1[key]) && isObject(obj1[key]) && isNullish(obj1[key])) {
                return
            }
            finalObject[key] = obj1[key]
            defaultObject[key] = obj2[key]

            // delete unnecessary keys
            // 
            if (! Array.isArray(obj1[key]) && isObject(obj1[key])) {
                if (typeof defaultObject[key] == 'undefined') {
                    isSameErrors.push("The default form object has no key like '" + key  + "'. Form data changed !")
                    console.log("The default form object has no key like '" + key  + "'. Form data changed !")
                    result = false
                    return false
                }
                let subProps = Object.keys(defaultObject[key])
                for (let s = 0; s < subProps.length; s++) {
                    let subKey = subProps[s]
                    if (typeof finalObject[key][subKey] == "undefined") {
                        delete defaultObject[key][subKey]
                    }
                }
            }
            let val1 = finalObject[key]
            let val2 = defaultObject[key]
            let isObjects = isObject(val1) && isObject(val2);
            if (isObjects && !isSame(val1, val2) || !isObjects && val1 !== val2) {
                isSameErrors.push("The '" + key + "' field is not equal. Form data changed !")
                console.log("The '" + key + "' field is not equal. Form data changed !")
                result = false
                return false
            }

            // added extra - for json data equality
            // 
            let val1Size = JSON.stringify(val1)
            let val2Size = JSON.stringify(val2)
            if (typeof val1Size === 'string' && typeof val2Size === 'string') {
                if (val1Size.length !== val2Size.length) {
                    isSameErrors.push("The '" + key + "' field is not equal. Form data changed !")
                    result = false
                    return false
                }
            }


        })
        // let encodedObj1 = JSON.stringify(finalObject)
        // let encodedObj2 = JSON.stringify(defaultObject)
        // if (encodedObj1 != encodedObj2) {
        //     return false
        // }
        return result
  }
  /**
   * Action route builder
   */
  const buildRoute = (action, path) => {
    return {
      path,
      name: `${name}_${action}`,
      props: true,
      component: {
        props: ["id"],
        render(c) {
          let componentName = `${upperFirst(camelCase(name))}${upperFirst(
            action
          )}`;

          let props = {
            id: this.id,
            title: this.$route.meta.title,
            resource,
            item: store.state[name].item,
            permissions: store.getters["auth/getPermissions"],
          };

          if (componentName in this.$options.components) {
            /**
             * Return client side page component
             */
            return c(componentName, {
              props,
            });
          }

          /**
           * Return guesser page component
           */
          return c(`Va${upperFirst(action)}Guesser`, {
            props,
          });
        },
        async beforeRouteEnter(to, from, next) {

          /**
           * Initialize from query if available
           */
          let id = to.params.id || to.query.source;

          if (id) {
            /**
             * Route model binding
             */
            try {
              let { data } = await store.dispatch(`${name}/getOne`, {
                id,
                include,
              });

              /**
               * Insert model into route & resource store
               */
              store.commit(`${name}/setItem`, data);
                  
              if (to.params.id) {
                setTitle(to, action, data);
                return next();
              }
            } catch ({ status, message }) {
              to.meta.title = message;
              document.title = message;

              store.commit(`messages/setError`, {
                status,
                message:
                  status === 404
                    ? i18n.t("va.pages.not_found", {
                        resource: resource.singularName,
                        id,
                      })
                    : message,
              });
              return next();
            }
          }

          setTitle(to, action);
          next();
        },
        async beforeRouteLeave(to, from, next) {

          let strArray = this.$route.name.split("_");
          if (Array.isArray(strArray) && strArray.length > 0) {
              let lastItem = strArray[strArray.length - 1]

              if (lastItem.trim() == "edit") {

                if (this.$root.$options.admin.saved) {
                    this.$root.$options.admin.saved = false
                    return next()
                }
                let defaultObject = store.getters[`${name}/getItem`];
                let finalObject = store.getters[`${name}/getChangedItem`];
                await isSame(finalObject, defaultObject)
                if (isSameErrors.length > 0) { // form changed

                    console.log("Form data is not equal to default form.")
                    // console.error(isSameErrors)
                    console.log("defaultObject:")
                    console.log(defaultObject)
                    console.log("finalObject")
                    console.log(finalObject)

                    isSameErrors = [] // reset errors

                    store.dispatch('openDialog').then(function(confirm){
                        if (confirm) {
                          store.commit(`${name}/removeItem`)
                          store.commit(`${name}/removeChangedItem`)
                          return next();
                        } else {
                            return next(false)
                        }
                    });
                    return next(false)     
                } else {
                    store.commit(`${name}/removeItem`)
                    store.commit(`${name}/removeChangedItem`)
                    return next();
                }
              }
          }
          store.commit(`${name}/removeItem`)
          store.commit(`${name}/removeChangedItem`)
          return next()
        },
      },
      meta: {
        authenticated: true,
        resource: name,
        translatable
      },
    };
  };

  /**
   * Return crud routes for this resource
   */
  return {
    path: `/${kebabCase(name)}`,
    component: {
      render(c) {
        return c("router-view");
      },
    },
    meta: {
      title: pluralName,
    },
    children: [
      { name: "list", path: "" },
      { name: "create", path: "create" },
      { name: "show", path: ":id" },
      { name: "edit", path: ":id/edit" },
    ]
      .filter(({ name }) => routes.includes(name))
      .map(({ name, path }) => buildRoute(name, path)),
  };
};