  created() {
    this.model.id = generateId(this);
    this.initializeCostEmployees();
    this.initializeEmployeeAllowanceSheet();
    // this.initializeAdditionalPays();
  },
  async mounted() {
    if (this.item == null) {
      let response = await this.$admin.http.get(
        "/employees/findEmployeeCostAllowanceSheet"
      );
      this.employeeCostAllowanceSheet = response.data.data;
      return;
    }
  },
  methods: {
    saveAllowanceItems(allowanceItems) {
      this.employeeCostAllowanceSheet = allowanceItems;
      this.model.employeeCostAllowanceSheet = JSON.stringify(allowanceItems);
    },
    initializeEmployeeAllowanceSheet() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(
          this.item,
          "employeeCostAllowanceSheet"
        )
      ) {
        this.model.employeeCostAllowanceSheet = this.item.employeeCostAllowanceSheet;
        this.employeeCostAllowanceSheet = JSON.parse(
          this.item.employeeCostAllowanceSheet
        );
      }
    },
    async initializeCostEmployees() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "costEmployees")
      ) {
        this.costEmployees = JSON.parse(this.item.costEmployees);
        this.model.costEmployees = this.item.costEmployees;
      }
      if (this.item.customerId && this.item.sowId) {
        let url =
          "/employees/findAllBySearch?customerId=" +
          this.item.customerId +
          "&sowId=" +
          this.item.sowId;
        let response = await this.$admin.http.get(url);
        this.employees = response.data.data;
        this.groupedEmployees = arrayGroupBy(
          this.employees,
          "customerShortName"
        );
      }
    },
    async searchDb() {
      await this.searchCostEmployees(this.search);
    },
    clearSearchData() {
      this.employees = [];
    },
    deleteSelectedItems() {
      let Self = this;
      if (
        Array.isArray(this.selectedSelected) &&
        this.selectedSelected.length > 0
      ) {
        this.selectedSelected.forEach(function (val) {
          Self.costEmployees = Self.costEmployees.filter(function (ele) {
            return ele.id !== val.id;
          });
        });
      }
      Self.model.costEmployees = JSON.stringify(Self.costEmployees);
    },
    async searchCostEmployees(q) {
      if (!this.model.customerId || !this.model.sowId) {
        return;
      }
      this.loadingEmployees = true;
      let qBase =
        "customerId=" + this.model.customerId + "&sowId=" + this.model.sowId;
      let url = "/employees/findAllBySearch?" + qBase;
      if (q) {
        url = "/employees/findAllBySearch?" + qBase + "&q=" + q;
      }
      let response = await this.$admin.http.get(url);
      this.employees = response.data.data;
      this.groupedEmployees = arrayGroupBy(this.employees, "customerShortName");
      this.loadingEmployees = false;
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "costEmployees")
      ) {
        this.selectedEmployees = JSON.parse(this.item.costEmployees);
        this.initializeParentCheckboxes();
      }
    },
    initializeParentCheckboxes() {
      let selectedGroupEmployees = arrayGroupBy(
        this.selectedEmployees,
        "customerShortName"
      );
      let Self = this;
      Object.keys(this.groupedEmployees).forEach(function (group) {
        let childrenCount = Self.groupedEmployees[group].length;
        if (
          typeof selectedGroupEmployees[group] != "undefined" &&
          selectedGroupEmployees[group].length == childrenCount
        ) {
          Self.parentChecked[group] = true;
        }
      });
    },
    filterText(value, search) {
      return (
        value != null &&
        search != null &&
        typeof value === "string" &&
        value.toString().toLocaleLowerCase().indexOf(search) !== -1
      );
    },
    toggle(isSelected, select, e, group) {
      select(!isSelected);
      let selectedChildrenCount = this.groupedEmployees[group].length;
      let selectedGroupCount = 1;
      this.selectedEmployees.forEach(function (val) {
        if (val.customerShortName == group) {
          if (isSelected) {
            --selectedGroupCount;
          } else {
            ++selectedGroupCount;
          }
        }
      });
      if (selectedGroupCount == selectedChildrenCount) {
        this.parentChecked[group] = true;
      } else {
        this.parentChecked[group] = false;
      }
    },
    toggleChildren(group, e) {
      const Self = this;
      if (e) {
        // mark child checkboxes
        let selectedIds = [];
        this.selectedEmployees.forEach(function (val) {
          selectedIds.push(val.employeeId);
        });
        if (typeof this.groupedEmployees[group] != "undefined") {
          this.groupedEmployees[group].forEach(function (gVal) {
            if (!selectedIds.includes(gVal.employeeId)) {
              Self.selectedEmployees.push(gVal);
            }
          });
          // toggleParent()
        }
      } else {
        // unmark child checkboxes
        if (typeof this.groupedEmployees[group] != "undefined") {
          this.groupedEmployees[group].forEach(function (gVal) {
            Self.selectedEmployees = Self.selectedEmployees.filter(
              (item) => item.employeeId != gVal.employeeId
            );
          });
          // toggleParent()
        }
      }
    },
    addToCostEmployees() {
      let Self = this;
      if (
        Array.isArray(this.selectedEmployees) &&
        this.selectedEmployees.length > 0
      ) {
        this.selectedEmployees.forEach(function (val) {
          let doesExist = Self.costEmployees.some(function (ele) {
            return ele.id === val.id;
          });
          // do not add same record two times
          //
          if (!doesExist) {
            Self.costEmployees.push(val);
          }
        });
      }
      this.model.costEmployees = JSON.stringify(this.costEmployees);
    },
    // initializeAdditionalPays() {
    //   if (
    //     typeof this.item === "object" &&
    //     this.item !== null &&
    //     Object.prototype.hasOwnProperty.call(
    //       this.item,
    //       "employeeCostAdditionalPays"
    //     )
    //   ) {
    //     this.model.employeeCostAdditionalPays = this.item.employeeCostAdditionalPays;
    //     this.employeeCostAdditionalPays = JSON.parse(
    //       this.item.employeeCostAdditionalPays
    //     );
    //   }
    // },
    detectCurrencyInput() {
      if (this.model.currencyId == "TRY") {
        this.currencyId = "TRY";
      }
      if (this.model.currencyId == "EUR") {
        this.currencyId = "EUR";
      }
      if (this.model.currencyId == "USD") {
        this.currencyId = "USD";
      }
    },
    detectAllowanceCurrencyId() {
      if (this.allowanceEditedItem.allowanceCurrencyId == "TRY") {
        this.allowanceCurrencyId = "TRY";
      }
      if (this.allowanceEditedItem.allowanceCurrencyId == "EUR") {
        this.allowanceCurrencyId = "EUR";
      }
      if (this.allowanceEditedItem.allowanceCurrencyId == "USD") {
        this.allowanceCurrencyId = "USD";
      }
    },
    onValidate() {
      if (this.$v["model"]["departmentId"].$invalid) {
        this.e1 = 1;
      }
      // if (this.$v["model"]["costEmployees"].$invalid) {
      //   this.e1 = 1;
      // }
    },
    onSaved() {
      if (this.e1 < 2) {
        this.e1 = 2;
        return;
      }
      if (this.e1 < 3) {
        this.e1 = 3;
        return;
      }
      if (this.e1 < 4) {
        this.e1 = 4;
        return;
      }
      if (this.e1 < 5) {
        this.e1 = 5;
        return;
      }
    },
  },
};
