        },
      ],
    };
  },
  watch: {
    dialog(val) {
      if (val == true) {
        this.renderModalComponent();
      }
      val || this.close();
    },
    dialogExcel(val) {
      if (val == true) {
        this.renderModalComponent();
      }
      val || this.close();
    },
  },
  methods: {
    renderModalComponent() {
      this.renderKey = this.renderKey + 1;
    },
    open() {
      this.dialog = true;
    },
    close() {
      this.dialog = false;
    },
    onSelect(selected) {
      this.selected = selected;
    },
    onUpdateFilter(val) {
      let filter = {
        source: "poNo",
        type: "selectfilter",
        attributes: {
          optionText: "name",
          multiple: true,
          reference: "poNumbers",
        },
        label: this.$t("expensereports.poNo"),
      };
      if (val.notEmptyPo == 1) {
        this.$refs.obj.enableFilter(filter);
      } else if (val.notEmptyPo == 0) {
        this.$refs.obj.disableFilter(filter);
      }
      if (val.customerId && typeof val.customerId[0] != "undefined") {
        this.selectedCustomerId = val.customerId[0];
      }
    },
    getExportOptions() {
      return [
        { key: "expense", label: "Masraf Tablosu" },
        { key: "subsistence", label: "Harcırah Tablosu" },
        { key: "ds", label: "Masraf Tablosu D&S" },
      ];
    },
    openExcel() {
      this.dialogExcel = true;
    },
    closeExcel() {
      this.dialogExcel = false;
    },
    async excelExport(option) {
      let userData = localStorage.getItem("user");
      let user = JSON.parse(userData);
      if (typeof user.id != "undefined") {
        let exportUrl =
          this.$admin.apiUrl +
          "/expensereports/export?userId=" +
          user.id +
          "&key=" +
          option.key;
        location.href = exportUrl;
      }
    },
  },
