import Vue from "vue";
import VuetifyAdmin from "vuetify-admin";
import "vuetify-admin/src/loader";
import {
  jsonServerDataProvider,
  jwtAuthProvider,
} from "vuetify-admin/src/providers";
import { en, tr } from "vuetify-admin/src/locales";

import router from "@/router";
import routes from "@/router/admin";
import store from "@/store";
import i18n from "@/i18n";
import axios from "axios";
import Snotify from "vue-snotify"; // 1. Import Snotify
Vue.use(Snotify,
    {
        toast: {
            position: "rightTop",
            timeout: 4000,
        }
    }
);
/**
 * Api URL
 */
const apiUrl = process.env.VUE_APP_API_URL // "http://pernet-backend";
/**
 * Load Admin UI components
 */
Vue.use(VuetifyAdmin);

axios.defaults.baseURL = apiUrl;
// Add a request interceptor
axios.interceptors.request.use(function (config) {
    config.headers["Content-Type"] = "application/json";
    var token = localStorage.getItem("token")
    if (typeof token == "undefined" || token == "undefined" || token == "") {
        return config;
    }
    config.headers["Authorization"] = "Bearer " + localStorage.getItem("token");
    return config;
  }, function (error) {
    return Promise.reject(error);
});
const http = axios

// Init constructor
// https://stackoverflow.com/questions/43431550/async-await-class-constructor
// 
/**
 * Init admin
 */
export default new VuetifyAdmin({
  router,
  store,
  i18n,
  apiUrl: apiUrl,
  downloadUrl: "/files/findOne/",
  title: "Pernet CRM",
  routes,
  locales: {
    en,
    tr,
  },
  translations: ["en", "tr"],
  dataProvider: jsonServerDataProvider(http),
  authProvider: jwtAuthProvider(http),
  // resources,
  http,
  // canAction: ({ resource, action, can }) => {
  //   if (can(["admin"])) {
  //     return true;
  //   }
  //   // any other custom actions on given resource and action...
  // },
  options: {
    dateFormat: "short",
    list: {
      disableGlobalSearch: false,
      disableItemsPerPage: true,
      itemsPerPage: 10,
      itemsPerPageOptions: [100],
    },
    tinyMCE: {
      apiKey: "e0hog6mkeo6dbsg6di5h7d5w86tu592mkl502kx3ka2bk8w7",
    },
  },
});