<template>
  <va-form :id="id" :item="item" v-model="model">
    <v-row justify="center">
      <v-col sm="12">
        <v-card flat>
          <v-card-text>
            <v-row>
              <v-col sm="4">
                <va-text-input
                  source="allowanceLabel"
                  :error-messages="allowanceLabelErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="4">
                <va-select-input
                  source="allowanceOrder"
                  reference="sqlOrders"
                  :error-messages="allowanceOrderErrors"
                ></va-select-input>
              </v-col>
            </v-row>
            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>
<script>
import { isAlpha } from "../../js/common";
import { required, maxLength, minLength } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      model: {
        id: null,
        allowanceLabel: null,
        allowanceOrder: null,
      },
    };
  },
  validations: {
    model: {
      allowanceLabel: {
        required,
        isAlpha,
        minLength: minLength(2),
        maxLength: maxLength(80),
      },
      allowanceOrder: {
        required,
        minLength: minLength(1),
        maxLength: maxLength(3),
      },
    },
  },
  computed: {
    allowanceLabelErrors() {
      const errors = [];
      const field = "allowanceLabel";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].isAlpha &&
        errors.push(this.$t("v.string.alpha"));
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "2" }));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    allowanceOrderErrors() {
      const errors = [];
      const field = "allowanceOrder";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "2" }));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
  },
  methods: {},
};
</script>
