<template>
  <div>
  <ul v-if="activeFiles.length">
    <li v-for="(file, i) in activeFiles" :key="i">
      <span class="file-item">
        <v-btn v-if="clearable" icon @click="clear(file[itemValue])">
          <v-icon>mdi-close</v-icon>
        </v-btn>
          <v-chip
            color="gray"
            x-small
            @click="getFileUrl(file)"
          >
            {{ getFileProp(file, fileName) }}
          </v-chip>
      </span>
    </li>
  </ul>
</div>
</template>

<script>
import Files from "../../../mixins/files";

/**
 * Show a list of file links that point to original files.
 */
export default {
  mixins: [Files],
  props: {
    /**
     * Set tablename
     */
    tableName: String,
  },
  methods: {
    getFileUrl(file) {
      let downloadUrl = this.$admin.apiUrl + this.$admin.downloadUrl + String(file.id) + '?tableName=' + this.tableName
      location.href = downloadUrl 
    }
  }
};
</script>

<style scoped>
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
li { }
.file-item {
  display: flex;
  align-items: center;
    margin: 0;
  padding: 0;
}
</style>
