<template>
  <div>
    <v-data-table
      :headers="headers"
      :items="getInvoiceItems"
      class="v-data-table__editable_wrapper no_top_border"
      :hide-default-footer="true"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>{{ $t("invoices.invoiceItems") }}</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn @click="createRowForm()" color="success" icon v-on="on">
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </template>
            <span>{{ $t("va.actions.create") }}</span>
          </v-tooltip>
        </v-toolbar>
      </template>
      <template
        v-for="field in getFields"
        v-slot:[`item.${field.source}`]="{ item }"
      >
        <template v-if="item._new || item.invoiceItemId === editRowId">
          <template v-if="field.source == 'itemDepartmentId'">
            <v-select
              :key="field.source"
              :filled="false"
              :item-text="'name'"
              :item-value="'id'"
              :items="departments"
              v-model="form.itemDepartmentId"
              @change="setDepartment($event)"
              :error-messages="itemDepartmentIdErrors"
              return-object
            >
            </v-select>
          </template>
          <template v-if="field.source == 'itemDescription'">
            <va-text-input
              :key="field.source"
              :source="field.source"
              v-model="form.itemDescription"
              :filled="false"
              disableFormState
            >
            </va-text-input>
          </template>
          <template v-if="field.source == 'itemCurrencyId'">
            <v-select
              :key="field.source"
              :filled="false"
              :item-text="'name'"
              :item-value="'id'"
              :items="currencies"
              v-model="form.itemCurrencyId"
              @change="setCurrency($event)"
              return-object
            >
            </v-select>
          </template>
          <template v-if="field.source == 'itemExchangeRate'">
            <va-currency-input
              :key="field.source"
              :source="field.source"
              v-model="form.itemExchangeRate"
              :options="{ currency: 'TRY', precision: 4 }"
              :filled="false"
              disableFormState
            ></va-currency-input>
          </template>
          <template v-if="field.source == 'itemAmount'">
            <va-currency-input
              :key="form[field.key] ? form[field.key] : field.source"
              :source="field.source"
              v-model="form.itemAmount"
              :options="{ currency: itemCurrencyId, precision: 2 }"
              :filled="false"
              :error-messages="itemAmountErrors"
              disableFormState
            ></va-currency-input>
          </template>
          <template v-if="field.source == 'itemTaxAmount'">
            <va-currency-input
              :key="field.source"
              :source="field.source"
              v-model="form.itemTaxAmount"
              :options="{ currency: itemCurrencyId, precision: 2 }"
              :filled="false"
              :error-messages="itemTaxAmountErrors"
              disableFormState
            ></va-currency-input>
          </template>
          <template v-if="field.source == 'itemBodyCount'">
            <va-text-input
              :key="field.source"
              v-model="form.itemBodyCount"
              type="number"
              :filled="false"
              :error-messages="itemBodyCountErrors"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'invoiceItemId'">
            <va-text-input
              :key="field.source"
              v-show="true"
              :label="$t('invoices.invoiceItemId')"
              v-model="form.invoiceItemId"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'itemFile'">
            <va-file-input
              :key="field.source"
              :source="field.source"
              v-model="form.itemFile"
              base64
              tableName="invoiceItems"
              lg="4"
              :filled="false"
              disableFormState
            >
            </va-file-input>
          </template>
        </template>
        <template v-else>
          <span v-if="field.type == 'select'" :key="field.source + '_label'">{{
            item[field.sourceLabel]
          }}</span>
          <span v-else-if="field.type == 'file'" :key="field.source + '_label'">
            <va-file-field
              :key="field.source"
              :source="field.source"
              :item="item"
              base64
              tableName="invoiceItems"
              v-slot="props"
              class="mb-0"
            >
              <slot
                :name="`field.${field.source}`"
                :item="props.item || item"
                v-bind="props"
              ></slot>
            </va-file-field>
          </span>
          <span
            v-else-if="field.type == 'currency'"
            :key="field.source + '_label'"
          >
            {{ item[field.source] | formatMoney(item.itemCurrencyId, 2) }}
          </span>
          <span v-else :key="field.source + '_label'">
            {{ item[field.source] }}
          </span>
        </template>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          small
          class="mr-2"
          v-if="item._new || item.invoiceItemId === editRowId"
          @click="saveItem()"
        >
          mdi-content-save
        </v-icon>
        <v-icon
          small
          v-if="item._new || item.invoiceItemId === editRowId"
          @click="cancel"
        >
          mdi-close
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.invoiceItemId != editRowId"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.invoiceItemId != editRowId"
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">{{
          $t("dialog.deleteConfirm")
        }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">{{
            $t("dialog.closeDeleteButton")
          }}</v-btn>
          <v-btn color="blue darken-1" text @click="deleteConfirm">{{
            $t("dialog.confirmDeleteButton")
          }}</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";
import { generateUid } from "../../js/common";

export default {
  props: ["items", "customerId"],
  data() {
    return {
      dialogDelete: false,
      form: null,
      editedIndex: -1,
      editRowId: null,
      currencyId: null,
      departments: null,
      currencies: [],
      invoiceItems: null,
      headers: [
        {
          text: this.$t("invoices.itemDepartmentName"),
          value: "itemDepartmentId",
        },
        { text: this.$t("invoices.itemDescription"), value: "itemDescription" },
        {
          text: this.$t("invoices.itemCurrencyId"),
          value: "itemCurrencyId",
        },
        {
          text: this.$t("invoices.itemExchangeRate"),
          value: "itemExchangeRate",
        },
        { text: this.$t("invoices.itemAmount"), value: "itemAmount" },
        { text: this.$t("invoices.itemTaxAmount"), value: "itemTaxAmount" },
        { text: this.$t("invoices.itemBodyCount"), value: "itemBodyCount" },
        { text: this.$t("invoices.itemFiles"), value: "itemFile" },
        {
          text: this.$t("invoices.invoiceItemsActions"),
          value: "actions",
          sortable: false,
        },
      ],
      fields: [
        {
          source: "itemDepartmentId",
          sourceLabel: "itemDepartmentName",
          reference: "departments",
          label: this.$t("invoices.itemDepartmentName"),
          type: "select",
        },
        {
          source: "itemDescription",
          label: this.$t("invoices.itemDescription"),
        },
        {
          source: "itemCurrencyId",
          sourceLabel: "itemCurrencyName",
          label: this.$t("invoices.itemCurrencyId"),
        },
        {
          source: "itemExchangeRate",
          label: this.$t("invoices.itemExchangeRate"),
          type: "currency",
        },
        {
          source: "itemAmount",
          key: "itemCurrencyId",
          label: this.$t("invoices.itemAmount"),
          type: "currency",
        },
        {
          source: "itemTaxAmount",
          label: this.$t("invoices.itemTaxAmount"),
          type: "currency",
        },
        {
          source: "itemBodyCount",
          label: this.$t("invoices.itemBodyCount"),
        },
        {
          source: "itemFile",
          type: "file",
          label: this.$t("invoices.itemFile"),
        },
      ],
    };
  },
  computed: {
    getInvoiceItems() {
      if (this.form && !this.editRowId) {
        let items = [{ _new: true }, ...this.invoiceItems];
        return items;
      }
      return this.invoiceItems;
    },
    getFields() {
      let fields = this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label: f.label,
          };
        });
      fields.itemCustomerId = null;
      fields.invoiceItemId = null;
      return fields;
    },
    itemDepartmentIdErrors() {
      const errors = [];
      const field = "itemDepartmentId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    itemAmountErrors() {
      const errors = [];
      const field = "itemAmount";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    itemTaxAmountErrors() {
      const errors = [];
      const field = "itemTaxAmount";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    itemBodyCountErrors() {
      const errors = [];
      const field = "itemBodyCount";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  validations: {
    form: {
      itemDepartmentId: {
        required,
      },
      itemAmount: {
        required,
      },
      itemTaxAmount: {
        required,
      },
      itemBodyCount: {
        required,
      },
    },
  },
  watch: {
    dialogDelete(val) {
      val || this.close();
    },
  },
  async created() {
    this.invoiceItems = this.items;
    this.itemCurrencyId = "TRY";
    var qs = "";
    if (this.customerId) {
      qs = "?customerId=" + this.customerId;
    }
    let response = await this.$admin.http.get("/departments/findAll" + qs);
    this.departments = response.data.data;
    var i = 0;
    for (const [key, value] of Object.entries(
      this.$t("resources.invoices.enums.itemCurrencyId")
    )) {
      this.currencies[i] = { id: key, name: value };
      ++i;
    }
  },
  methods: {
    detectCurrencyInput() {
      if (this.form.itemCurrencyId) {
        this.itemCurrencyId = this.form.itemCurrencyId;
      }
    },
    setDepartment(event) {
      this.form.itemDepartmentId = event.id;
      this.form.itemDepartmentName = event.name;
    },
    setCurrency(event) {
      this.form.itemCurrencyId = event.id;
      this.form.itemCurrencyName = event.name;
      this.detectCurrencyInput();
    },
    editItem(item) {
      this.createRowForm("edit", item);
      this.editedIndex = this.invoiceItems.indexOf(item);
    },
    createRowForm(action = "new", item = null) {
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
      if (action == "new") {
        this.editRowId = null;
        this.form.itemCurrencyId = "TRY";
        this.form.itemCustomerId = this.customerId;
        this.form.invoiceItemId = generateUid();
      } else {
        this.editRowId = item ? item.invoiceItemId : null;
      }
    },
    saveItem() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      if (this.editedIndex > -1) {
        Object.assign(this.invoiceItems[this.editedIndex], this.form);
      } else {
        this.invoiceItems.push(this.form);
      }
      this.$emit("save", this.invoiceItems);
      this.close();
    },
    deleteItem(item) {
      this.editedIndex = this.invoiceItems.indexOf(item);
      this.dialogDelete = true;
    },
    deleteConfirm() {
      this.invoiceItems.splice(this.editedIndex, 1);
      this.$emit("save", this.invoiceItems);
      this.close();
    },
    cancel() {
      this.close();
    },
    close() {
      this.$nextTick(async () => {
        this.editedIndex = -1;
        this.$v.$reset();
      });
      this.dialogDelete = false;
      this.form = null;
      this.editRowId = null;
    },
  },
};
</script>
