<template>
  <va-form :id="id" :item="item" v-model="model">
    <v-row justify="center">
      <v-col sm="12">
        <v-card flat>
          <v-card-text>
            <v-row>
              <v-col sm="4">
                <va-text-input
                  source="healthDocName"
                  :error-messages="healthDocNameErrors"
                ></va-text-input>
              </v-col>
              <v-col sm="2">
                <va-text-input
                  source="healthDocValidity"
                  :error-messages="healthDocValidityErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="6">
                <va-text-input
                  v-show="false"
                  :label="$t('healthdocs.healthDocJobTitles')"
                  v-model="model.healthDocJobTitles"
                ></va-text-input>
                <healthdocs-job-titles
                  :key="model.id"
                  :items="healthDocJobTitles"
                  :healthDocId="model.id"
                  @save="saveJobTitles"
                >
                </healthdocs-job-titles>
              </v-col>
            </v-row>
            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>

<script>
import { generateId } from "../../js/common";
import {
  required,
  maxLength,
  minLength,
  numeric,
} from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      model: {
        id: null,
        healthDocName: null,
        healthDocValidity: null,
        healthDocJobTitles: null,
      },
      healthDocJobTitles: [],
    };
  },
  validations: {
    model: {
      healthDocName: {
        required,
        minLength: minLength(2),
        maxLength: maxLength(120),
      },
      healthDocValidity: {
        required,
        numeric,
        maxLength: maxLength(4),
      },
    },
  },
  watch: {
    dialog(val) {
      // if (val == true) {
      //   // dialog open
      // }
      val || this.close();
    },
  },
  computed: {
    healthDocNameErrors() {
      const errors = [];
      const field = "healthDocName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "2" }));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "120" }));
      return errors;
    },
    healthDocValidityErrors() {
      const errors = [];
      const field = "healthDocValidity";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].numeric &&
        errors.push(this.$t("v.number.numeric"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "4" }));
      return errors;
    },
  },
  created() {
    this.initializeHealthDocJobTitles();
    this.model.id = generateId(this);
  },
  methods: {
    initializeHealthDocJobTitles() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "healthDocJobTitles")
      ) {
        this.model.healthDocJobTitles = this.item.healthDocJobTitles;
        this.healthDocJobTitles = JSON.parse(this.item.healthDocJobTitles);
      }
    },
    saveJobTitles(healthDocJobTitles) {
      this.healthDocJobTitles = healthDocJobTitles;
      this.model.healthDocJobTitles = JSON.stringify(healthDocJobTitles);
    },
  },
};
</script>
