<template>
  <div>
    <div class="d-flex justify-end"></div>
    <v-data-table
      :headers="headers"
      :items="getDebits"
      class="v-data-table__editable_wrapper no_top_border"
      :hide-default-footer="true"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <!-- <v-toolbar-title>{{ $t("employees.debits") }}</v-toolbar-title> -->
          <!-- <v-spacer></v-spacer> -->
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn @click="createRowForm()" color="success" icon v-on="on">
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </template>
            <span>{{ $t("va.actions.create") }}</span>
          </v-tooltip>
        </v-toolbar>
      </template>
      <template
        v-for="field in getFields"
        v-slot:[`item.${field.source}`]="{ item }"
      >
        <template v-if="item._new || item.debitId === editRowId">
          <template v-if="field.source == 'debitItemId'">
            <v-select
              :key="field.source"
              :source="field.source"
              v-model="form.debitItemId"
              :item-text="'name'"
              :item-value="'id'"
              :items="debititems"
              @change="setDebitItem($event)"
              :filled="false"
              :error-messages="debitItemIdErrors"
              return-object
            ></v-select>
          </template>
          <template v-if="field.source == 'employmentEndDate'">
            <v-text-field
              v-if="employmentEndDate != null"
              :key="field.source"
              :source="field.source"
              v-model="dateFormatted"
              readonly
              :label="$t('employees.employmentEndDate')"
            ></v-text-field>
          </template>
          <template v-if="field.source == 'debitStartDate'">
            <va-date-input
              :key="field.source"
              :source="field.source"
              v-model="form.debitStartDate"
              format="short"
              :filled="false"
              :error-messages="debitStartDateErrors"
              disableFormState
            ></va-date-input>
          </template>
          <template v-if="field.source == 'debitEndDate'">
            <va-date-input
              :key="field.source"
              :source="field.source"
              v-model="form.debitEndDate"
              format="short"
              :error-messages="debitEndDateErrors"
              :filled="false"
              disableFormState
            ></va-date-input>
          </template>
          <template v-if="field.source == 'debitItemSerialNo'">
            <va-text-input
              :key="field.source"
              :source="field.source"
              v-model="form.debitItemSerialNo"
              :error-messages="debitItemSerialNoErrors"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'debitDescription'">
            <va-text-input
              :key="field.source"
              :source="field.source"
              v-model="form.debitDescription"
              :error-messages="debitDescriptionErrors"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
        </template>
        <template v-else>
          <span v-if="field.type == 'select'" :key="field.source + '_label'">{{
            item[field.sourceLabel]
          }}</span>
          <span v-else-if="field.type == 'date'" :key="field.source + '_label'">
            {{
              item[field.source]
                ? new Date(item[field.source]).toLocaleDateString("tr")
                : ""
            }}
          </span>
          <span
            v-else-if="field.type == 'boolean'"
            :key="field.source + '_label'"
          >
            <v-icon v-if="item[field.source]" color="green">mdi-check</v-icon>
            <v-icon v-else color="red">mdi-close</v-icon>
          </span>
          <span v-else :key="field.source + '_label'">
            {{ item[field.source] }}
          </span>
        </template>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          small
          class="mr-2"
          v-if="item._new || item.debitId === editRowId"
          @click="saveItem()"
        >
          mdi-content-save
        </v-icon>
        <v-icon
          small
          v-if="item._new || item.debitId === editRowId"
          @click="cancel"
        >
          mdi-close
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.debitId != editRowId"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.debitId != editRowId"
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>

    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">{{
          $t("dialog.deleteConfirm")
        }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">{{
            $t("dialog.closeDeleteButton")
          }}</v-btn>
          <v-btn color="blue darken-1" text @click="deleteConfirm">{{
            $t("dialog.confirmDeleteButton")
          }}</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { generateUid } from "../../js/common";
import { required, maxLength } from "vuelidate/lib/validators";

export default {
  props: ["items", "employmentEndDate"],
  data() {
    return {
      dialogDelete: false,
      jobTitles: null,
      form: null,
      editedIndex: -1,
      editRowId: null,
      debits: null,
      headers: [
        {
          text: this.$t("employees.debitItemName"),
          value: "debitItemId",
        },
        {
          text: this.$t("employees.employmentEndDate"),
          value: "employmentEndDate",
        },
        {
          text: this.$t("employees.debitStartDate"),
          value: "debitStartDate",
        },
        {
          text: this.$t("employees.debitEndDate"),
          value: "debitEndDate",
        },
        {
          text: this.$t("employees.debitItemSerialNo"),
          value: "debitItemSerialNo",
        },
        {
          text: this.$t("employees.debitDescription"),
          value: "debitDescription",
        },
        {
          text: this.$t("employees.debitActions"),
          value: "actions",
          sortable: false,
        },
      ],
      fields: [
        {
          text: this.$t("employees.debitItemName"),
          source: "debitItemId",
          sourceLabel: "debitItemName",
          type: "select",
        },
        {
          text: this.$t("employees.employmentEndDate"),
          source: "employmentEndDate",
          type: "date",
        },
        {
          text: this.$t("employees.debitStartDate"),
          source: "debitStartDate",
          type: "date",
        },
        {
          text: this.$t("employees.debitEndDate"),
          source: "debitEndDate",
          type: "date",
        },
        {
          text: this.$t("employees.debitItemSerialNo"),
          source: "debitItemSerialNo",
        },
        {
          text: this.$t("employees.debitDescription"),
          source: "debitDescription",
        },
      ],
    };
  },
  computed: {
    getDebits() {
      if (this.form && !this.editRowId) {
        let items = [{ _new: true }, ...this.debits];
        return items;
      }
      return this.debits;
    },
    getFields() {
      let fields = this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label: f.label,
          };
        });
      fields.debitId = null;
      return fields;
    },
    dateFormatted() {
      if (this.employmentEndDate != null) {
        return this.$d(
          new Date(String(this.employmentEndDate)),
          this.$admin.options.dateFormat
        );
      }
      return null;
    },
    debitItemIdErrors() {
      const errors = [];
      const field = "debitItemId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    debitStartDateErrors() {
      const errors = [];
      const field = "debitStartDate";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    debitEndDateErrors() {
      const errors = [];
      const field = "debitEndDate";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    debitItemSerialNoErrors() {
      const errors = [];
      const field = "debitItemSerialNo";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "60" }));
      return errors;
    },
    debitDescriptionErrors() {
      const errors = [];
      const field = "debitDescription";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "255" }));
      return errors;
    },
  },
  async created() {
    this.debits = this.items;
    let response = await this.$admin.http.get("/debititems/findAll");
    this.debititems = response.data.data;
  },
  validations: {
    form: {
      debitItemId: {
        required,
      },
      debitStartDate: {
        required,
      },
      debitEndDate: {
        required,
      },
      debitItemSerialNo: {
        maxLength: maxLength(60),
      },
      debitDescription: {
        maxLength: maxLength(255),
      },
    },
  },
  methods: {
    setDebitItem(event) {
      this.form.debitItemId = event.id;
      this.form.debitItemName = event.name;
    },
    createRowForm(action = "new", item = null) {
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
      if (action == "new") {
        this.editRowId = null;
        this.form.debitId = generateUid();
      } else {
        this.editRowId = item ? item.debitId : null;
      }
    },
    editItem(item) {
      this.createRowForm("edit", item);
      this.editedIndex = this.debits.indexOf(item);
    },
    saveItem() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      if (this.editedIndex > -1) {
        Object.assign(this.debits[this.editedIndex], this.form);
      } else {
        this.debits.push(this.form);
      }
      this.$emit("save", this.debits);
      this.close();
    },
    deleteItem(item) {
      this.editedIndex = this.debits.indexOf(item);
      this.dialogDelete = true;
    },
    deleteConfirm() {
      this.debits.splice(this.editedIndex, 1);
      this.$emit("save", this.debits);
      this.close();
    },
    cancel() {
      this.close();
    },
    close() {
      this.$nextTick(async () => {
        this.editedIndex = -1;
        this.$v.$reset();
      });
      this.form = null;
      this.dialogDelete = false;
      this.editRowId = null;
    },
  },
};
</script>
