<template>
  <va-form :id="id" :item="item" v-model="model">
    <v-row justify="center">
      <v-col sm="12">
        <v-card>
          <v-card-text>
            <v-row>
              <v-col sm="12">
                <v-row>
                  <v-col sm="3">
                    <va-text-input
                      source="departmentName"
                      :error-messages="departmentNameErrors"
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3">
                    <va-select-input
                      source="customerId"
                      model="customerId"
                      reference="customers"
                      :error-messages="customerIdErrors"
                    ></va-select-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="3">
                    <va-text-input
                      source="managerName"
                      :error-messages="managerNameErrors"
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3">
                    <va-text-input
                      source="managerSurname"
                      :error-messages="managerSurnameErrors"
                    ></va-text-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="3">
                    <va-select-input
                      source="managerPhoneAreaCodeId"
                      reference="areaCodes"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="3">
                    <va-text-input
                      source="managerPhone"
                      :error-messages="managerPhoneErrors"
                    ></va-text-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="3">
                    <va-select-input
                      source="managerMobileAreaCodeId"
                      reference="areaCodes"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="3">
                    <va-text-input
                      source="managerMobile"
                      :error-messages="managerMobileErrors"
                    ></va-text-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="3">
                    <va-text-input
                      source="managerEmail"
                      :error-messages="managerEmailErrors"
                    ></va-text-input>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>

            <v-row>
              <v-col sm="12">
                <va-text-input
                  v-show="false"
                  source="subDepartments"
                  v-model="model.subDepartments"
                ></va-text-input>
                <departments-items
                  v-if="model.customerId && model.id"
                  :items="subDepartments"
                  :departmentId="model.id"
                  @save="saveSubDepartments"
                >
                </departments-items>
              </v-col>
            </v-row>

            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>

<script>
/// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components

import { generateId } from "../../js/common";
import {
  required,
  minLength,
  maxLength,
  email,
} from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      model: {
        id: null,
        departmentName: null,
        customerId: null,
        managerName: null,
        managerSurname: null,
        managerPhoneAreaCodeId: "TR",
        managerPhone: null,
        managerMobileAreaCodeId: "TR",
        managerMobile: null,
        managerEmail: null,
        subDepartments: "",
      },
      dialog: false,
      dialogDelete: false,
      subDepartments: [],
    };
  },
  validations: {
    model: {
      departmentName: {
        required,
        maxLength: maxLength(100),
      },
      customerId: {
        required,
        maxLength: maxLength(36),
      },
      managerName: {
        required,
        maxLength: maxLength(100),
      },
      managerSurname: {
        required,
        maxLength: maxLength(100),
      },
      managerPhone: {
        minLength: minLength(10),
      },
      managerMobile: {
        minLength: minLength(10),
      },
      managerEmail: {
        email,
      },
    },
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    departmentNameErrors() {
      const errors = [];
      const field = "departmentName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    customerIdErrors() {
      const errors = [];
      const field = "customerId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    managerNameErrors() {
      const errors = [];
      const field = "managerName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    managerSurnameErrors() {
      const errors = [];
      const field = "managerSurname";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    managerPhoneErrors() {
      const errors = [];
      const field = "managerPhone";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "10" }));
      return errors;
    },
    managerMobileErrors() {
      const errors = [];
      const field = "managerMobile";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "10" }));
      return errors;
    },
    managerEmailErrors() {
      const errors = [];
      const field = "managerEmail";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].email && errors.push(this.$t("v.email.invalid"));
      return errors;
    },
  },
  created() {
    this.initializeSubDepartments();
    this.model.id = generateId(this);
  },
  methods: {
    initializeSubDepartments() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "subDepartments")
      ) {
        this.subDepartments = JSON.parse(this.item.subDepartments);
        this.model.subDepartments = this.item.subDepartments;
      }
    },
    saveSubDepartments(subDepartments) {
      this.subDepartments = subDepartments;
      this.model.subDepartments = JSON.stringify(subDepartments);
    },
  },
};
</script>
