<template>
  <div>
    <v-menu
      v-model="menu"
      :close-on-content-click="false"
      :nudge-right="40"
      transition="scale-transition"
      offset-y
      min-width="290px"
    >
      <template v-slot:activator="{ on }">
          <v-text-field
            v-bind="commonProps"
            v-model="dateFormatted"
            v-on="on"
            :filled="filled"
            append-icon="mdi-calendar"
            @blur="setDate"
            @change="change"
            @input="updateValue"
          ></v-text-field>
      </template>
      <v-date-picker
        v-model="date"
        no-title   
        @change="change"
        @input="updateDate"
      ></v-date-picker>
    </v-menu>
  </div>
</template>

<script>
import Input from "../../../mixins/input";
import Editable from "../../../mixins/editable";

/**
 * Use for date type value editing. Is composed of a readonly textfield associated to a vuetify datepicker.
 * Do not support time, use classic VaTextInput in that case.
 */
export default {
  mixins: [Input, Editable],
  props: {
    /**
     * Date on ISO format to be edited.
     * @model
     */
    value: {
      type: String,
      default() {
        return this.$d(new Date(), this.format);
      },
    },
    /**
     * Use full filled background color style.
     */
    filled: {
      type: Boolean,
      default: true,
    },
    /**
     * Name of date format to use for textfield input.
     * Must be predefined on your VueI18n plugin.
     */
    format: {
      type: String,
      default() {
        return this.$admin.options.dateFormat;
      },
    },
    /**
     * Error status for form validation input focus feature
     */
    status: {
      type: Boolean,
    }
  },
  data() {
    return {
      menu: false,
      date: this.getInputDate(),
      defaultDate: "2022-01-01",
      dateFormatted: null,
    };
  },
  // computed: {
  //   getDate: {
  //     get() {
  //       return this.getInputDate()
  //     },
  //     set(value) {
  //       return this.getInputDate(value)
  //     }
  //   },
  // },
  watch: {
    date () {
      if (Date.parse(this.date)) {
        this.dateFormatted = this.formatInputDate(this.date)
      }
    },
  },
  created() {
    this.dateFormatted = this.formatDate()
  },
  methods: {
    setDate() {
      this.date = this.parseDate(this.dateFormatted)
    },
    getInputDate(value) {
      let date = null
      /**
       * Return ISO 8601
       */
      if (value) {
        date = new Date(value)
      } else {
        date = this.input ? new Date(this.input) : new Date()
      }
      let month = 1 + date.getMonth()
      if (month < 10) {
        month = `0${month}`
      }
      let day = date.getDate()
      if (day < 10) {
        day = `0${day}`
      }
      return `${date.getFullYear()}-${month}-${day}`
    },
    formatInputDate(date) {
      return this.$d(new Date(date), this.format)
    },
    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('.')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    formatDate() {
      /**
       * Return ISO 8601
       */
      let date = this.input ? new Date(this.input) : new Date()
      return this.$d(new Date(date), this.format)  
    },
    cleanInput(){
      this.input = null;
    },
    updateDate(val) {
      this.menu = false;
      this.update(val);
    },
    updateValue(val) {
      // format date when sending date to backend api
      let formattedValue = this.parseDate(val)
      this.update(formattedValue)
    }
  },
};
</script>
