<template>
  <form @submit.prevent="save">
    <v-data-table
      :headers="headers"
      :items="items"
      :show-select="!disableSelect"
      :disable-sort="disableSort"
      :value="listState.selected"
      :dense="dense"
      hide-default-footer
      :loading="listState.loading"
      :multi-sort="multiSort"
      :single-expand="singleExpand"
      :show-expand="showExpand"
      @click:row="onRowClick"
      :items-per-page="listState.options.itemsPerPage"
      :group-by="groupBy"
      :sort-by.sync="listState.options.sortBy"
      :sort-desc.sync="listState.options.sortDesc"
      @input="(s) => (listState.selected = s)"
      @pagination="onLoadedItems"
      @update:sort-desc="listState.reload"
      :class="{ 'clickable-rows': rowClick || !!$listeners['row-click'] }"
    >
          <template v-slot:[`group.header`]="{ isOpen, toggle, groupBy, group, isMobile }">
              <td class="text-start" :colspan="isMobile ? undefined : headers.length">
                <v-btn class="ma-0" icon small @click="toggle">
                  <v-icon>{{ isOpen ? '$minus' : '$plus' }}</v-icon>
                </v-btn>
                {{ headers.find(header => header.value === groupBy[0]).text }}: {{ group }}
              </td>
          </template>

<!--             <template v-slot:[`item.data-table-select`]="{on,props }">
              <v-simple-checkbox color="green" v-bind="props" v-on="on">
              </v-simple-checkbox>
            </template> -->
            <!-- <template v-slot:[`item`]="props">

                    <td v-for="(prop, key) in props.item" :key="key" @click="onClickItem(key, props.item[key])">
                        aa{{props.item[key]}}     
                    </td>
            </template>  -->

      <template
        v-for="field in getFields"
        v-slot:[`item.${field.source}`]="{ item, value }"
      >
        <template v-if="item._new || item.id === editRowId">
          <template v-if="field.type == 'select'">
            <component
              :key="form[field.key] ? getKey(form[field.key]) : field.source"
              :is="`va-select-input`"
              :resource="listState.resource"
              :source="field.source"
              :item="item"
              :reference="field.reference"
              @change="setFilter($event, field.filterId, field.source)"
              :filter="getFilters(field.filters)"
              v-model="form[field.source]"
              dense
              v-bind="field.attributes"
              :label="item._new ? ' ' : field.label"
              :filled="field.filled ? field.filled : false"
              :error-messages="vError(field.source)"
              :status="(typeof fieldErrors[field.source] != 'undefined' && fieldErrors[field.source] == true) ? true : false"
              class="mt-6"
          ></component>
          </template>
          <template v-else>
              <component
                :key="field.source"
                :is="`va-${field.input || field.type || 'text'}-input`"
                :resource="listState.resource"
                :item="item"
                :reference="field.reference"
                v-model="form[field.source]"
                dense
                :filled="field.filled ? field.filled : false"
                :label="(item._new | field.type == 'boolean') ? ' ' : field.label"
                v-bind="checkProperty(field, 'options', 'source') ? getOptions(field.options, form[field.options.source]) : field.attributes"
                :error-messages="vError(field.source)"
                :status="(typeof fieldErrors[field.source] != 'undefined' && fieldErrors[field.source] == true) ? true : false"
                class="mt-6"
              ></component>
          </template>
        </template>
        <template v-else>

<!--           {{ eForm[item.index][field.key] }} -->

          <component
            v-if="field.editable"
            :key="field.source"
            :is="`va-${field.input || field.type || 'text'}-input`"
            :resource="listState.resource"
            :source="field.source"
            :reference="field.reference"
            editable
            :item="item"
            :value="value"
            :filled="false"
            v-bind="checkProperty(field, 'options', 'source') ? getOptions(field.options, item[field.options.source]) : field.attributes"
            dense
            :label="field.label"
            :hide-details="true"
          ></component>
          
          <router-link
            v-else-if="field.link"
            :key="field.source"
            :to="{
              name: `${listState.resource}_${field.link}`,
              params: { id: item.id },
            }"
          >
            <component
              v-if="field.type"
              :key="field.source"
              :is="`va-${field.type}-field`"
              :resource="listState.resource"
              :item="item"
              :source="field.source"
              v-bind="checkProperty(field, 'options', 'source') ? getOptions(field.options, item[field.options.source]) : field.attributes"
              v-slot="props"
            >
              <!--
                @slot Use for field cell templating.
                @binding {string} resource Name of resource.
                @binding {string} source Property source.
                @binding {string} item Full object item.
                @binding {string} value Value of property source.
              -->
              <slot
                :name="`field.${field.source}`"
                :item="props.item || item"
                v-bind="props"
              ></slot>
            </component>
            <slot
              v-else
              :name="`field.${field.source}`"
              v-bind="{ item, value }"
            >
              {{ value }}
            </slot>
          </router-link>

          <component
            v-else-if="field.type"
            :key="field.source"
            :is="`va-${field.type}-field`"
            :resource="listState.resource"
            :item="item"
            :source="field.sourceLabel ? field.sourceLabel : field.source"
            v-bind="checkProperty(field, 'options', 'source') ? getOptions(field.options, item[field.options.source]) : field.attributes"
            v-slot="props"
          >
            <slot
              :name="`field.${field.source}`"
              :item="props.item || item"
              v-bind="props"
            ></slot>
          </component>
          <slot v-else :name="`field.${field.source}`" v-bind="{ item, value }">
            {{ value }}
          </slot>

        </template>
      </template>

      <template v-slot:[`item.actions`]="{ item }">
        <div class="item-actions">
          <!-- @slot Full cell template which contains all row actions. -->
          <slot name="cell.actions" v-bind="{ item }">
            <template v-if="item._new || item.id === editRowId">
              <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                  <v-btn
                    color="success"
                    icon
                    v-on="on"
                    type="submit"
                    :loading="saving"
                  >
                    <v-icon>mdi-floppy</v-icon>
                  </v-btn>
                </template>
                <span>{{ $t("va.actions.save") }}</span>
              </v-tooltip>
              <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                  <v-btn
                    @click="
                      editRowId = null;
                      form = null;
                      errors = {};
                    "
                    color="red"
                    icon
                    v-on="on"
                  >
                    <v-icon>mdi-close</v-icon>
                  </v-btn>
                </template>
                <span>{{ $t("va.actions.cancel") }}</span>
              </v-tooltip>
            </template>
            <template v-else>
              <va-show-button
                v-if="!disableShow"
                :disable-redirect="disableShowRedirect"
                :resource="listState.resource"
                :item="item"
                icon
                @click="(item) => onAction('show', item)"
              ></va-show-button>

              <va-table-edit-button
                v-if="rowEdit"
                :disable-redirect="true"
                :resource="listState.resource"
                :item="item"
                icon
                @click="(item) => onAction('edit', item)"
              ></va-table-edit-button>

              <va-clone-button
                v-if="!disableClone"
                :disable-redirect="disableCreateRedirect"
                :resource="listState.resource"
                :item="item"
                icon
                @click="(item) => onAction('create', item)"
              ></va-clone-button>

              <va-edit-button
                v-if="!disableEdit"
                :disable-redirect="disableEditRedirect"
                :resource="listState.resource"
                :item="item"
                icon
                @click="(item) => onAction('edit', item, false)"
              ></va-edit-button>

              <!-- @slot Use it for additional custom row actions with components based on VaActionButton. -->
              <slot name="item.actions" v-bind="{ item }"></slot>

              <!--
            Triggered on successful dissociation of resource item.
            @event dissociated
          -->
              <va-dissociate-button
                v-if="association"
                :resource="listState.resource"
                :item="item"
                :source-resource="association.resource"
                :source="association.source"
                :source-id="association.id"
                icon
                @dissociated="$emit('dissociated', item)"
              ></va-dissociate-button>
              <!--
                Triggered on successful deletetion of ressource item.
                @event deleted
              -->
              <va-delete-button
                v-if="!disableDelete"
                :resource="listState.resource"
                :item="item"
                icon
                @deleted="$emit('deleted', item)"
              ></va-delete-button>
            </template>
          </slot>
        </div>
      </template>
      <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length">
          <!-- @slot Place for full width cell which contains additional item infos. Visible when row is expanded. -->
          <slot name="expanded-item" v-bind="{ item }"></slot>
        </td>
      </template>

      <v-tooltip bottom slot="header.actions" v-if="rowCreate && hasAction('create')">
        <template v-slot:activator="{ on }">
          <v-btn @click="createRowForm()" color="success" icon v-on="on">
            <v-icon>mdi-plus</v-icon>
          </v-btn>
        </template>
        <span>{{ $t("va.actions.create") }}</span>
      </v-tooltip>

       <template slot="no-data">
           {{ $t("datatable.no_data_available") }}
       </template>
    </v-data-table>
  </form>
</template>

<script>
import upperFirst from "lodash/upperFirst";
import size from "lodash/size";

// import { required } from "vuelidate/lib/validators";
// import { validationMixin } from 'vuelidate';

/**
 * Data table component, you will need data iterator as `VaList` in order to make it usable.
 * This component allows you to template all fields columns.
 */
export default {
  inject: {
    validations: { default: undefined },
    errorMessages: { default: undefined },
    listState: { default: undefined },
  },
  props: {
    /**
     * Make each row clickable. Use predefined function as edit or show.
     * @values show, edit
     */
    rowClick: {
      type: [String, Boolean],
      default: null,
      validator: (v) => ["show", "edit"].includes(v),
    },
    /**
     * List of columns for each property of resource data.
     * Each column can be a simple string or a full object with advanced field properties.
     * Valid properties are `source`, `type`, `label`, `sortable`, `align`, `link`, `attributes`, `editable`.
     */
    fields: {
      type: Array,
      default: () => [],
    },
    /**
     * Reduce height of each row.
     */
    dense: Boolean,
    /**
     * Enable multi sort feature, enabled by default.
     */
    multiSort: {
      type: Boolean,
      default: true,
    },
    /**
     * Enable row expand mode.
     * Use it for quick detail view.
     */
    showExpand: Boolean,
    /**
     * Only one row can expanded at once
     */
    singleExpand: {
      type: Boolean,
      default: true,
    },
    groupBy: {
      type: String
    },
    /**
     * Disable select feature.
     */
    disableSelect: Boolean,
    /**
     * Disable sorting.
     */
    disableSort: Boolean,
    /**
     * Disable show action row.
     */
    disableShow: Boolean,
    /**
     * Disable show action row.
     */
    disableEdit: Boolean,
    /**
     * Disable clone action row.
     */
    disableClone: Boolean,
    /**
     * Disable delete action row.
     */
    disableDelete: Boolean,
    /**
     * Disable actions column.
     */
    disableActions: Boolean,
    /**
     * Disable create redirection. Will force clone button to show.
     */
    disableCreateRedirect: Boolean,
    /**
     * Disable show redirection. Will force show button to show.
     */
    disableShowRedirect: Boolean,
    /**
     * Disable edit redirection. Will force edit button to show.
     */
    disableEditRedirect: Boolean,
    /**
     * Disable select feature.
     */
    disableGenerateUid: Boolean,
    /**
     * Association infos object in case of this list is related to a current show or edit resource page.
     * Enable the dissociation between resources directly by an additional dissociation action.
     */
    association: {
      type: Object,
      default: () => {},
    },
    /**
     * Allow new item row.
     */
    rowCreate: Boolean,
    /**
     * Allow editable row.
     */
    rowEdit: Boolean,
    /**
     * Additional form object data to merge with row-create form.
     */
    createData: {
      type: Object,
      default: () => {},
    },
    /**
     * Additional form object data to merge with row-edit form.
     */
    updateData: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      filterIds: {},
      editRowId: null,
      form: null,
      errors: {},
      fieldErrors: {},
      saving: false,
    };
  },
  validations() {
    return {
      form: this.validations
    }
  },
  computed: {
    items() {
      if (this.form && !this.editRowId) {
        return [{ _new: true }, ...this.listState.items];
      }
      if (this.listState.items.length > 0) { // add index numbers
        return this.listState.items.map((items, index) => ({
          ...items,
          index: index
        }))  
      }
      return this.listState.items;
    },
    headers() {
      let fields = this.getFields.map((field) => {
        return {
          text: field.label,
          value: field.source,
          sortable: field.sortable,
          groupable: field.groupable,
          align: field.align || this.getDefaultAlign(field),
        };
      });

      if (!this.disableActions) {
        fields.push({
          value: "actions",
          sortable: false,
        });
      }
      return fields;
    },
    getFields() {
      return this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label:
              f.label ||
              this.$admin.getSourceLabel(
                this.listState.resource,
                f.labelKey || f.source
              ),
          };
        });
    },
  },
  watch: {
    multiSort: {
      handler(val) {
        this.listState.options.multiSort = val;
      },
      immediate: true,
    },
  },
  methods: {
    onLoadedItems() {
      return
      // for (var i=0;i<this.items.length;i++) {
      //   let item = this.items[i]
      //   let fields = this.getFields
      //     .map((f) => f.source)
      //     .reduce((o, source) => {
      //       return {
      //         ...o,
      //         [source]: item ? item[source] : null,
      //       };
      //     }, {});
      //   this.eForm[i] = fields
      // }
      // console.error(this.eForm)
    },
    vError(source) {
        if (typeof this.errorMessages[source + 'Errors'] != 'undefined') {
            let errorRules = this.errorMessages[source + 'Errors']();
            const errors = [];
            for (let i=0;i<errorRules.length;i++) {
              let rule = errorRules[i]
              if (Object.prototype.hasOwnProperty.call(rule, 'required')) {
                if (!this.$v["form"][source].$dirty) return errors; 
                !this.$v["form"][source].required && errors.push(this.$t("v.text.required"));
              }
              if (Object.prototype.hasOwnProperty.call(rule, 'minLength')) {
                if (!this.$v["form"][source].$dirty) return errors; 
                !this.$v["form"][source].minLength && errors.push(this.$t("v.string.minLength", { min: rule.minLength }));
              }
              if (Object.prototype.hasOwnProperty.call(rule, 'maxLength')) {
                if (!this.$v["form"][source].$dirty) return errors; 
                !this.$v["form"][source].maxLength && errors.push(this.$t("v.string.maxLength", { max: rule.maxLength }));
              }
              if (Object.prototype.hasOwnProperty.call(rule, 'email')) {
                if (!this.$v["form"][source].$dirty) return errors; 
                !this.$v["form"][source].email && errors.push(this.$t("v.email.invalid"));
              }
            }
            return errors;
        }
    },
    // @change="setFilter($event, field.filterId, source)"
    // :filter="getFilters(field.filters)"
    setFilter(event, filterId, source){ 
      // console.error(event)
      if (filterId) {
        if (Array.isArray(event)) { // if multiple select input
          let ids = []
          for (let i = 0; i < event.length; i++) {
              ids.push(event[i].id)
          }
          this.filterIds[filterId] = ids.join()  
        } else {   // else standart select input
          this.filterIds[filterId] = event.id
        }
        // console.error(this.filterIds)
      }
      if (typeof this.$v['form'][source] != "undefined") {
        this.$v['form'][source].touch
      }
      return
    },
    getFilters(filters) {
      if (filters) {
        let filterObject = {}
        if (size(this.filterIds) > 0) {
          for (var key in this.filterIds) {
              if (filters.includes(key)) {
                  filterObject[key] = this.filterIds[key]
              }
          }
        }
        // console.error(filterObject)
        return filterObject;
      }
      return {}     
    },
    getKey(keyValue, index = 0) {
      if (Array.isArray(keyValue)) {
          let ids = []
          for (let i = 0; i < keyValue.length; i++) {
              ids.push(keyValue[i].id)
          }
          return ids.join() + '-' + String(index)
      }
      return keyValue + '-' + String(index)
    },
    checkProperty(field, prop1, prop2) {
      if (Object.prototype.hasOwnProperty.call(field, prop1)) {
        if (Object.prototype.hasOwnProperty.call(field[prop1], prop2)) {
           return true
        }
      }
      return false
    },
    getOptions(fieldOptions, fieldValue) {
      let opt = { options: {} } // fieldOptions.id fieldValue
      opt.options[fieldOptions.id] = fieldValue 
      if (! fieldValue && typeof fieldOptions.default != 'undefined') {
        opt.options[fieldOptions.id] = fieldOptions.default
      }
      if (Object.prototype.hasOwnProperty.call(fieldOptions, 'extra')) {
          for (const [key, value] of Object.entries(fieldOptions.extra)) {
            opt.options[key] = value
          }
      }
      return opt
    },
    getDefaultAlign(field) {
      if (["number"].includes(field.type)) {
        return "right";
      }
      return "left";
    },
    onRowClick(item) {
      switch (this.rowClick) {
        case "show":
          this.$router.push({
            name: `${this.listState.resource}_show`,
            params: { id: item.id },
          });
          break;
        case "edit":
          this.$router.push({
            name: `${this.listState.resource}_edit`,
            params: { id: item.id },
          });
          break;
        default:
          /**
           * Generic `row-click` event if you set rowClick on `true` with selected item.
           */
          this.$emit("row-click", item);
          break;
      }
    },
    async onAction(action, item, disableRowEdit = null) {

      if (action === "edit" && this.rowEdit && disableRowEdit == null) {
        this.createRowForm(item);
        return;
      }

      if (action === "create" && this.rowCreate) {
        this.createRowForm(item, "new");
        return;
      }

      if (!this[`disable${upperFirst(action)}Redirect`]) {
        return;
      }

      let hasItem = action !== "create";

      let title = this.$admin
        .getResource(this.listState.resource)
        .getTitle(action, hasItem ? item : null);
      let id = hasItem ? item.id : null;

      /**
       * Get freshed item
       */
      let { data } = await this.$store.dispatch(
        `${this.listState.resource}/getOne`,
        {
          id: item.id,
        }
      );
      this.$store.commit(`${this.listState.resource}/setItem`, data);

      /**
       * Triggered on action on specific row.
       * This event will return a freshed item Object from your API.
       */
      this.$emit("item-action", { action, title, id, item: data });
    },
    createRowForm(item = null, action = null) {
      if (action == "new") {
        this.editRowId = null
      } else {
        this.editRowId = item ? item.id : null;  
      }
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
      // let filterFormVal = this.$store.getters[`${this.listState.resource}/getFilterForm`];
      // // console.error(filterFormVal)

      // for (const [key, val] of Object.entries(filterFormVal)) {
      //   if (key in this.form) {
      //     this.form[key] = this.generateSelectItem(val)
      //   }
      // }
      // console.error(this.form)
      this.errors = {};
    },
    generateSelectItem(val){
      if (Array.isArray(val)) {
        return val[0]
      }
      return val
    },
    generateUid() {
      return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function (c) {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      });
    },
    hasAction(action) {
      return this.$admin.getResource(this.listState.resource).canAction(action)
    },
    async save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        // Add input name to error fields 
        // 
        for (let key in Object.keys(this.$v['form'])) {
          const input = Object.keys(this.$v['form'])[key];              
          if (input.includes("$")) return false;
          if (this.$v['form'][input].$error) {
            this.fieldErrors[input] = true
            break;
          }
        }
        this.$nextTick(() => this.focusFirstStatus());
        return false;
      }
      this.saving = true;
      try {
        if (this.editRowId) {
          await this.$store.dispatch(`${this.listState.resource}/update`, {
              id: this.editRowId,
              data: { ...this.form, ...this.updateData },
          })
        } else {
          let newCreateData = {}
          if (!this.disableGenerateUid) {
            newCreateData.id = this.generateUid()
          } else {
            newCreateData.id = this.generateUid()
          }
          Object.assign(newCreateData, this.createData)
          await this.$store.dispatch(`${this.listState.resource}/create`, {
              data: { ...this.form, ...newCreateData },
          });
        }
        this.editRowId = null;
        this.form = null;
        this.errors = {};
        this.listState.reload();
      } catch (e) {
        if (e.errors) {
          this.errors = e.errors;
        }
      } finally {
        this.saving = false;
      }
    },

    focusFirstStatus(component = this) {
      if (component.status || component.error) {
        component.$el.querySelector("input").focus();
        return true;
      }
      let focused = false;
      component.$children.some((childComponent) => {
        focused = this.focusFirstStatus(childComponent);
        return focused;
      });
      return focused;
    },

  },
};
</script>

<style>
.v-data-table tbody tr.v-data-table__expanded__content {
  box-shadow: none;
}

.v-data-table tbody tr.v-data-table__expanded__content td {
  padding: 1.5rem;
}

.v-data-table.clickable-rows tr {
  cursor: pointer;
}

.v-data-table .item-actions {
  white-space: nowrap;
}
</style>