<template>
  <div>
    <v-data-table
      :headers="headers"
      :items="getAllowanceItems"
      class="v-data-table__editable_wrapper no_top_border"
      :hide-default-footer="true"
    >
      <template
        v-for="field in getFields"
        v-slot:[`item.${field.source}`]="{ item }"
      >
        <template v-if="item._new || item.allowanceId === editRowId">
          <template v-if="field.source == 'allowanceName'">
            <div style="width: 180px" :key="field.source">
              {{ item.allowanceName }}
            </div>
          </template>
          <template v-if="field.source == 'allowanceCurrencyId'">
            <v-select
              :key="field.source"
              :filled="false"
              :item-text="'name'"
              :item-value="'id'"
              :items="currencies"
              v-model="form.allowanceCurrencyId"
              @change="setCurrency($event)"
              :error-messages="allowanceCurrencyIdErrors"
              return-object
            >
            </v-select>
          </template>
          <template v-if="field.source == 'allowanceAmount'">
            <va-currency-input
              :key="field.source"
              :source="field.source"
              v-model="form.allowanceAmount"
              :options="{ currency: currencyId, precision: 2 }"
              :filled="false"
              :error-messages="allowanceAmountErrors"
              disableFormState
            ></va-currency-input>
          </template>
          <template v-if="field.source == 'allowanceSalaryTypeId'">
            <v-select
              :key="field.source"
              :source="field.source"
              :filled="false"
              :item-text="'name'"
              :item-value="'id'"
              :items="salaryTypes"
              v-model="form.allowanceSalaryTypeId"
              :error-messages="allowanceSalaryTypeIdErrors"
              return-object
            >
            </v-select>
          </template>
          <template v-if="field.source == 'allowanceExceptPayroll'">
            <va-boolean-input
              :key="field.source"
              :source="field.source"
              v-model="form.allowanceExceptPayroll"
              disableFormState
            ></va-boolean-input>
          </template>
        </template>
        <template v-else>
          <span v-if="field.type == 'select'" :key="field.source + '_label'">{{
            printSelect(item, field.source)
          }}</span>
          <span
            v-else-if="field.type == 'currency'"
            :key="field.source + '_label'"
          >
            {{ item[field.source] | formatMoney(item.allowanceCurrencyId, 2) }}
          </span>
          <span
            v-else-if="field.type == 'boolean'"
            :key="field.source + '_label'"
          >
            <v-icon v-if="item[field.source]" color="green">mdi-check</v-icon>
            <v-icon v-else color="red">mdi-close</v-icon>
          </span>
          <span v-else :key="field.source + '_label'">
            {{ item[field.source] }}
          </span>
        </template>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          small
          class="mr-2"
          v-if="item._new || item.allowanceId === editRowId"
          @click="saveItem()"
        >
          mdi-content-save
        </v-icon>
        <v-icon
          small
          v-if="item._new || item.allowanceId === editRowId"
          @click="cancel"
        >
          mdi-close
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.allowanceId != editRowId"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
      </template>
    </v-data-table>
    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">{{
          $t("dialog.deleteConfirm")
        }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">{{
            $t("dialog.closeDeleteButton")
          }}</v-btn>
          <v-btn color="blue darken-1" text @click="deleteConfirm">{{
            $t("dialog.confirmDeleteButton")
          }}</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";
import { generateUid } from "../../js/common";

export default {
  props: ["items"],
  data() {
    return {
      currencyId: "TRY",
      dialogDelete: false,
      form: null,
      editedIndex: -1,
      editRowId: null,
      departments: null,
      currencies: [],
      salaryTypes: [],
      allowances: null,
      headers: [
        {
          text: this.$t("costs.allowanceName"),
          value: "allowanceName",
        },
        {
          text: this.$t("costs.allowanceAmount"),
          value: "allowanceAmount",
        },
        {
          text: this.$t("costs.allowanceCurrencyId"),
          value: "allowanceCurrencyId",
        },
        {
          text: this.$t("costs.allowance"),
          value: "allowanceSalaryTypeId",
        },
        {
          text: this.$t("costs.headerAllowanceExceptPayroll"),
          value: "allowanceExceptPayroll",
        },
        {
          text: this.$t("costs.allowanceSheetActions"),
          value: "actions",
          sortable: false,
        },
      ],
      fields: [
        {
          text: this.$t("costs.allowanceName"),
          source: "allowanceName",
        },
        {
          text: this.$t("costs.allowanceAmount"),
          source: "allowanceAmount",
          type: "currency",
        },
        {
          text: this.$t("costs.allowanceCurrencyId"),
          source: "allowanceCurrencyId",
          sourceLabel: "allowanceCurrencyName",
          type: "select",
        },
        {
          text: this.$t("costs.allowanceSalaryTypeId"),
          source: "allowanceSalaryTypeId",
          sourceLabel: "allowanceSalaryTypeName",
          type: "select",
        },
        {
          text: this.$t("costs.allowanceExceptPayroll"),
          source: "allowanceExceptPayroll",
          type: "boolean",
        },
      ],
    };
  },
  computed: {
    getAllowanceItems() {
      if (this.form && !this.editRowId) {
        let items = [{ _new: true }, ...this.allowances];
        return items;
      }
      return this.allowances;
    },
    getFields() {
      let fields = this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label: f.label,
          };
        });
      fields.allowanceId = null;
      return fields;
    },
    allowanceCurrencyIdErrors() {
      const errors = [];
      const field = "allowanceCurrencyId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    allowanceAmountErrors() {
      const errors = [];
      const field = "allowanceAmount";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    allowanceSalaryTypeIdErrors() {
      const errors = [];
      const field = "allowanceSalaryTypeId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  async created() {
    this.allowances = this.items;
    this.currencyId = "TRY";
    var i = 0;
    for (const [key, value] of Object.entries(
      this.$t("resources.costs.enums.currencyId")
    )) {
      this.currencies[i] = { id: key, name: value };
      ++i;
    }
    var k = 0;
    for (const [key, value] of Object.entries(
      this.$t("resources.costs.enums.salaryTypeId")
    )) {
      this.salaryTypes[k] = { id: key, name: value };
      ++k;
    }
  },
  validations: {
    form: {
      allowanceCurrencyId: {
        required,
      },
      allowanceAmount: {
        required,
      },
      allowanceSalaryTypeId: {
        required,
      },
    },
  },
  methods: {
    printSelect(item, source) {
      if (source == "allowanceSalaryTypeId") {
        return item.allowanceSalaryTypeId == "gross"
          ? this.$t("costs.grossSalary")
          : this.$t("costs.netSalary");
      }
      return item[source];
    },
    detectCurrencyInput() {
      if (this.form.allowanceCurrencyId == "TRY") {
        this.currencyId = "TRY";
      }
      if (this.form.allowanceCurrencyId == "EUR") {
        this.currencyId = "EUR";
      }
      if (this.form.allowanceCurrencyId == "USD") {
        this.currencyId = "USD";
      }
    },
    setCurrency(event) {
      this.form.allowanceCurrencyId = event.id;
      this.form.allowanceCurrencyName = event.name;
      this.detectCurrencyInput();
    },
    editItem(item) {
      this.createRowForm("edit", item);
      this.editedIndex = this.allowances.indexOf(item);
    },
    createRowForm(action = "new", item = null) {
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
      if (action == "new") {
        this.editRowId = null;
        this.form.allowanceId = generateUid();
        this.form.allowanceCurrencyId = "TRY";
      } else {
        this.editRowId = item ? item.allowanceId : null;
      }
    },
    saveItem() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      if (this.editedIndex > -1) {
        Object.assign(this.allowances[this.editedIndex], this.form);
      } else {
        this.allowances.push(this.form);
      }
      this.$emit("save", this.allowances);
      this.close();
    },
    deleteItem(item) {
      this.editedIndex = this.allowances.indexOf(item);
      this.dialogDelete = true;
    },
    deleteConfirm() {
      this.allowances.splice(this.editedIndex, 1);
      this.$emit("save", this.allowances);
      this.close();
    },
    cancel() {
      this.close();
    },
    close() {
      this.$nextTick(async () => {
        this.editedIndex = -1;
        this.$v.$reset();
      });
      this.dialogDelete = false;
      this.form = null;
      this.editRowId = null;
    },
  },
};
</script>
