<template>
  <va-form :id="id" :item="item" v-model="model" disable-redirect>
    <v-tabs v-model="tabs">
      <v-tab>{{ $t("customers.tab1") }}</v-tab>
      <v-tab>{{ $t("customers.tab2") }}</v-tab>
      <v-tab>{{ $t("customers.tab3") }}</v-tab>
      <v-tab>{{ $t("customers.tab4") }}</v-tab>
    </v-tabs>

    <v-tabs-items v-model="tabs">
      <v-tab-item>
        <v-row justify="center">
          <v-col sm="12">
            <v-card flat>
              <v-card-text>
                <v-row>
                  <v-col sm="6">
                    <v-row>
                      <v-col sm="6">
                        <va-text-input
                          v-show="false"
                          source="id"
                        ></va-text-input>
                        <va-text-input
                          source="customerShortName"
                          :error-messages="customerShortNameErrors"
                        ></va-text-input>
                      </v-col>
                      <v-col sm="6">
                        <va-color-picker-input
                          dot-size="25"
                          mode="hexa"
                          show-swatches
                          swatches-max-height="150"
                          source="customerColor"
                          :error-messages="customerColorErrors"
                        ></va-color-picker-input>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col sm="12">
                        <va-text-input
                          source="customerName"
                          :error-messages="customerNameErrors"
                        ></va-text-input>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col sm="12">
                        <va-text-input source="address1"></va-text-input>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col sm="12">
                        <va-text-input source="address2"></va-text-input>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col sm="6">
                        <va-text-input
                          source="zipCode"
                          :error-messages="zipCodeErrors"
                        ></va-text-input>
                      </v-col>
                      <v-col sm="6">
                        <va-select-input
                          source="countryId"
                          reference="countries"
                          :error-messages="countryIdErrors"
                        ></va-select-input>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col sm="6">
                        <va-select-input
                          source="cityId"
                          reference="cities"
                          :filter="{ countryId: model.countryId }"
                          :key="model.countryId"
                          :error-messages="cityIdErrors"
                        ></va-select-input>
                      </v-col>
                      <v-col sm="6">
                        <va-text-input
                          source="taxOffice"
                          :error-messages="taxOfficeErrors"
                        ></va-text-input>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col sm="6">
                        <va-text-input
                          source="taxNumber"
                          :error-messages="taxNumberErrors"
                        ></va-text-input>
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>

                <va-save-button></va-save-button>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-tab-item>

      <v-tab-item>
        <v-row justify="center">
          <v-col sm="12">
            <v-card flat>
              <v-card-text>
                <v-row>
                  <v-col sm="6">
                    <va-text-input
                      v-show="false"
                      :label="$t('customers.jobTitles')"
                      v-model="model.customerJobTitles"
                    ></va-text-input>
                    <customers-job-titles
                      :items="customerJobTitles"
                      @save="saveJobTitles"
                    >
                    </customers-job-titles>
                  </v-col>
                </v-row>
                <va-save-button></va-save-button>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-tab-item>

      <v-tab-item>
        <v-row justify="center">
          <v-col sm="12">
            <v-card flat>
              <v-card-text>
                <v-row>
                  <v-col sm="6">
                    <va-text-input
                      v-show="false"
                      :label="$t('customers.allowances')"
                      v-model="model.customerAllowances"
                    ></va-text-input>
                    <customers-allowances
                      :items="customerAllowances"
                      @save="saveAllowances"
                    >
                    </customers-allowances>
                  </v-col>
                </v-row>
                <va-save-button></va-save-button>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-tab-item>

      <v-tab-item>
        <v-row justify="center">
          <v-col sm="12">
            <v-card flat>
              <v-card-text>
                <v-row>
                  <v-col sm="6">
                    <va-text-input
                      v-show="false"
                      :label="$t('customers.expensetypes')"
                      v-model="model.customerExpenseTypes"
                    ></va-text-input>
                    <customers-expense-types
                      :items="customerExpenseTypes"
                      @save="saveExpenseTypes"
                    >
                    </customers-expense-types>
                  </v-col>
                </v-row>
                <va-save-button></va-save-button>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-tab-item>
    </v-tabs-items>
  </va-form>
</template>

<script>
/// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components

import { generateId } from "../../js/common";
import { required, maxLength, numeric } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      tabs: null,
      menu: false,
      model: {
        id: null,
        customerColor: "#FFFFFF",
        customerShortName: null,
        customerName: null,
        address1: null,
        address2: null,
        zipCode: null,
        countryId: null,
        cityId: null,
        taxOffice: null,
        taxNumber: null,
        customerJobTitles: null,
        customerAllowances: null,
        customerExpenseTypes: null,
      },
      customerJobTitles: [],
      customerAllowances: [],
      customerExpenseTypes: [],
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: this.$t("customers.jobTitleName"),
          value: "jobTitleName",
        },
        {
          text: this.$t("customers.jobTitleActions"),
          value: "actions",
          sortable: false,
        },
      ],
      editedIndex: -1,
      editedItem: {
        jobTitleId: "",
        jobTitleName: "",
      },
      defaultItem: {
        jobTitleId: "",
        jobTitleName: "",
      },
      allowanceDialog: false,
      allowanceDialogDelete: false,
      headerAllowances: [
        {
          text: this.$t("customers.allowanceName"),
          value: "allowanceName",
        },
        {
          text: this.$t("customers.allowanceActions"),
          value: "actions",
          sortable: false,
        },
      ],
      allowanceEditedIndex: -1,
      allowanceEditedItem: {
        allowanceId: "",
        allowanceName: "",
      },
      allowanceDefaultItem: {
        allowanceId: "",
        allowanceName: "",
      },
      expenseTypeDialog: false,
      expenseTypeDialogDelete: false,
      headerExpenseTypes: [
        {
          text: this.$t("customers.expenseTypeName"),
          value: "expenseTypeName",
        },
        {
          text: this.$t("customers.expenseTypeActions"),
          value: "actions",
          sortable: false,
        },
      ],
      expenseTypeEditedIndex: -1,
      expenseTypeEditedItem: {
        expenseTypeId: "",
        expenseTypeName: "",
      },
      expenseTypeDefaultItem: {
        expenseTypeId: "",
        expenseTypeName: "",
      },
    };
  },
  validations: {
    model: {
      customerShortName: {
        required,
        maxLength: maxLength(100),
      },
      customerColor: {
        required,
        maxLength: maxLength(7),
      },
      customerName: {
        required,
        maxLength: maxLength(255),
      },
      address1: {
        maxLength: maxLength(255),
      },
      address2: {
        maxLength: maxLength(255),
      },
      zipCode: {
        numeric,
        maxLength: maxLength(10),
      },
      countryId: {
        required,
        maxLength: maxLength(2),
      },
      cityId: {
        required,
        numeric,
      },
      taxOffice: {
        maxLength: maxLength(100),
      },
      taxNumber: {
        numeric,
        maxLength: maxLength(20),
      },
    },
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    allowanceFormTitle() {
      return this.allowanceEditedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    expenseTypeFormTitle() {
      return this.expenseTypeEditedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    customerShortNameErrors() {
      const errors = [];
      const field = "customerShortName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    customerColorErrors() {
      const errors = [];
      const field = "customerColor";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "7" }));
      return errors;
    },
    customerNameErrors() {
      const errors = [];
      const field = "customerName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    zipCodeErrors() {
      const errors = [];
      const field = "zipCode";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].numeric &&
        errors.push(this.$t("v.number.numeric"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "10" }));
      return errors;
    },
    countryIdErrors() {
      const errors = [];
      const field = "countryId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "2" }));
      return errors;
    },
    cityIdErrors() {
      const errors = [];
      const field = "cityId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].numeric &&
        errors.push(this.$t("v.number.numeric"));
      return errors;
    },
    taxOfficeErrors() {
      const errors = [];
      const field = "taxOffice";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    taxNumberErrors() {
      const errors = [];
      const field = "taxNumber";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].numeric &&
        errors.push(this.$t("v.number.numeric"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "20" }));
      return errors;
    },
  },
  created() {
    this.initializeCustomerJobTitles();
    this.initializeCustomerAllowances();
    this.initializeCustomerExpenseTypes();
    this.model.id = generateId(this);
    if (this.item == null) {
      this.model.countryId = "TR";
    }
  },
  methods: {
    initializeCustomerJobTitles() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "customerJobTitles")
      ) {
        this.model.customerJobTitles = this.item.customerJobTitles;
        this.customerJobTitles = JSON.parse(this.item.customerJobTitles);
      }
    },
    initializeCustomerAllowances() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "customerAllowances")
      ) {
        this.model.customerAllowances = this.item.customerAllowances;
        this.customerAllowances = JSON.parse(this.item.customerAllowances);
      }
    },
    initializeCustomerExpenseTypes() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "customerExpenseTypes")
      ) {
        this.model.customerExpenseTypes = this.item.customerExpenseTypes;
        this.customerExpenseTypes = JSON.parse(this.item.customerExpenseTypes);
      }
    },
    saveJobTitles(jobTitles) {
      this.customerJobTitles = jobTitles;
      this.model.customerJobTitles = JSON.stringify(jobTitles);
    },
    saveAllowances(allowances) {
      this.customerAllowances = allowances;
      this.model.customerAllowances = JSON.stringify(allowances);
    },
    saveExpenseTypes(expenseTypes) {
      this.customerExpenseTypes = expenseTypes;
      this.model.customerExpenseTypes = JSON.stringify(expenseTypes);
    },
  },
};
</script>
