<template>
  <div>
    <v-card>
      <v-card-text>
        <va-list
          :filters="filters"
          :items-per-page="10"
          :disableExport="true"
          :disable-items-per-page="false"
          :disable-global-search="false"
        >
          <va-data-table :fields="fields" class="no_top_border" show-expand>
            <template v-slot:expanded-item="{ item }">
              <v-container class="pl-16 ml-10">
                <v-row>
                  <v-simple-table class="v-data-table__editable_wrapper">
                    <template v-if="hasSubDepartment(item)">
                      <thead>
                        <tr>
                          <th class="text-left">
                            {{ $t("departments.subDepartmentName") }}
                          </th>
                          <th class="text-left">
                            {{ $t("departments.subDepartmentManagerName") }}
                          </th>
                          <th class="text-left">
                            {{ $t("departments.subDepartmentManagerSurname") }}
                          </th>
                          <th class="text-left">
                            {{ $t("departments.subDepartmentManagerPhone") }}
                          </th>
                          <th class="text-left">
                            {{ $t("departments.subDepartmentManagerMobile") }}
                          </th>
                          <th class="text-left">
                            {{ $t("departments.subDepartmentManagerEmail") }}
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr
                          v-for="row in item.subDepartments"
                          :key="row.subDepartmentId"
                        >
                          <td>{{ row.subDepartmentName }}</td>
                          <td>{{ row.subDepartmentManagerName }}</td>
                          <td>{{ row.subDepartmentManagerSurname }}</td>
                          <td>{{ row.subDepartmentManagerPhone }}</td>
                          <td>{{ row.subDepartmentManagerMobile }}</td>
                          <td>{{ row.subDepartmentManagerEmail }}</td>
                        </tr>
                      </tbody>
                    </template>
                    <template v-else>
                      <!--                         <tbody>
                          <tr>
                            <td>{{ $t("datatable.no_data_available") }}</td>
                          </tr>
                        </tbody> -->
                    </template>
                  </v-simple-table>
                </v-row>
              </v-container>
            </template>
          </va-data-table>
          <!-- 
        <template v-slot:actions>
          <my-custom-button>sdsd</my-custom-button>
        </template> -->
        </va-list>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
export default {
  props: ["resource", "title"],
  data() {
    return {
      filters: [
        {
          source: "departmentName",
          label: this.$t("departments.departmentName"),
        },
        {
          source: "customerId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "customers",
          },
          label: this.$t("sows.customerId"),
        },
        { source: "managerName", label: this.$t("departments.managerName") },
        { source: "managerPhone", label: this.$t("departments.managerPhone") },
        {
          source: "managerMobile",
          label: this.$t("departments.managerMobile"),
        },
        { source: "managerEmail", label: this.$t("departments.managerEmail") },
        // { source: "active", type: "boolean" },
      ],
      fields: [
        // { source: "id", label: "Müşteri ID", sortable: true },
        {
          source: "departmentName",
          label: this.$t("departments.departmentName"),
          sortable: true,
        },
        {
          source: "customerShortName",
          label: this.$t("departments.customerShortName"),
          sortable: true,
        },
        {
          source: "managerName",
          label: this.$t("departments.managerName"),
          sortable: true,
        },
        { source: "managerPhone", label: this.$t("departments.managerPhone") },
        {
          source: "managerMobile",
          label: this.$t("departments.managerMobile"),
        },
        { source: "managerEmail", label: this.$t("departments.managerEmail") },
      ],
    };
  },
  methods: {
    hasSubDepartment(item) {
      if (
        Array.isArray(item.subDepartments) &&
        item.subDepartments.length > 0
      ) {
        return true;
      }
      return false;
    },
  },
};
</script>
