<template>
  <div>
    <v-data-table
      :headers="headers"
      :items="getOrderItems"
      class="v-data-table__editable_wrapper no_top_border"
      :hide-default-footer="true"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>{{
            $t("orders.purchaseOrderItems")
          }}</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn @click="createRowForm()" color="success" icon v-on="on">
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </template>
            <span>{{ $t("va.actions.create") }}</span>
          </v-tooltip>
        </v-toolbar>
      </template>
      <template
        v-for="field in getFields"
        v-slot:[`item.${field.source}`]="{ item }"
      >
        <template v-if="item._new || item.purchaseOrderItemId === editRowId">
          <template v-if="field.source == 'itemDepartmentId'">
            <v-select
              :key="field.source"
              :filled="false"
              :filter="{
                customerId: customerId,
              }"
              :item-text="'name'"
              :item-value="'id'"
              :items="departments"
              v-model="form.itemDepartmentId"
              @change="setDepartment($event)"
              :error-messages="itemDepartmentIdErrors"
              return-object
            >
            </v-select>
          </template>
          <template v-if="field.source == 'itemCurrencyId'">
            <v-select
              :key="field.source"
              :filled="false"
              :item-text="'name'"
              :item-value="'id'"
              :items="currencies"
              v-model="form.itemCurrencyId"
              @change="setCurrency($event)"
              return-object
            >
            </v-select>
          </template>
          <template v-if="field.source == 'itemDescription'">
            <va-text-input
              :key="field.source"
              :source="field.source"
              v-model="form.itemDescription"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'itemExchangeRate'">
            <va-currency-input
              :key="field.source"
              :source="field.source"
              v-model="form.itemExchangeRate"
              :options="{ currency: 'TRY', precision: 4 }"
              :filled="false"
              disableFormState
            ></va-currency-input>
          </template>
          <template v-if="field.source == 'itemTaxAmount'">
            <va-currency-input
              :key="field.source"
              :source="field.source"
              v-model="form.itemTaxAmount"
              :options="{ currency: currencyId, precision: 2 }"
              :filled="false"
              :error-messages="itemTaxAmountErrors"
              disableFormState
            ></va-currency-input>
          </template>
          <template v-if="field.source == 'itemTotalTaxAmount'">
            <va-currency-input
              :key="field.source"
              :source="field.source"
              v-model="form.itemTotalTaxAmount"
              :options="{ currency: currencyId, precision: 2 }"
              :filled="false"
              :error-messages="itemTotalTaxAmountErrors"
              disableFormState
            ></va-currency-input>
          </template>
          <template v-if="field.source == 'itemAmount'">
            <va-currency-input
              :key="field.source"
              :source="field.source"
              v-model="form.itemAmount"
              :options="{ currency: currencyId, precision: 2 }"
              :filled="false"
              :error-messages="itemAmountErrors"
              disableFormState
            ></va-currency-input>
          </template>
          <template v-if="field.source == 'itemTotalAmount'">
            <va-currency-input
              :key="field.source"
              :source="field.source"
              v-model="form.itemTotalAmount"
              :options="{ currency: currencyId, precision: 2 }"
              :filled="false"
              :error-messages="itemTotalAmountErrors"
              disableFormState
            ></va-currency-input>
          </template>
        </template>
        <template v-else>
          <span v-if="field.type == 'select'" :key="field.source + '_label'">{{
            item[field.sourceLabel]
          }}</span>
          <span
            v-else-if="field.type == 'currency'"
            :key="field.source + '_label'"
          >
            {{ item[field.source] | formatMoney(item.itemCurrencyId, 2) }}
          </span>
          <span v-else :key="field.source + '_label'">
            {{ item[field.source] }}
          </span>
        </template>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          small
          class="mr-2"
          v-if="item._new || item.purchaseOrderItemId === editRowId"
          @click="saveItem()"
        >
          mdi-content-save
        </v-icon>
        <v-icon
          small
          v-if="item._new || item.purchaseOrderItemId === editRowId"
          @click="cancel"
        >
          mdi-close
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.purchaseOrderItemId != editRowId"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.purchaseOrderItemId != editRowId"
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">{{
          $t("dialog.deleteConfirm")
        }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">{{
            $t("dialog.closeDeleteButton")
          }}</v-btn>
          <v-btn color="blue darken-1" text @click="deleteConfirm">{{
            $t("dialog.confirmDeleteButton")
          }}</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";
import { generateUid } from "../../js/common";

export default {
  props: ["items", "customerId"],
  data() {
    return {
      currencyId: null,
      dialogDelete: false,
      form: null,
      editedIndex: -1,
      editRowId: null,
      departments: null,
      currencies: [],
      orderItems: null,
      headers: [
        { text: this.$t("orders.itemDescription"), value: "itemDescription" },
        {
          text: this.$t("orders.itemDepartmentId"),
          value: "itemDepartmentId",
        },
        { text: this.$t("orders.itemCurrencyId"), value: "itemCurrencyId" },
        { text: this.$t("orders.itemExchangeRate"), value: "itemExchangeRate" },
        { text: this.$t("orders.itemAmount"), value: "itemAmount" },
        { text: this.$t("orders.itemTaxAmount"), value: "itemTaxAmount" },
        { text: this.$t("orders.itemTotalAmount"), value: "itemTotalAmount" },
        {
          text: this.$t("orders.itemTotalTaxAmount"),
          value: "itemTotalTaxAmount",
        },
        {
          text: this.$t("orders.purchaseOrderItemsActions"),
          value: "actions",
          sortable: false,
        },
      ],
      fields: [
        {
          text: this.$t("orders.itemDescription"),
          source: "itemDescription",
        },
        {
          text: this.$t("orders.itemDepartmentName"),
          source: "itemDepartmentId",
          sourceLabel: "itemDepartmentName",
          type: "select",
        },
        {
          text: this.$t("orders.itemCurrencyId"),
          source: "itemCurrencyId",
          sourceLabel: "itemCurrencyId",
          type: "select",
        },
        {
          text: this.$t("orders.itemExchangeRate"),
          source: "itemExchangeRate",
          type: "currency",
        },
        {
          text: this.$t("orders.itemAmount"),
          source: "itemAmount",
          type: "currency",
        },
        {
          text: this.$t("orders.itemTaxAmount"),
          source: "itemTaxAmount",
          type: "currency",
        },
        {
          text: this.$t("orders.itemTotalAmount"),
          source: "itemTotalAmount",
          type: "currency",
        },
        {
          text: this.$t("orders.itemTotalTaxAmount"),
          source: "itemTotalTaxAmount",
          type: "currency",
        },
      ],
    };
  },
  computed: {
    getOrderItems() {
      if (this.form && !this.editRowId) {
        let items = [{ _new: true }, ...this.orderItems];
        return items;
      }
      return this.orderItems;
    },
    getFields() {
      let fields = this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label: f.label,
          };
        });
      fields.itemCustomerId = this.customerId;
      fields.purchaseOrderItemId = null;
      return fields;
    },
    itemCurrencyIdErrors() {
      const errors = [];
      const field = "itemCurrencyId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    itemDepartmentIdErrors() {
      const errors = [];
      const field = "itemDepartmentId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    itemAmountErrors() {
      const errors = [];
      const field = "itemAmount";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    itemTotalAmountErrors() {
      const errors = [];
      const field = "itemTotalAmount";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    itemTaxAmountErrors() {
      const errors = [];
      const field = "itemTaxAmount";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    itemTotalTaxAmountErrors() {
      const errors = [];
      const field = "itemTotalTaxAmount";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  async created() {
    this.currencyId = "TRY";
    this.orderItems = this.items;
    var qs = "";
    if (this.customerId) {
      qs = "?customerId=" + this.customerId;
    }
    let response = await this.$admin.http.get("/departments/findAll" + qs);
    this.departments = response.data.data;
    var i = 0;
    for (const [key, value] of Object.entries(
      this.$t("resources.orders.enums.itemCurrencyId")
    )) {
      this.currencies[i] = { id: key, name: value };
      ++i;
    }
  },
  validations: {
    form: {
      itemCurrencyId: {
        required,
      },
      itemDepartmentId: {
        required,
      },
      itemAmount: {
        required,
      },
      itemTotalAmount: {
        required,
      },
      itemTaxAmount: {
        required,
      },
      itemTotalTaxAmount: {
        required,
      },
    },
  },
  methods: {
    detectCurrencyInput() {
      if (this.form.itemCurrencyId) {
        this.currencyId = this.form.itemCurrencyId;
      }
    },
    setDepartment(event) {
      this.form.itemDepartmentName = event.name;
      this.form.itemDepartmentId = event.id;
    },
    setCurrency(event) {
      this.form.itemCurrencyName = event.name;
      this.form.itemCurrencyId = event.id;
      this.detectCurrencyInput();
    },
    editItem(item) {
      this.createRowForm("edit", item);
      this.editedIndex = this.orderItems.indexOf(item);
    },
    createRowForm(action = "new", item = null) {
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
      if (action == "new") {
        this.editRowId = null;
        this.form.itemCurrencyId = "TRY";
        this.form.itemCustomerId = this.customerId;
        this.form.purchaseOrderItemId = generateUid();
      } else {
        this.editRowId = item ? item.purchaseOrderItemId : null;
      }
    },
    saveItem() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      if (this.editedIndex > -1) {
        Object.assign(this.orderItems[this.editedIndex], this.form);
      } else {
        this.orderItems.push(this.form);
      }
      this.$emit("save", this.orderItems);
      this.close();
    },
    deleteItem(item) {
      this.editedIndex = this.orderItems.indexOf(item);
      this.dialogDelete = true;
    },
    deleteConfirm() {
      this.orderItems.splice(this.editedIndex, 1);
      this.$emit("save", this.orderItems);
      this.close();
    },
    cancel() {
      this.close();
    },
    close() {
      this.$nextTick(async () => {
        this.editedIndex = -1;
        this.$v.$reset();
      });
      this.dialogDelete = false;
      this.form = null;
      this.editRowId = null;
    },
  },
};
</script>
