import i18n from "@/i18n";

/**
 * Date format
 */
["en", "tr"].forEach((locale) => {
  i18n.setDateTimeFormat(locale, {
    year: {
      year: "numeric",
    },
    month: {
      month: "short",
    },
    short: {
      year: "numeric",
      month: "numeric",
      day: "numeric",
    },
    long: {
      year: "numeric",
      month: "long",
      day: "numeric",
      weekday: "long",
      hour: "numeric",
      minute: "numeric",
      hour12: false,
    },
  });
});

/**
 * Number format
 */
i18n.setNumberFormat("en", {
  currency: {
    style: "currency",
    currency: "USD",
  },
});
i18n.setNumberFormat("tr", {
  currency: {
    style: "currency",
    currency: "TL",
  },
});
