<template>
  <div>
    <div class="d-flex justify-end"></div>
    <v-data-table
      :headers="headers"
      :items="getAgreements"
      class="v-data-table__editable_wrapper no_top_border"
      :hide-default-footer="true"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <!-- <v-toolbar-title>{{ $t("employees.agreements") }}</v-toolbar-title> -->
          <!-- <v-spacer></v-spacer> -->
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn @click="createRowForm()" color="success" icon v-on="on">
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </template>
            <span>{{ $t("va.actions.create") }}</span>
          </v-tooltip>
        </v-toolbar>
      </template>
      <template
        v-for="field in getFields"
        v-slot:[`item.${field.source}`]="{ item }"
      >
        <template v-if="item._new || item.agreementId === editRowId">
          <template v-if="field.source == 'agreementId'">
            <va-text-input
              v-show="false"
              :key="field.source"
              v-model="form.agreementId"
              :source="field.source"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'sowId'">
            <v-select
              :key="field.source"
              :source="field.source"
              :filter="{ customerId: customerId }"
              :item-text="'name'"
              :item-value="'id'"
              :items="sows"
              v-model="form.sowId"
              @change="setSow($event)"
              :filled="false"
              :error-messages="sowIdErrors"
              return-object
            >
            </v-select>
          </template>
          <template v-if="field.source == 'customerEmployeeNo'">
            <va-text-input
              :key="field.source"
              :source="field.source"
              v-model="form.customerEmployeeNo"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'jobTitleId'">
            <v-select
              :key="field.source"
              :source="field.source"
              @change="setJobTitle($event)"
              :item-text="'name'"
              :item-value="'id'"
              :items="jobTitles"
              v-model="form.jobTitleId"
              :filled="false"
              :error-messages="jobTitleIdErrors"
              return-object
            >
            </v-select>
          </template>
          <template v-if="field.source == 'agreementTypeId'">
            <v-select
              :key="field.source"
              :source="field.source"
              @change="setAgreementType($event)"
              :item-text="'name'"
              :item-value="'id'"
              :items="agreementTypes"
              v-model="form.agreementTypeId"
              :filled="false"
              :error-messages="agreementTypeIdErrors"
              return-object
            >
            </v-select>
          </template>
          <template v-if="field.source == 'departmentId'">
            <v-select
              :key="field.source"
              :source="field.source"
              @change="setDepartment($event)"
              :item-text="'name'"
              :item-value="'id'"
              :items="departments"
              v-model="form.departmentId"
              :filled="false"
              return-object
            >
            </v-select>
          </template>
          <template v-if="field.source == 'subDepartmentId'">
            <v-select
              :key="field.source + subDeparmantRenderKey"
              :source="field.source"
              @change="setSubDepartment($event)"
              :filter="{ departmentId: form.departmentId }"
              :item-text="'name'"
              :item-value="'id'"
              :items="subDepartments"
              v-model="form.subDepartmentId"
              :filled="false"
              return-object
            >
            </v-select>
          </template>
          <template v-if="field.source == 'startDate'">
            <va-date-input
              :key="field.source"
              v-model="form.startDate"
              :source="field.source"
              format="short"
              :filled="false"
              disableFormState
            ></va-date-input>
          </template>
          <template v-if="field.source == 'endDate'">
            <va-date-input
              :key="field.source"
              v-model="form.endDate"
              :source="field.source"
              format="short"
              :filled="false"
              disableFormState
            ></va-date-input>
          </template>
          <template v-if="field.source == 'isEnd'">
            <va-boolean-input
              :key="field.source"
              :source="field.source"
              :filled="false"
              v-model="form.isEnd"
              disableFormState
            ></va-boolean-input>
          </template>
          <template v-if="field.source == 'earlyEndDate'">
            <va-date-input
              :key="field.source"
              v-model="form.earlyEndDate"
              :source="field.source"
              format="short"
              :filled="false"
              disableFormState
            ></va-date-input>
          </template>
          <template v-if="field.source == 'currencyId'">
            <v-select
              :key="field.source"
              :source="field.source"
              @change="setCurrency($event)"
              :item-text="'name'"
              :item-value="'id'"
              :items="currencies"
              v-model="form.currencyId"
              :filled="false"
              return-object
            >
            </v-select>
          </template>
          <template v-if="field.source == 'endReason'">
            <va-text-input
              :key="field.source"
              v-model="form.endReason"
              :source="field.source"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
          <template v-if="field.source == 'grossSalary'">
            <va-currency-input
              :source="field.source"
              :key="field.source + currencyId"
              v-model="form.grossSalary"
              :filled="false"
              :options="{ currency: currencyId, precision: 2 }"
              disableFormState
            ></va-currency-input>
          </template>
          <template v-if="field.source == 'netSalary'">
            <va-currency-input
              :source="field.source"
              :key="field.source + currencyId"
              v-model="form.netSalary"
              :filled="false"
              :options="{ currency: currencyId, precision: 2 }"
              disableFormState
            ></va-currency-input>
          </template>
          <template v-if="field.source == 'bonus'">
            <va-currency-input
              :source="field.source"
              :key="field.source + currencyId"
              v-model="form.bonus"
              :filled="false"
              :options="{ currency: currencyId, precision: 2 }"
              disableFormState
            ></va-currency-input>
          </template>
          <template v-if="field.source == 'oss'">
            <va-boolean-input
              :source="field.source"
              :key="field.source"
              v-model="form.oss"
              :filled="false"
              disableFormState
            ></va-boolean-input>
          </template>
          <template v-if="field.source == 'car'">
            <va-boolean-input
              :source="field.source"
              :key="field.source"
              v-model="form.car"
              :filled="false"
              disableFormState
            ></va-boolean-input>
          </template>
          <template v-if="field.source == 'ticket'">
            <va-boolean-input
              :source="field.source"
              :key="field.source"
              v-model="form.ticket"
              :filled="false"
              disableFormState
            ></va-boolean-input>
          </template>
          <template v-if="field.source == 'transportation'">
            <va-boolean-input
              :source="field.source"
              :key="field.source"
              v-model="form.transportation"
              :filled="false"
              disableFormState
            ></va-boolean-input>
          </template>
          <template v-if="field.source == 'description'">
            <va-text-input
              :source="field.source"
              :key="field.source"
              v-model="form.description"
              :filled="false"
              disableFormState
            ></va-text-input>
          </template>
        </template>
        <template v-else>
          <span v-if="field.type == 'select'" :key="field.source + '_label'">{{
            item[field.sourceLabel]
          }}</span>
          <span v-else-if="field.type == 'date'" :key="field.source + '_label'">
            {{
              item[field.source]
                ? new Date(item[field.source]).toLocaleDateString("tr")
                : ""
            }}
          </span>
          <span
            v-else-if="field.type == 'currency'"
            :key="field.source + '_label'"
          >
            {{ item[field.source] | formatMoney(item.currencyId, 2) }}
          </span>
          <span
            v-else-if="field.type == 'boolean'"
            :key="field.source + '_label'"
          >
            <v-icon v-if="item[field.source]" color="green">mdi-check</v-icon>
            <v-icon v-else color="red">mdi-close</v-icon>
          </span>
          <span v-else :key="field.source + '_label'">
            {{ item[field.source] }}
          </span>
        </template>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          small
          class="mr-2"
          v-if="item._new || item.agreementId === editRowId"
          @click="saveItem()"
        >
          mdi-content-save
        </v-icon>
        <v-icon
          small
          v-if="item._new || item.agreementId === editRowId"
          @click="cancel"
        >
          mdi-close
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.agreementId != editRowId"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          v-if="!item._new && item.agreementId != editRowId"
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>

    <v-dialog v-model="dialogDelete" max-width="500px">
      <v-card>
        <v-card-title class="text-h5">{{
          $t("dialog.deleteConfirm")
        }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">{{
            $t("dialog.closeDeleteButton")
          }}</v-btn>
          <v-btn color="blue darken-1" text @click="deleteConfirm">{{
            $t("dialog.confirmDeleteButton")
          }}</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { required } from "vuelidate/lib/validators";
import { generateUid } from "../../js/common";

export default {
  props: ["items", "customerId"],
  data() {
    return {
      dialogDelete: false,
      currencyId: "TRY",
      departmentId: null,
      currencies: [],
      sows: null,
      departments: null,
      jobTitles: null,
      subDepartments: null,
      agreementTypes: null,
      subDeparmantRenderKey: 0,
      form: null,
      editedIndex: -1,
      editRowId: null,
      agreements: null,
      headers: [
        {
          text: this.$t("employees.sowId"),
          value: "sowId",
        },
        {
          text: this.$t("employees.customerEmployeeNo"),
          value: "customerEmployeeNo",
        },
        {
          text: this.$t("employees.departmentId"),
          value: "departmentId",
        },
        {
          text: this.$t("employees.subDepartmentId"),
          value: "subDepartmentId",
        },
        {
          text: this.$t("employees.agreementTypeId"),
          value: "agreementTypeId",
        },
        {
          text: this.$t("employees.jobTitle"),
          value: "jobTitleId",
        },
        {
          text: this.$t("employees.startDate"),
          value: "startDate",
        },
        {
          text: this.$t("employees.endDate"),
          value: "endDate",
        },
        {
          text: this.$t("employees.isEnd"),
          value: "isEnd",
        },
        {
          text: this.$t("employees.endReason"),
          value: "endReason",
        },
        {
          text: this.$t("employees.earlyEndDate"),
          value: "earlyEndDate",
        },
        {
          text: this.$t("employees.currencyId"),
          value: "currencyId",
        },
        {
          text: this.$t("employees.grossSalary"),
          value: "grossSalary",
        },
        {
          text: this.$t("employees.netSalary"),
          value: "netSalary",
        },
        {
          text: this.$t("employees.description"),
          value: "description",
        },
        {
          text: this.$t("employees.bonus"),
          value: "bonus",
        },
        {
          text: this.$t("employees.oss"),
          value: "oss",
        },
        {
          text: this.$t("employees.car"),
          value: "car",
        },
        {
          text: this.$t("employees.ticket"),
          value: "ticket",
        },
        {
          text: this.$t("employees.transportation"),
          value: "transportation",
        },
        {
          text: this.$t("employees.actions"),
          value: "actions",
          sortable: false,
        },
      ],
      fields: [
        {
          source: "sowId",
          sourceLabel: "sowName",
          type: "select",
          label: this.$t("employees.sowId"),
        },
        {
          source: "customerEmployeeNo",
          label: this.$t("employees.customerEmployeeNo"),
        },
        {
          source: "departmentId",
          sourceLabel: "departmentName",
          type: "select",
          label: this.$t("employees.departmentId"),
        },
        {
          source: "subDepartmentId",
          sourceLabel: "subDepartmentName",
          type: "select",
          label: this.$t("employees.subDepartmentId"),
        },
        {
          source: "agreementTypeId",
          sourceLabel: "agreementTypeName",
          type: "select",
          label: this.$t("employees.agreementTypeId"),
        },
        {
          source: "jobTitleId",
          sourceLabel: "jobTitleName",
          type: "select",
          label: this.$t("employees.jobTitleId"),
        },
        {
          source: "startDate",
          label: this.$t("employees.startDate"),
          type: "date",
        },
        {
          source: "endDate",
          label: this.$t("employees.endDate"),
          type: "date",
        },
        {
          label: this.$t("employees.isEnd"),
          source: "isEnd",
          type: "boolean",
        },
        {
          label: this.$t("employees.endReason"),
          source: "endReason",
        },
        {
          label: this.$t("employees.earlyEndDate"),
          source: "earlyEndDate",
          type: "date",
        },
        {
          label: this.$t("employees.currencyId"),
          source: "currencyId",
          sourceLabel: "currencyName",
          type: "select",
        },
        {
          label: this.$t("employees.grossSalary"),
          source: "grossSalary",
          type: "currency",
        },
        {
          label: this.$t("employees.netSalary"),
          source: "netSalary",
          type: "currency",
        },
        {
          label: this.$t("employees.description"),
          source: "description",
        },
        {
          label: this.$t("employees.bonus"),
          source: "bonus",
          type: "currency",
        },
        {
          label: this.$t("employees.oss"),
          source: "oss",
          type: "boolean",
        },
        {
          label: this.$t("employees.car"),
          source: "car",
          type: "boolean",
        },
        {
          label: this.$t("employees.ticket"),
          source: "ticket",
          type: "boolean",
        },
        {
          label: this.$t("employees.transportation"),
          source: "transportation",
          type: "boolean",
        },
      ],
    };
  },
  computed: {
    getAgreements() {
      if (this.form && !this.editRowId) {
        let items = [{ _new: true }, ...this.agreements];
        return items;
      }
      return this.agreements;
    },
    getFields() {
      let fields = this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label: f.label,
          };
        });
      fields.agreementId = null;
      return fields;
    },
    sowIdErrors() {
      const errors = [];
      const field = "sowId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    jobTitleIdErrors() {
      const errors = [];
      const field = "jobTitleId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    agreementTypeIdErrors() {
      const errors = [];
      const field = "agreementTypeId";
      if (!this.$v["form"][field].$dirty) return errors;
      !this.$v["form"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  validations: {
    form: {
      sowId: {
        required,
      },
      jobTitleId: {
        required,
      },
      agreementTypeId: {
        required,
      },
    },
  },
  async created() {
    this.agreements = this.items;
    this.currencyId = "TRY";
    let response = null;
    let qs = "";
    if (this.customerId) {
      qs = "?customerId=" + this.customerId;
    }
    response = await this.$admin.http.get("/sows/findAll" + qs);
    this.sows = response.data.data;

    response = await this.$admin.http.get("/departments/findAll" + qs);
    this.departments = response.data.data;

    response = await this.$admin.http.get("/agreementTypes/findAll");
    this.agreementTypes = response.data.data;

    this.initSubDepartments();

    response = await this.$admin.http.get("/jobTitles/findAll");
    this.jobTitles = response.data.data;

    let i = 0;
    for (const [key, value] of Object.entries(
      this.$t("resources.employees.enums.currencyId")
    )) {
      this.currencies[i] = { id: key, name: value };
      ++i;
    }
  },
  watch: {
    dialogDelete(val) {
      val || this.close();
    },
  },
  methods: {
    async initSubDepartments() {
      let qs = "";
      if (this.departmentId) {
        qs += "?departmentId=" + this.departmentId;
      }
      let response = await this.$admin.http.get("/subDepartments/findAll" + qs);
      this.subDepartments = response.data.data;
    },
    renderSubDepartment() {
      this.subDeparmantRenderKey = this.subDeparmantRenderKey + 1;
      this.initSubDepartments();
    },
    detectCurrencyInput() {
      if (this.form.currencyId == "TRY") {
        this.currencyId = "TRY";
      }
      if (this.form.currencyId == "EUR") {
        this.currencyId = "EUR";
      }
      if (this.form.currencyId == "USD") {
        this.currencyId = "USD";
      }
    },
    setSow(event) {
      this.form.sowId = event.id;
      this.form.sowName = event.name;
    },
    setDepartment(event) {
      this.departmentId = event.id;
      this.form.departmentId = event.id;
      this.form.departmentName = event.name;
      this.renderSubDepartment();
    },
    setSubDepartment(event) {
      this.form.subDepartmentId = event.id;
      this.form.subDepartmentName = event.name;
    },
    setAgreementType(event) {
      this.form.agreementTypeId = event.id;
      this.form.agreementTypeName = event.name;
    },
    setCurrency(event) {
      this.form.currencyId = event.id;
      this.form.currencyName = event.name;
      this.detectCurrencyInput();
    },
    setJobTitle(event) {
      this.form.jobTitleId = event.id;
      this.form.jobTitleName = event.name;
    },
    createRowForm(action = "new", item = null) {
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
      if (action == "new") {
        this.editRowId = null;
        this.form.customerId = this.customerId;
        this.form.currencyId = "TRY";
        this.form.currencyName = "Türk Lirası";
        this.form.agreementId = generateUid();
      } else {
        this.editRowId = item ? item.agreementId : null;
      }
    },
    editItem(item) {
      this.createRowForm("edit", item);
      this.editedIndex = this.agreements.indexOf(item);
    },
    saveItem() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      if (this.editedIndex > -1) {
        Object.assign(this.agreements[this.editedIndex], this.form);
      } else {
        this.agreements.push(this.form);
      }
      this.$emit("save", this.agreements);
      this.close();
    },
    deleteItem(item) {
      this.editedIndex = this.agreements.indexOf(item);
      this.dialogDelete = true;
    },
    deleteConfirm() {
      this.agreements.splice(this.editedIndex, 1);
      this.$emit("save", this.agreements);
      this.close();
    },
    cancel() {
      this.close();
    },
    close() {
      this.$nextTick(async () => {
        this.editedIndex = -1;
        this.$v.$reset();
      });
      this.form = null;
      this.dialogDelete = false;
      this.editRowId = null;
    },
  },
};
</script>
