          label: this.$t("expenses.customerId"),
          alwaysOn: true,
        },
        {
          source: "departmentId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "departments",
          },
          filterId: "customerId",
          label: this.$t("expensereports.departmentName"),
