            multiple: true,
            reference: "paymentTypes",
          },
          label: this.$t("expenses.paymentTypeId"),
          alwaysOn: true,
        },
        {
          source: "expenseApplication",
          type: "text",
          label: this.$t("expenses.expenseApplication"),
        },
        {
          source: "packageNo",
          type: "text",
          label: this.$t("expenses.packageNo"),
        },
        {
          source: "startDate",
          type: "date",
          label: this.$t("expenses.startDate"),
          alwaysOn: true,
        },
        {
          source: "endDate",
          type: "date",
          label: this.$t("expenses.endDate"),
          alwaysOn: true,
        },
      ],
      fields: [
        {
          source: "customerId",
          sourceLabel: "customerShortName",
          // attributes: {
          //   multiple: true
          // },
          type: "select",
          reference: "customers",
          filterId: "customerId",
          label: this.$t("expenses.customerName"),
          sortable: true,
          editable: false,
        },
        {
          source: "workplaceId",
          sourceLabel: "workplaceName",
          type: "select",
          reference: "workplaces",
          label: this.$t("expenses.workplaceName"),
          sortable: true,
          editable: false,
        },
        {
          source: "sowId",
          sourceLabel: "sowName",
          type: "select",
          reference: "sows",
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expenses.sowName"),
          sortable: true,
          editable: false,
        },
        {
          source: "departmentId",
          sourceLabel: "departmentName",
          type: "select",
          reference: "departments",
          filterId: "departmentId",
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expenses.departmentName"),
          sortable: true,
          editable: false,
        },
        {
          source: "subDepartmentId",
