          type: "select",
          reference: "subDepartments",
          label: this.$t("expenses.subDepartmentName"),
          key: "departmentId",
          filters: ["departmentId"],
          sortable: true,
          editable: false,
        },
        {
          source: "employeeId",
          sourceLabel: "employeeName",
          type: "select",
          reference: "employees",
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expenses.employeeName"),
          sortable: true,
          editable: false,
        },
        {
          source: "employeeName",
          label: this.$t("expenses.employeeName"),
          sortable: true,
        },
        {
          source: "pernetNumber",
          label: this.$t("employees.pernetNumber"),
          sortable: true,
        },
        {
          source: "expenseTypeId",
          sourceLabel: "expenseTypeName",
          type: "select",
          reference: "expenseTypes",
          label: this.$t("expenses.expenseTypeId"),
          sortable: true,
          editable: true,
        },
        {
          source: "paymentTypeId",
          sourceLabel: "paymentTypeName",
          type: "select",
          reference: "paymentTypes",
          label: this.$t("expenses.paymentTypeId"),
          sortable: true,
          editable: true,
        },
        {
          source: "description",
          label: this.$t("expenses.description"),
          sortable: true,
          editable: true,
        },
        {
          source: "startDate",
          type: "date",
          label: this.$t("expenses.startDate"),
          sortable: true,
          editable: true,
        },
        {
          source: "endDate",
          type: "date",
          label: this.$t("expenses.endDate"),
          sortable: true,
          editable: true,
        },
        {
          source: "docNumber",
          label: this.$t("expenses.docNumber"),
          sortable: true,
        },
        {
          source: "currencyId",
          type: "select",
          label: this.$t("expenses.currencyId"),
          sortable: true,
          editable: true,
        },
        {
          source: "amount",
          type: "currency",
          options: {
            id: "currency",
            source: "currencyId",
            default: "TRY",
            extra: { precision: 2 },
          },
          label: this.$t("expenses.amount"),
          sortable: true,
          editable: true,
        },
        {
          source: "tax",
          type: "currency",
          options: {
            id: "currency",
            source: "currencyId",
            default: "TRY",
            extra: { precision: 2 },
          },
          label: this.$t("expenses.tax"),
          sortable: true,
          // editable: true,
        },
        {
          source: "totalAmount",
          type: "currency",
          options: {
            id: "currency",
            source: "currencyId",
            default: "TRY",
            extra: { precision: 2 },
          },
          label: this.$t("expenses.totalAmount"),
          sortable: true,
          // editable: true,
        },
        {
          source: "whoPaidId",
          sourceLabel: "whoPaidId",
          type: "select",
          label: this.$t("expenses.whoPaidId"),
          sortable: true,
          editable: true,
        },
        {
          source: "isPaidOff",
          label: this.$t("expenses.isPaidOff"),
          type: "boolean",
          sortable: true,
          editable: true,
        },
        {
          source: "employeePaidDate",
          type: "date",
          label: this.$t("expenses.employeePaidDate"),
          sortable: true,
          editable: true,
        },
        // {
        //   source: "confirmNo",
        //   label: this.$t("expenses.confirmNo"),
        //   sortable: true,
        // },
        // {
        //   source: "confirmStatus",
        //   label: this.$t("expenses.confirmStatus"),
        //   type: "boolean",
        //   sortable: true,
        // },
        {
          source: "sentForApproval",
          label: this.$t("expenses.sentForApproval"),
          type: "boolean",
          sortable: true,
          editable: true,
        },
        {
          source: "expenseApplication",
          label: this.$t("expenses.expenseApplication"),
          sortable: true,
        },
        {
          source: "packageNo",
          label: this.$t("expenses.packageNo"),
          sortable: true,
        },
        {
          source: "file",
          label: this.$t("expenses.file"),
          type: "file",
          attributes: {
            base64: true,
            tableName: "expenseFiles",
          },
          sortable: false,
        },
      ],
    };
  },
  watch: {
    dialog(val) {
      if (val == true) {
        this.renderModalComponent();
      }
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  methods: {
    onWidth(w) {
      this.width = w;
    },
    onFilterChange(newVal) {
      this.$store.commit("expenses/setFilterForm", newVal);
      let customerSelected = Object.prototype.hasOwnProperty.call(
        newVal,
        "customerId"
      );
      if (customerSelected && !isEmpty(newVal.customerId)) {
        this.customerId = true;
      } else {
        this.customerId = false;
      }
    },
    renderModalComponent() {
      this.renderKey = this.renderKey + 1;
    },
    open() {
      this.dialog = true;
    },
    close() {
      this.dialog = false;
      this.width = "600px";
    },
    onSelect(selected) {
      this.selected = selected;
    },
    async importExcel() {
      this.open();
      return;
    },
  },
};
</script>
