<template>
  <div>
    <va-form
      :id="id"
      :item="item"
      @validate="onValidate"
      v-model="model"
      disable-redirect
    >
      <v-card>
        <v-tabs v-model="tabs">
          <v-tab>{{ $t("sows.tab1") }}</v-tab>
          <v-tab>{{ $t("sows.tab2") }}</v-tab>
          <v-tab>{{ $t("sows.tab3") }} ({{ purchaseOrders.length }})</v-tab>
        </v-tabs>
        <v-tabs-items v-model="tabs">
          <v-tab-item>
            <v-card-text>
              <v-row>
                <v-col sm="3">
                  <va-select-input
                    source="customerId"
                    reference="customers"
