                  ></va-select-input>
                </v-col>
                <v-col sm="3"></v-col>
              </v-row>
              <v-row>
                <v-col sm="2">
                  <va-select-input
                    source="purchaserId"
                    reference="purchasers"
                    :filter="{ departmentId: model.departmentId }"
                    :key="model.departmentId"
                    :error-messages="purchaserIdErrors"
                  ></va-select-input>
                </v-col>
                <v-col sm="2">
                  <va-text-input
                    source="sowName"
                    :error-messages="sowNameErrors"
                  ></va-text-input>
                </v-col>
                <v-col sm="4">
                  <va-text-input
                    source="sowDescription"
                    :error-messages="sowDescriptionErrors"
                    multiline
                  ></va-text-input>
                </v-col>
              </v-row>
              <v-row>
                <v-col sm="2">
                  <va-date-input
                    source="startDate"
                    format="short"
                    :error-messages="startDateErrors"
                  ></va-date-input>
                </v-col>
                <v-col sm="2">
                  <va-date-input
                    source="endDate"
                    format="short"
                    :error-messages="endDateErrors"
                  ></va-date-input>
                </v-col>
                <v-col sm="2">
                  <va-select-input
                    source="currencyId"
                    @change="detectCurrencyInput()"
