<template>
  <va-form :id="id" :item="item" v-model="model">
    <v-row justify="center">
      <v-col sm="12">
        <v-card flat>
          <v-card-text>
            <v-row>
              <v-col sm="4">
                <va-text-input
                  source="jobTitleName"
                  :error-messages="jobTitleNameErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>
<script>
import { generateId } from "../../js/common";
import { required, maxLength, minLength } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      model: {
        id: null,
        jobTitleName: null,
      },
    };
  },
  validations: {
    model: {
      jobTitleName: {
        required,
        minLength: minLength(2),
        maxLength: maxLength(255),
      },
    },
  },
  computed: {
    jobTitleNameErrors() {
      const errors = [];
      const field = "jobTitleName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "2" }));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "255" }));
      return errors;
    },
  },
  created() {
    this.model.id = generateId(this);
  },
  methods: {},
};
</script>
