<template>
  <div>
    <va-aside-layout :title="asideTitle">
      <!-- <costs-forma :id="id" :item="item" @saved="onSaved"></costs-forma> -->
      <!-- <costs-show :item="item"></costs-show> -->
      <costs-forma
        v-if="action != 'show'"
        :id="id"
        :item="item"
        @saved="onSaved"
      ></costs-forma>
    </va-aside-layout>

    <v-card>
      <v-card-text>
        <va-list
          :filters="filters"
          :items-per-page="10"
          :disableExport="true"
          :disable-items-per-page="false"
          :disable-global-search="false"
          disable-create-redirect
          @action="onAction"
        >
          <va-data-table
            :fields="fields"
            disable-create-redirect
            @item-action="onAction"
          >
            <template v-slot:[`field.calculationDate`]="{ item }">
              <span>{{
                new Date(item.calculationDate).toLocaleDateString("tr")
              }}</span>
            </template>
            <template v-slot:[`field.currencyId`]="{ item }">
              {{ item.currencyId == "TRY" ? "TL" : item.currencyId }}
            </template>
            <template v-slot:[`field.isConfirmed`]="{ item }">
              {{
                item.isConfirmed == 1 ? $t("datatable.yes") : $t("datatable.no")
              }}
            </template>
          </va-data-table>

          <!--         <template v-slot:actions>
          <my-custom-button>sdsd</my-custom-button>
        </template> -->
        </va-list>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
export default {
  props: ["resource", "title", "opened"],
  data() {
    return {
      filters: [
        { source: "versionName", label: this.$t("costs.versionName") },
        {
          source: "currencyId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
          },
          label: this.$t("costs.currencyId"),
        },
        {
          source: "calculationDateStart",
          type: "date",
          label: this.$t("costs.calculationDateStart"),
        },
        {
          source: "calculationDateEnd",
          type: "date",
          label: this.$t("costs.calculationDateEnd"),
        },
        {
          source: "sowId",
          type: "autocompletefilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "sows",
          },
          label: this.$t("costs.sowId"),
        },
        {
          source: "customerId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "customers",
          },
          label: this.$t("costs.customerId"),
        },
        { source: "employeeName", label: this.$t("costs.employeeName") },
        { source: "employeeEmail", label: this.$t("costs.employeeEmail") },
        { source: "employeePhone", label: this.$t("costs.employeePhone") },
        { source: "pernetNumber", label: this.$t("costs.pernetNumber") },
        {
          source: "departmentId",
          type: "autocompletefilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "departments",
          },
          label: this.$t("costs.departmentId"),
        },
        {
          source: "isConfirmed",
          type: "boolean",
          label: this.$t("costs.isConfirmed"),
        },
        {
          source: "confirmedDepartmentId",
          type: "autocompletefilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "departments",
          },
          label: this.$t("costs.confirmedDepartment"),
        },
        {
          source: "confirmedPurchaserId",
          type: "autocompletefilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "purchasers",
          },
          label: this.$t("costs.confirmedPurchaser"),
        },
      ],
      fields: [
        {
          source: "customerShortName",
          label: this.$t("costs.customerShortName"),
          sortable: true,
        },
        {
          source: "departmentName",
          label: this.$t("costs.departmentName"),
          sortable: true,
        },
        {
          source: "versionName",
          label: this.$t("costs.versionName"),
          sortable: true,
        },
        {
          source: "currencyId",
          label: this.$t("costs.currencyId"),
          sortable: true,
        },
        {
          source: "calculationDate",
          label: this.$t("costs.calculationDate"),
          sortable: true,
        },
        {
          source: "employeeName",
          label: this.$t("costs.employeeName"),
          sortable: true,
        },
        {
          source: "pernetNumber",
          label: this.$t("costs.pernetNumber"),
          sortable: true,
        },
        {
          source: "resultTotalCost",
          label: this.$t("costs.resultTotalCost"),
          sortable: true,
        },
        { source: "isConfirmed", label: this.$t("costs.isConfirmed") },
        {
          source: "confirmedDepartmentName",
          label: this.$t("costs.confirmedDepartmentName"),
          sortable: true,
        },
        {
          source: "confirmedPurchaserName",
          label: this.$t("costs.confirmedPurchaserName"),
          sortable: true,
        },
        {
          source: "startDate",
          label: this.$t("costs.startDate"),
          sortable: true,
        },
        {
          source: "endDate",
          label: this.$t("costs.endDate"),
          sortable: true,
        },
      ],
      asideTitle: null,
      id: null,
      action: null,
      item: null,
    };
  },
  methods: {
    async onAction({ action, title, id, item }) {
      this.asideTitle = title;
      this.id = id;
      this.item = item;
      this.action = action;
      if (action === "create") {
        this.action = null;
      }
    },
    async onSaved() {
      this.asideTitle = null;
    },
  },
};
</script>
