import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import i18n from "./i18n";
import vuetify from "./plugins/vuetify";
import admin from "./plugins/admin";
import axios from "axios";
import "./plugins/i18n";
import "./plugins/chartist";
import Vuelidate from "vuelidate";
import AddressField from "./components/fields/AddressField";
import Modal from "./components/Modal";
import VueMask from "v-mask";
import money from "v-money";
import moment from "moment";
import resources from "@/resources";

// console.log(Vue.version)
/**
 * money formatter for table grids and viewing data as string
 */
Vue.filter("formatMoney", function (value, currency, precision = 2) {
  var formatter = new Intl.NumberFormat("tr-TR", {
    style: "currency",
    currency: currency,
    minimumFractionDigits: precision,
  });
  return formatter.format(value);
});
/**
 * date formatter
 */
Vue.filter("formatDate", function (value) {
  if (value) {
    return moment(String(value)).format("DD.MM.YYYY hh:mm");
  }
});
/**
 * file size formatter
 */
Vue.filter("formatSize", function (value) {
  if (value >= 1073741824) {
    value = value / 1073741824;
    value = Number.parseFloat(value).toFixed(2) + " Gb";
  } else if (value >= 1048576) {
    value = value / 1048576;
    value = Number.parseFloat(value).toFixed(2) + " Mb";
  } else if (value >= 1024) {
    value = value / 1024;
    value = Number.parseFloat(value).toFixed(2) + " Kb";
  } else if (value > 1) {
    value = value + " B";
  } else if (value == 1) {
    value = value + " B";
  } else {
    value = "0 B";
  }
  return value;
});
Vue.use(money, { precision: 4 });
Vue.use(VueMask);
Vue.component("modal", Modal);
Vue.component("VaAddressField", AddressField);
Vue.config.productionTip = false;
Vue.use(Vuelidate);

// admin.getResource('roles').canAction('edit');

// Assign resources async
//
admin.setResources(resources);

let vm = new Vue({
  router,
  store,
  vuetify,
  i18n,
  admin,
  render: (h) => h(App),
}).$mount("#app");

// axios global config
//
axios.interceptors.response.use(
  (response) => {
    if (
      (response.status == "200" &&
        response.config.url.indexOf("/create") > 0) ||
      response.config.url.indexOf("/update") > 0
    ) {
      vm.$snotify.success(vm.$t("form.saved"));
    }
    return response;
  },
  function (error) {
    const originalRequest = error.config;
    // if (originalRequest.url === 'auth/logout') {  // do logout
    //     return Promise.reject(error);
    // }
    var token = localStorage.getItem("token");
    var user = localStorage.getItem("user");
    if (typeof token === "undefined" || token == "undefined" || token == "") {
      window.location.href = "/login";
      return;
    }
    if (typeof user === "undefined" || user == "undefined" || user == "") {
      window.location.href = "/login";
      return;
    }
    // Object.prototype.hasOwnProperty.call(error.response.data, 'error')
    if (
      error.response.headers["token-expired"] == "1" &&
      !originalRequest._retry
    ) {
      originalRequest._retry = true;
      const options = {
        headers: {
          "Content-Type": "application/json",
        },
      };
      return axios
        .post(
          "auth/refresh",
          {
            token: token,
          },
          options
        )
        .then((res) => {
          /**
           * Set new token the original request
           */
          if (res.status === 200) {
            localStorage.setItem("token", res.data.data.token);
            originalRequest.headers["Authorization"] =
              "Bearer " + res.data.data.token;
            return axios(originalRequest);
          }
          if (res.status === 401) {
            // Token expired
            vm.$snotify.error(res.data.error);
            localStorage.removeItem("token");
            localStorage.removeItem("user");
            window.location.href = "/login";
            return;
          }
        });
    }
    // info notifications
    //
    if (typeof error.response.data.info !== "undefined") {
      vm.$snotify.info(error.response.data.info);
    }
    // form validations & general errors
    //
    if (typeof error.response.data.error !== "undefined") {
      let object = error.response.data.error;
      if (object instanceof Object) {
        Object.keys(object).forEach(function (k) {
          if (Array.isArray(object[k])) {
            object[k].forEach(function (subObject) {
              if (typeof subObject === "string") {
                vm.$snotify.error(`${subObject}`);
              }
              Object.values(subObject).forEach(function (subErrors) {
                if (Array.isArray(subErrors)) {
                  subErrors.forEach(function (strError) {
                    vm.$snotify.error(strError);
                  });
                }
              });
            });
          } else {
            vm.$snotify.error(`${object[k]}`);
          }
        });
      }
      if (typeof object === "string") {
        if (object === "Token Expired") {
          vm.$t("auth.tokenExpired");
          localStorage.removeItem("token");
          localStorage.removeItem("user");
          window.location.href = "/login";
        } else {
          vm.$snotify.error(object);
        }
      }
      return Promise.reject(error);
    }
    return Promise.reject(error);
  }
);

