<template>
  <div>
    <div class="d-flex align-center mb-2">
      <h3 class="display-0">{{ $t("settings.title") }}</h3>
    </div>
    <div>
      <v-row class="mt-4">
        <v-col sm="6">
          <v-alert dense text :type="alertType">
            Kur değerleri en son
            <strong>{{ exchangeRateDate | formatDate }}</strong>
            tarihinde güncellendi
          </v-alert>
        </v-col>
      </v-row>

      <v-row>
        <v-col sm="6">
          <v-form @submit.prevent="create">
            <v-card>
              <v-card-text>
                <v-row>
                  <v-col sm="6">
                    <h3 class="subtitle-1 mb-4">
                      {{ $t("settings.exchangeRateValues") }}
                    </h3>
                    <va-currency-input
                      v-model="usdExchangeRate"
                      :label="$t('settings.usdExchangeRate')"
                      :options="{ currency: 'TRY', precision: 4 }"
                      :error-messages="usdExchangeRateErrors"
                    ></va-currency-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="6">
                    <va-currency-input
                      v-model="euroExchangeRate"
                      :label="$t('settings.euroExchangeRate')"
                      :options="{ currency: 'TRY', precision: 4 }"
                      :error-messages="euroExchangeRateErrors"
                    ></va-currency-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="6">
                    <va-currency-input
                      v-model="poundExchangeRate"
                      :label="$t('settings.poundExchangeRate')"
                      :options="{ currency: 'TRY', precision: 4 }"
                      :error-messages="poundExchangeRateErrors"
                    ></va-currency-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="12">
                    <v-btn
                      color="primary"
                      :loading="settingsUpdating"
                      type="submit"
                      >{{ $t("profile.update") }}</v-btn
                    >
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-form>
        </v-col>

        <v-col sm="6">
          <v-card>
            <v-card-title>
              <h5 class="subtitle-1">
                {{ $t("settings.exchangeRateHistory") }}
              </h5>
            </v-card-title>
            <v-card-text>
              <va-list
                resource="exchangeRates"
                :items-per-page="3"
                :hideTitle="true"
                :disableExport="true"
                :disableCreate="true"
                :disable-items-per-page="false"
                :disable-global-search="true"
                :disablePagination="false"
                :hideBulkDelete="true"
                :hideHeader="true"
              >
                <va-data-table :disableActions="true" :fields="fields">
                  <template v-slot:[`field.usdExchangeRate`]="{ item }">
                    {{ item.usdExchangeRate | formatMoney("TRY", 4) }}
                  </template>
                  <template v-slot:[`field.euroExchangeRate`]="{ item }">
                    {{ item.euroExchangeRate | formatMoney("TRY", 4) }}
                  </template>
                  <template v-slot:[`field.poundExchangeRate`]="{ item }">
                    {{ item.poundExchangeRate | formatMoney("TRY", 4) }}
                  </template>
                </va-data-table>
              </va-list>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>

      <v-row class="mt-2">
        <v-col sm="4">
          <v-card class="v-card-usd-chart">
            <v-card-title>
              <h3 class="subtitle-1">Usd</h3>
            </v-card-title>
            <template v-bind="$attrs" v-on="$listeners">
              <chartist
                :key="chartistKeyUsd"
                :data="dailyUsdChart.data"
                :options="dailyUsdChart.options"
                type="Line"
                style="max-height: 280px"
              />
            </template>
          </v-card>
        </v-col>

        <v-col sm="4">
          <v-card class="v-card-euro-chart">
            <v-card-title>
              <h3 class="subtitle-1">Euro</h3>
            </v-card-title>
            <template v-bind="$attrs" v-on="$listeners">
              <chartist
                :key="chartistKeyEuro"
                :data="dailyEuroChart.data"
                :options="dailyEuroChart.options"
                type="Line"
                style="max-height: 280px"
              />
            </template>
          </v-card>
        </v-col>

        <v-col sm="4">
          <v-card class="v-card-pound-chart">
            <v-card-title>
              <h3 class="subtitle-1">Pound</h3>
            </v-card-title>
            <template v-bind="$attrs" v-on="$listeners">
              <chartist
                :key="chartistKeyPound"
                :data="dailyPoundChart.data"
                :options="dailyPoundChart.options"
                type="Line"
                style="max-height: 280px"
              />
            </template>
          </v-card>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import { dayDiff } from "../js/common";
import { required } from "vuelidate/lib/validators";

export default {
  data() {
    return {
      dailyUsdChart: {
        data: {
          labels: ["Pzt", "Sal", "Çar", "Prş", "Cm", "Cmt", "Paz"],
          series: [[]],
        },
        options: {
          lineSmooth: this.$chartist.Interpolation.cardinal({
            tension: 0,
          }),
          low: 0,
          high: 300000, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
          chartPadding: {
            top: 0,
            right: 20,
            bottom: 0,
            left: 20,
          },
        },
      },
      dailyEuroChart: {
        data: {
          labels: ["Pzt", "Sal", "Çar", "Prş", "Cm", "Cmt", "Paz"],
          series: [[]],
        },
        options: {
          lineSmooth: this.$chartist.Interpolation.cardinal({
            tension: 0,
          }),
          low: 0,
          high: 300000, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
          chartPadding: {
            top: 0,
            right: 20,
            bottom: 0,
            left: 20,
          },
        },
      },
      dailyPoundChart: {
        data: {
          labels: ["Pzt", "Sal", "Çar", "Prş", "Cm", "Cmt", "Paz"],
          series: [[]],
        },
        options: {
          lineSmooth: this.$chartist.Interpolation.cardinal({
            tension: 0,
          }),
          low: 0,
          high: 300000, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
          chartPadding: {
            top: 0,
            right: 20,
            bottom: 0,
            left: 20,
          },
        },
      },
      chartistKeyUsd: 1001,
      chartistKeyEuro: 1004,
      chartistKeyPound: 1006,
      value: [],
      alertType: "success",
      settingsUpdating: false,
      usdExchangeRate: null,
      euroExchangeRate: null,
      poundExchangeRate: null,
      exchangeRateDate: null,
      fields: [
        {
          source: "usdExchangeRate",
          label: this.$t("settings.usdExchangeRate"),
          sortable: true,
        },
        {
          source: "euroExchangeRate",
          label: this.$t("settings.euroExchangeRate"),
          sortable: true,
        },
        {
          source: "poundExchangeRate",
          label: this.$t("settings.poundExchangeRate"),
          sortable: true,
        },
        {
          source: "exchangeRateDate",
          label: this.$t("settings.exchangeRateDate"),
          sortable: true,
        },
      ],
    };
  },
  validations: {
    usdExchangeRate: {
      required,
    },
    euroExchangeRate: {
      required,
    },
    poundExchangeRate: {
      required,
    },
  },
  computed: {
    usdExchangeRateErrors() {
      const errors = [];
      const field = "usdExchangeRate";
      if (!this.$v[field].$dirty) return errors;
      !this.$v[field].required && errors.push(this.$t("v.text.required"));
      return errors;
    },
    euroExchangeRateErrors() {
      const errors = [];
      const field = "euroExchangeRate";
      if (!this.$v[field].$dirty) return errors;
      !this.$v[field].required && errors.push(this.$t("v.text.required"));
      return errors;
    },
    poundExchangeRateErrors() {
      const errors = [];
      const field = "poundExchangeRate";
      if (!this.$v[field].$dirty) return errors;
      !this.$v[field].required && errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  created() {
    this.initializeWeeklyChart();
    this.initializeExchangeRates();
  },
  methods: {
    ...mapActions({
      checkAuth: "auth/checkAuth",
    }),
    renderUsdChart() {
      this.chartistKeyUsd = this.chartistKeyUsd + 1;
    },
    renderEuroChart() {
      this.chartistKeyEuro = this.chartistKeyEuro + 1;
    },
    renderPoundChart() {
      this.chartistKeyPound = this.chartistKeyPound + 1;
    },
    async initializeExchangeRates() {
      let response = await this.$admin.http.get("/exchangeRates/findOne");
      let data = response.data.data;
      this.usdExchangeRate = data.usdExchangeRate;
      this.euroExchangeRate = data.euroExchangeRate;
      this.poundExchangeRate = data.poundExchangeRate;
      this.exchangeRateDate = data.exchangeRateDate;
      let day = dayDiff(this.exchangeRateDate);
      if (day > 1) {
        this.alertType = "warning";
      } else {
        this.alertType = "success";
      }
    },
    async initializeWeeklyChart() {
      let response = await this.$admin.http.get(
        "/exchangeRates/findWeeklyChart"
      );
      let data = response.data.data;
      let usdData = [];
      let euroData = [];
      let poundData = [];
      data.forEach(function (val) {
        let usdValue = String(val.usdExchangeRate).replace(".", "");
        let euroValue = String(val.euroExchangeRate).replace(".", "");
        let poundValue = String(val.poundExchangeRate).replace(".", "");
        usdData.push(Number(usdValue));
        euroData.push(Number(euroValue));
        poundData.push(Number(poundValue));
      });
      usdData.sort(function (a, b) {
        return a - b;
      });
      euroData.sort(function (a, b) {
        return a - b;
      });
      poundData.sort(function (a, b) {
        return a - b;
      });
      for (let i = 0; i < usdData.length; i++) {
        this.dailyUsdChart.data.series[0][i] = usdData[i];
        this.dailyEuroChart.data.series[0][i] = euroData[i];
        this.dailyPoundChart.data.series[0][i] = poundData[i];
      }
      this.renderUsdChart();
      this.renderEuroChart();
      this.renderPoundChart();
    },
    async update(method, url, data) {
      try {
        await this.$admin.http({ method, url, data });
        this.alertType = "success";
        return true;
      } catch (e) {
        this.$snotify.warning(e.message);
      }
      return false;
    },
    async create() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return false;
      }
      this.settingsUpdating = true;
      try {
        if (
          await this.update("post", "/exchangeRates/create", {
            usdExchangeRate: this.usdExchangeRate,
            euroExchangeRate: this.euroExchangeRate,
            poundExchangeRate: this.poundExchangeRate,
          })
        ) {
          /**
           * Recheck auth
           */
          this.checkAuth();
        }
      } finally {
        this.settingsUpdating = false;
      }
    },
  },
};
</script>

<style lang="sass">
.v-card-usd-chart
    .ct-series-a .ct-point,
    .ct-series-a .ct-line,
    .ct-series-a .ct-bar,
    .ct-series-a .ct-slice-donut
        stroke: rgba(0,128,0,.8)

.v-card-euro-chart
    .ct-series-a .ct-point,
    .ct-series-a .ct-line,
    .ct-series-a .ct-bar,
    .ct-series-a .ct-slice-donut
        stroke: rgba(0,128,255,.8)
    .ct-series-a .ct-slice-pie,
    .ct-series-a .ct-area
        fill: rgba(255,255,255,.4)

.v-card-pound-chart
    .ct-series-a .ct-point,
    .ct-series-a .ct-line,
    .ct-series-a .ct-bar,
    .ct-series-a .ct-slice-donut
        stroke: rgba(235, 149, 50,.8)
    .ct-series-a .ct-slice-pie,
    .ct-series-a .ct-area
        fill: rgba(255,255,255,.4)
</style>
