<template>
  <div>
    <div>
      <v-select
        v-model="select"
        :items="agreements"
        item-text="name"
        item-value="id"
        :label="$t('employees.chooseAgreement')"
        return-object
        single-line
        @change="changeState($event)"
      ></v-select>
    </div>
    <div id="printMe" v-if="selectedAgreement" class="printing">
      <va-show-layout
        :showList="false"
        :showClone="false"
        :showEdit="false"
        :showDelete="false"
      >
        <h1 class="display-small">{{ $t("employees.printTitle") }}</h1>
        <va-show :item="item">
          <v-row>
            <v-col sm="12">
              <b>{{ $t("employees.pernetNumber") }}:</b> {{ item.pernetNumber }}
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="12">
              <b>{{ $t("employees.name") }}:</b> {{ item.name }}
              {{ item.middleName }} {{ item.surname }} {{ item.secondSurname }}
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="12">
              <b>{{ $t("employees.tckn") }}:</b> {{ item.tckn }}
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="12">
              <b>{{ $t("employees.employmentStartDate") }}:</b>
              {{ item.employmentStartDate }}
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="12">
              <b>{{ $t("employees.jobTitle") }}:</b>
              {{ selectedAgreement.jobTitleName }}
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="12">
              <b>{{ $t("employees.customerShortName") }}:</b>
              {{ item.customerName }}
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="12">
              <b>{{ $t("employees.phone") }}:</b>
              {{ item.phone }} - {{ item.mobile1 }} - {{ item.mobile2 }}
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="12">
              <b>{{ $t("employees.email") }}:</b>
              {{ item.email1 }} - {{ item.email2 }}
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="12">
              <b>{{ $t("employees.agreementTypeId") }}:</b>
              {{ selectedAgreement.agreementTypeName }}
            </v-col>
          </v-row>

          <v-row>
            <v-col sm="12">
              <h1 class="display-small">
                {{ $t("employees.printDocuments") }}
              </h1>
              <v-simple-table dense class="docs" style="margin-bottom: 0">
                <template v-slot:default>
                  <tbody>
                    <tr v-for="item in employeeInfoSheet" :key="item.fieldId">
                      <td>{{ item.fieldLabel }}</td>
                      <td v-if="item.fieldValue">
                        <span
                          style="color: black"
                          class="v-icon mdi mdi-check"
                        ></span>
                      </td>
                      <td v-else>
                        <span
                          style="color: black"
                          class="v-icon mdi mdi-close"
                        ></span>
                      </td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-col>
          </v-row>

          <template v-if="item.infoSheet.employeeInfoSheetDescription">
            <v-row>
              <v-col sm="12">
                <h1 class="display-small">
                  {{ $t("employees.employeeInfoSheetDescription") }}
                </h1>
                <v-simple-table dense class="description">
                  <template v-slot:default>
                    <tbody>
                      <tr>
                        <td>
                          {{ item.infoSheet.employeeInfoSheetDescription }}
                        </td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </v-col>
            </v-row>
          </template>
        </va-show>
      </va-show-layout>
    </div>
  </div>
</template>

<script>
import { Printd } from "printd";

// https://github.com/joseluisq/printd

export default {
  props: ["item"],
  data() {
    return {
      select: null,
      selectedAgreement: null,
      agreements: [],
      employeeInfoSheet: [],
      d: null,
      cssText: `
        .printing {
          font-family: sans-serif;
          font-size: 13px;
          text-align: left;
          padding: 0;
          margin: 0;
          line-height: 20px;
        }
        h1 {
          font-size: 18px;
        }
        pre {
          color: #c7254e;
        }
        table, td, th {
          padding-left: 4px;
          border: 1px solid #979797;
        }
        .docs table {
          width: 40%;
          border-collapse: collapse;
        }
        .description  table {
          width: 100%;
          border-collapse: collapse;
        }
        `,
    };
  },
  mounted() {
    if (this.item.employeeAgreements) {
      this.employeeInfoSheet = JSON.parse(this.item.employeeInfoSheet);
      let agreements = JSON.parse(this.item.employeeAgreements);
      this.agreements = [{ id: "", name: "-" }];
      for (let i = 0; i < agreements.length; i++) {
        this.agreements[i] = {
          id: agreements[i].agreementId,
          name:
            agreements[i].customerName +
            " - " +
            agreements[i].jobTitleName +
            " - " +
            agreements[i].agreementTypeName +
            " - " +
            agreements[i].startDate +
            " / " +
            agreements[i].endDate,
          obj: agreements[i],
        };
      }
      this.d = new Printd();
      const { contentWindow } = this.d.getIFrame();
      contentWindow.addEventListener("beforeprint", () =>
        console.log("before print event!")
      );
      contentWindow.addEventListener("afterprint", () =>
        console.log("after print event!")
      );
    }
  },
  methods: {
    changeState(e) {
      this.selectedAgreement = e.obj;
    },
    printMe() {
      const styles = [
        "https://cdnjs.cloudflare.com/ajax/libs/MaterialDesign-Webfont/2.0.46/css/materialdesignicons.css",
        this.cssText,
      ];
      if (this.selectedAgreement) {
        this.d.print(document.getElementById("printMe"), styles);
      }
    },
  },
};
</script>
