<template>
  <div>
    <template>
      <va-form
        :id="id"
        :item="item"
        @save="onSave"
        disable-redirect
        @saved="$emit('saved')"
        v-model="model"
      >
        <v-row>
          <v-col>
            <va-text-input v-show="false" source="id"></va-text-input>
            <va-select-input
              source="currencyId"
              @change="generateVersionName($event, 'currencyId')"
              :error-messages="currencyIdErrors"
            ></va-select-input>
            <va-select-input
              source="customerId"
              reference="customers"
              :error-messages="customerIdErrors"
            ></va-select-input>
            <va-select-input
              source="sowId"
              reference="sows"
              :key="model.customerId"
              :filter="{ customerId: model.customerId }"
              :error-messages="sowIdErrors"
              @change="generateVersionName($event, 'sowId')"
            ></va-select-input>
            <va-text-input
              v-show="false"
              source="currencyId"
              v-model="model.currencyIdHidden"
            ></va-text-input>
            <va-text-input
              source="versionName"
              v-model="model.versionName"
              :error-messages="versionNameErrors"
            ></va-text-input>
          </v-col>
        </v-row>

        <!-- <v-btn
              color="primary"
              @click="e1 = 2"
            >
              Kaydet
            </v-btn> -->

        <va-save-button></va-save-button>
      </va-form>
    </template>
  </div>
</template>

<script>
import { generateUid } from "../../js/common";
import { required, maxLength } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      model: {
        versionName: null,
        sowId: null,
        customerId: null,
        currencyId: null,
        currencyIdHidden: null,
      },
    };
  },
  validations: {
    model: {
      versionName: {
        required,
        maxLength: maxLength(160),
      },
      sowId: {
        required,
        maxLength: maxLength(36),
      },
      customerId: {
        required,
        maxLength: maxLength(36),
      },
      currencyId: {
        required,
        maxLength: maxLength(3),
      },
    },
  },
  computed: {
    versionNameErrors() {
      const errors = [];
      const field = "versionName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    sowIdErrors() {
      const errors = [];
      const field = "sowId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    customerIdErrors() {
      const errors = [];
      const field = "customerId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    currencyIdErrors() {
      const errors = [];
      const field = "currencyId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "3" }));
      return errors;
    },
  },
  created() {
    this.model.id = this.id ? this.id : generateUid();
  },
  methods: {
    generateVersionName(event, field) {
      if (field == "currencyId") {
        this.model.currencyIdHidden = event.id;
      } else if (field == "sowId") {
        this.model.versionName =
          event.name.toString().replace(/\s/g, "") +
          "_" +
          this.model.currencyIdHidden +
          "_calisma_1";
      }
    },
    onSave(formState) {
      formState.model.versionName = this.model.versionName;
      window.location.href = "/costs/" + this.model.id + "/edit";
    },
  },
};
</script>
